import React, { useEffect, useState } from "react";
import { MDBCol, MDBInput, MDBRow } from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import { BROWSE } from "../../../../../services/redux/slices/resources/batches";
import Tracks from "../../../../../services/fakeDb/track";

const jhsOptions = [
  { value: 7, label: "Grade 7" },
  { value: 8, label: "Grade 8" },
  { value: 9, label: "Grade 9" },
  { value: 10, label: "Grade 10" },
];

const Enrollment = ({ setDetails, data, isNewNReject }) => {
  const { token, information } = useSelector(({ auth }) => auth),
    { collections } = useSelector(({ batches }) => batches),
    [isNewStudent, setIsNewStudent] = useState(true),
    [enrolleeStatus, setEnrolleeStatus] = useState(""),
    [batchSem, setBatchSem] = useState(""),
    [openProgress, setOpenProgress] = useState({
      jhs: false,
      shs: false,
    }),
    [studentStatus, setStudentStatus] = useState({
      returnee: false,
      transferee: false,
    }),
    [isPassed, setIspassed] = useState(true),
    [lastSchool, setLastSchool] = useState({
      name: "",
      address: "",
    }),
    [pendingStudent, setPendingStudent] = useState(false),
    dispatch = useDispatch();

  const handleStageChange = (progress) => {
    if (progress === "jhs") {
      setDetails({
        ...data,
        progress,
        lvl: 7,
        specialization: "",
        strand: "",
      });
      return;
    }

    setDetails({
      ...data,
      progress,
      lvl: progress === "shs" ? 11 : "",
    });
  };

  // handler function for returnee and transferee checkbox
  const handleLearner = (name, checked) => {
    setStudentStatus({
      returnee: name === "returnee" ? checked : false,
      transferee: name === "transferee" ? checked : false,
    });
    setDetails({
      ...data,
      returnee: name === "returnee" ? checked : false,
      transferee: name === "transferee" ? checked : false,
    });

    if (!checked) {
      setDetails({
        ...data,
        progress: "jhs",
        lvl: 7,
        returnee: name === "returnee" ? checked : false,
        transferee: name === "transferee" ? checked : false,
      });
    }
  };

  // handler for the selection of level and specialization
  const handleChange = (e) => {
    const { name, value } = e.target;
    let optValue = 0;

    if (name === "lvl") {
      optValue = parseInt(value);
      
      setDetails({
        ...data,
        lvl: optValue,
      });
      return;
    }else{
      setDetails({
        ...data,
        [name]: value,
      });
    }

  };

  const handleLastSchool = (e) => {
    const { name, value } = e.target;
    setLastSchool((prev) => ({
      ...prev,
      [name]: value,
    }));

    setDetails({
      ...data,
      lasSchool: {
        ...data.lastSchool,
        [name]: value,
      },
    });
  };

  useEffect(() => {
    if(information.enrolleeData){
      setIsNewStudent(false);
      setIspassed(true);
      setEnrolleeStatus(information.enrolleeData?.status);
    }
  }, [information]);

  const batchInfo = information.enrolleeData?.batch?._id;

  useEffect(() => {
    if (collections) {
      const batchOpen = collections.filter((item) => item.status === "ACTIVE");
      // setBatch(batchOpen);
      batchOpen.forEach((active) => {
        if (enrolleeStatus === "PENDING" && active.id !== batchInfo) {
          setPendingStudent(true);
        }

        if (active.progress === "jhs") {
          if (data.progress === "jhs" && isNewStudent) {
            setOpenProgress((prev) => ({ ...prev, jhs: true }));
          }
        } else if (active.progress === "shs") {
          setOpenProgress((prev) => ({ ...prev, shs: true }));
          setBatchSem(active.semester);
        }
      });
    }
  }, [collections, data.progress, isNewStudent, batchInfo, enrolleeStatus]);

  const disabledProgress = (progressType) => {
    if (isNewNReject && !(data.transferee || data.returnee)) {
      return false;
    }
    if (data.returnee || data.transferee) {
      return false;}

    if (progressType === "jhs") {
      if (studentStatus.returnee || studentStatus.transferee) {
        return false;}
      if (openProgress.jhs && (data.returnee || data.transferee)){
        return false;}
      if (openProgress.jhs && data.progress === "jhs") {
        return false;}
      return true;

    } else if (progressType === "shs") {
      if (isNewStudent && !(data.returnee || data.transferee)) {
        return true; }
      if (openProgress.shs && data.progress === "shs" && (data.returnee || data.transferee)) {
        return false;}
      return true;
    }
  };

  const disabledLevelSelection = (value) => { 

    if (!openProgress.jhs) {
      return true;
    }
    
    if (isNewNReject&& (data.transferee || data.returnee)) {
      return false;
    }

    if (isNewStudent && (data.transferee || data.returnee)) {
      return false;
    } 
    else if (value === data.lvl && isNewStudent) {
      return false;
    } 
    else if (value === data.lvl && (enrolleeStatus === "REJECT" || pendingStudent) ) {
      return false;
    } 
    return true;
  };

  const disabledLevelForShs = (value) => {
    if (!openProgress.shs) {
      return true;
    }

    if (isNewStudent && (data.transferee || data.returnee)) {
      return false;
    } else if (value === data.lvl && (enrolleeStatus === "REJECT" || pendingStudent)) {
      return false;
    } 
    else if (
      !isNewStudent &&
      value === data.lvl &&
      isPassed &&
      enrolleeStatus !== "REJECT" &&
      batchSem === "2nd"
    ) {
      return false;
    } 
    else if (
      !isNewStudent &&
      value === data.lvl &&
      isPassed &&
      enrolleeStatus !== "REJECT" &&
      batchSem !== "2nd" &&
      value !== data.lvl
    ) {
      return false;
    } 
    else if (
      !isNewStudent &&
      value === data.lvl +1 &&
      isPassed &&
      enrolleeStatus !== "REJECT" &&
      batchSem !== "2nd"
    ) {
      return true;
    }
    return true;
  };

  useEffect(() => {
    if (token) {
      dispatch(BROWSE({ token }));
    }
  }, [dispatch, token]);

  return (
    <>
      <MDBRow style={{ backgroundColor: "#0000FF" }}>
        <MDBCol>
          <h5 className="font-weight-bold text-white label mt-2 mb-2 ml-5">
            VI. Enrollment
          </h5>
        </MDBCol>
      </MDBRow>
      {isNewStudent && (
        <>
          <MDBRow className="mt-4 mr-4 ml-4">
            <MDBCol>
              <h6>
                <strong>
                  For Returning Learner (Balik Aral) and Those Who will
                  Transfer/Move in
                </strong>
              </h6>
            </MDBCol>
          </MDBRow>

          <MDBRow className="mr-2 ml-2 mt-2">
            <MDBCol>
              <MDBRow>
                <MDBCol>
                  <MDBInput
                    label="Returning Learner (Balik Aral)"
                    type="checkbox"
                    name="returnee"
                    value="returnee"
                    checked={studentStatus.returnee}
                    onChange={(e) =>
                      handleLearner(e.target.name, e.target.checked)
                    }
                    id="returnee"
                  />
                </MDBCol>
                <MDBCol>
                  <MDBInput
                    label="Transferee"
                    type="checkbox"
                    name="transferee"
                    value="transferee"
                    checked={studentStatus.transferee}
                    onChange={(e) =>
                      handleLearner(e.target.name, e.target.checked)
                    }
                    id="transferee"
                  />
                </MDBCol>
              </MDBRow>
            </MDBCol>
          </MDBRow>

          {(studentStatus.returnee || studentStatus.transferee) && (
            <MDBRow className="mt-4 mr-4 ml-4">
              <MDBCol>
                <h6>Last School Attended</h6>

                <input
                  placeholder="Last School Attended"
                  className="form-control"
                  value={lastSchool.name}
                  name="name"
                  onChange={handleLastSchool}
                  disabled={!studentStatus.returnee && !studentStatus.transferee}
                />
              </MDBCol>

              <MDBCol>
                <h6>Last School Address</h6>

                <input
                  placeholder="Last School Attended"
                  className="form-control"
                  name="address"
                  value={lastSchool.address}
                  onChange={handleLastSchool}
                  disabled={!studentStatus.returnee && !studentStatus.transferee}
                />
              </MDBCol>
            </MDBRow>
          )}
        </>
      )}
      {batchSem && data.progress === "shs" && (
        <MDBRow className="mt-4 mr-4 ml-4">
          <MDBCol>
            <h6>
              <b>{batchSem} Semester</b>
            </h6>
          </MDBCol>
        </MDBRow>
      )}
      <MDBRow className="mt-4 mr-4 ml-4">
        <MDBCol>
          <h6>
            <strong>Level</strong>
          </h6>
          <select
            name="progress"
            value={data.progress}
            className="form-control"
            onChange={(e) => handleStageChange(e.target.value)}
          >
            <option value="jhs" disabled={disabledProgress("jhs")}>
              JHS
            </option>

            <option value="shs" disabled={disabledProgress("shs")}>
              SHS
            </option>
          </select>
        </MDBCol>

        <MDBCol>
          <h6>
            <strong>Grade Level</strong>
          </h6>
          <select
            name="lvl"
            value={data.lvl.toString()}
            className="form-control"
            onChange={(e) => handleChange(e)}
          >
            {data.progress === "jhs" ? (
              <>
                {jhsOptions.map((option, i) => (
                  <option
                    key={i}
                    value={option.value.toString()}
                    disabled={disabledLevelSelection(parseInt(option.value))}
                  >
                    {option.label}
                  </option>
                ))}
              </>
            ) : (
              data.progress === "shs" && (
                <>
                  <option disabled={disabledLevelForShs(11)} value={String(11)}>
                    Grade 11
                  </option>
                  <option disabled={disabledLevelForShs(12)} value={String(12)}>
                    Grade 12
                  </option>
                </>
              )
            )}
          </select>
        </MDBCol>
      </MDBRow>

      {data.progress === "shs" && (
        <MDBRow className="mt-4 mr-4 ml-4">
          <MDBCol>
            <h6>
              <strong>Specialization</strong>
            </h6>
            <select
              name="specialization"
              value={data.specialization}
              className="form-control"
              onChange={(e) => handleChange(e)}
            >
              <option>Choose specialization</option>
              {Tracks.collections.map((track) => (
                <option key={track.name} value={track.name}>
                  {track.name}
                </option>
              ))}
            </select>
          </MDBCol>

          <MDBCol>
            <h6>
              <strong>Strand</strong>
            </h6>
            <select
              name="strand"
              value={data.strand}
              className="form-control"
              onChange={(e) => handleChange(e)}
            >
              <option>Choose strand</option>
              {Tracks.getStrand(data.specialization)?.map((strand) => (
                <option key={strand.abbr} value={strand.abbr}>
                  {strand.name}
                </option>
              ))}
            </select>
          </MDBCol>
        </MDBRow>
      )}
    </>
  );
};

export default Enrollment;
