import React from "react";
import "./strand.css";

export default function Strands() {
  const strands = {
    HUMSS: { female: 10, male: 20 },
    ABM: { female: 10, male: 20 },
    AGRI: { female: 10, male: 20 },
    ICT: { female: 10, male: 20 },
    BEAUTY: { female: 10, male: 20 },
    HUMS: { female: 10, male: 20 },
  };
  return (
    <section>
      <div className="strand-slider" style={{ backgroundColor: "#111" }}>
        <div className="strand-slide-track">
          {Object.entries(strands)?.map(([key, value]) => {
            const { male = 0, female = 0 } = value;

            return (
              <div
                style={{ "--i": 3 }}
                key={`strand-${key}`}
                className={`strand-slide ${
                  Object.entries(strands).length > 5 && "strand-animation"
                }`}
              >
                <div className="strand-card">
                  <p className="font-weight-bold">{key}</p>
                  <div className="strand-population">
                    <div className="strand-half-box-left pl-4 ml-2">
                      <p>{String(male).padStart(2, "0")}</p>
                      <p>male</p>
                    </div>

                    <div className="strand-loader"></div>
                    <p className="strand-total-population">
                      {String(female + male).padStart(2, "0")}
                    </p>

                    <div>
                      <div className="strand-half-box-right pr-4 mr-2">
                        <p>{String(female).padStart(2, "0")}</p>
                        <p>female</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
}
