import React, { useEffect, useState } from "react";
import {
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBIcon,
  MDBModalHeader,
  MDBInput,
  MDBRow,
  MDBCol,
  MDBModalFooter,
} from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import { SAVE } from "../../../../services/redux/slices/admission/employees";
import CustomSelect from "../../../../components/customSelect";
import AddressSelect from "../../../../components/addressSelect";
import getAge from "../../../../services/utilities/getAge";

const _form = {
  fullName: {
    fname: "",
    mname: "",
    lname: "",
    suffix: "",
  },
  position: "",
  access: "",
  mobile: "",
  dob: "",
  civil: "",
  isMale: false,
  motherTounge: "",

  address: {
    isSame: true,
    permanent: {
      region: "REGION III (CENTRAL LUZON)",
      province: "NUEVA ECIJA",
      city: "GENERAL TINIO (PAPAYA)",
      barangay: "Pias",
      street: "",
      no: "",
      code: "",
    },
    current: {
      region: "REGION III (CENTRAL LUZON)",
      province: "NUEVA ECIJA",
      city: "GENERAL TINIO (PAPAYA)",
      barangay: "Pias",
      street: "",
      no: "",
      code: "",
    },
  },
  birthplace: "",
  email: "",
  password: "",
  suffix: "N/A",
  wasBanned: false,
};

export default function Modal({ show, toggle, selected, willCreate }) {
  const { token } = useSelector(({ auth }) => auth),
    { isLoading } = useSelector(({ rooms }) => rooms),
    [form, setForm] = useState(_form),
    dispatch = useDispatch();

  useEffect(() => {
    if (!willCreate && selected._id) setForm(selected);
  }, [willCreate, selected]);

  const handleSubmit = (e) => {
    e.preventDefault();

    const { password = "", confirmPassword = "", address } = form;

    if (password !== confirmPassword)
      return (document.getElementById("warningMessage").innerHTML =
        "Password and Confirm Password is not match!");

    var updatedForm = form;
    if (address.isSame) {
      updatedForm = {
        ...form,
        address: { ...form.address, permanent: form.address?.current },
      };
    }
    dispatch(SAVE({ data: updatedForm, token }));

    document.getElementById("warningMessage").innerHTML = "";

    setForm(_form);
    toggle();
  };

  const handleClose = () => {
    setForm(_form);
    toggle();
  };

  const handleChange = (key, value, objKey) => {
    if (!objKey) {
      return setForm((prev) => ({ ...prev, [key]: value }));
    }
    setForm((prev) => ({ ...prev, [key]: { ...prev[key], [objKey]: value } }));
  };

  const defaultDisplayInWarning = "";
  return (
    <MDBModal
      isOpen={show}
      toggle={toggle}
      backdrop
      disableFocusTrap={false}
      size="xl"
    >
      <MDBModalHeader
        toggle={handleClose}
        className="light-blue darken-3 white-text"
      >
        <MDBIcon icon="user" className="mr-2" />
        Applicant Registration
      </MDBModalHeader>
      <form onSubmit={handleSubmit}>
        <MDBModalBody className="mb-0 p-5">
          <h5>Personal Information</h5>
          <MDBRow>
            <MDBCol md="4">
              <MDBInput
                type="text"
                label="Position"
                value={form.position}
                onChange={(e) =>
                  handleChange("position", e.target.value.toUpperCase())
                }
                required
              />
            </MDBCol>
            <MDBCol md="4">
              <CustomSelect
                choices={["Teacher", "Registrar"]}
                label={"Allowed Access"}
                preValue={form.access}
                onChange={(value) => handleChange("access", value)}
              />
            </MDBCol>
            <MDBCol md="4">
              <MDBInput
                type="number"
                label="Mobile No. +63"
                value={form.mobile}
                onChange={(e) =>
                  handleChange("mobile", e.target.value.toUpperCase())
                }
                required
              />
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="6" className="border border-black">
              <MDBInput
                label="Last Name"
                type="text"
                value={form.fullName?.lname}
                onChange={(e) =>
                  handleChange(
                    "fullName",
                    e.target.value.toUpperCase(),
                    "lname"
                  )
                }
                required
              />
              <MDBInput
                label="Middle Name"
                type="text"
                value={form.fullName?.mname}
                onChange={(e) =>
                  handleChange(
                    "fullName",
                    e.target.value.toUpperCase(),
                    "mname"
                  )
                }
                required
              />
              <MDBRow>
                <MDBCol md="8">
                  <MDBInput
                    label="First Name"
                    type="text"
                    value={form.fullName?.fname}
                    onChange={(e) =>
                      handleChange(
                        "fullName",
                        e.target.value.toUpperCase(),
                        "fname"
                      )
                    }
                    required
                  />
                </MDBCol>
                <MDBCol>
                  <CustomSelect
                    choices={["N/A", "JR", "III", "IV", "V", "SR"]}
                    label="Suffix"
                    preValue={form.suffix}
                    onChange={(value) => handleChange("suffix", value)}
                  />
                </MDBCol>
              </MDBRow>
            </MDBCol>
            <MDBCol md="6">
              <MDBInput
                label="Mother Toungue(ex: Tagalog, English)"
                value={form?.motherTounge}
                onChange={(e) =>
                  handleChange("motherTounge", e.target.value.toUpperCase())
                }
                required
              />
              <MDBRow>
                <MDBCol md="4">
                  <MDBInput
                    type="date"
                    label="Date of Birth"
                    value={form?.dob}
                    onChange={(e) =>
                      handleChange("dob", e.target.value.toUpperCase())
                    }
                    required
                  />
                </MDBCol>
                <MDBCol md="4">
                  <MDBInput
                    type="text"
                    label="Age"
                    readOnly
                    value={getAge(form.dob)}
                  />
                </MDBCol>
                <MDBCol md="4">
                  <CustomSelect
                    choices={["Single", "Married"]}
                    label={"Civil Status"}
                    value={form?.civil}
                    onChange={(value) => handleChange("civil", value)}
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow className="d-flex align-items-center">
                <MDBCol md="3">
                  <div className="form-check">
                    <input
                      className="form-check-input"
                      type="checkbox"
                      id="Male"
                      checked={form?.isMale}
                      onChange={(e) => handleChange("isMale", e.target.checked)}
                    />
                    <label className="form-check-label" htmlFor="Male">
                      Male
                    </label>
                  </div>
                  <div className="form-check">
                    <input
                      className="form-check-input"
                      type="checkbox"
                      id="Female"
                      checked={!form?.isMale}
                      onChange={(e) =>
                        handleChange("isMale", !e.target.checked)
                      }
                    />
                    <label className="form-check-label" htmlFor="Female">
                      Female
                    </label>
                  </div>
                </MDBCol>
                <MDBCol>
                  <MDBInput
                    label="Place of Birth (Municipality/City)"
                    value={form?.birthplace}
                    onChange={(e) =>
                      handleChange("birthplace", e.target.value.toUpperCase())
                    }
                    required
                  />
                </MDBCol>
              </MDBRow>
            </MDBCol>
          </MDBRow>
          <MDBRow className="mt-5">
            <MDBCol md="12">
              <AddressSelect
                label="Current Address"
                address={form.address?.current}
                handleChange={(key, value) =>
                  handleChange(key, value, "current")
                }
              />
            </MDBCol>
          </MDBRow>
          <MDBRow className="mt-2">
            <MDBCol md="12" className="d-flex">
              <label>
                Is your permanent address is same with your current address?
              </label>
              <div className="form-check">
                <input
                  className="form-check-input"
                  type="checkbox"
                  id="yes"
                  checked={form.address?.isSame}
                  onChange={({ target }) =>
                    handleChange("address", target.checked, "isSame")
                  }
                />
                <label className="form-check-label" htmlFor="yes">
                  Yes
                </label>
              </div>
              <div className="form-check">
                <input
                  className="form-check-input"
                  type="checkbox"
                  id="no"
                  checked={!form.address?.isSame}
                  onChange={({ target }) =>
                    handleChange("address", !target.checked, "isSame")
                  }
                />
                <label className="form-check-label" htmlFor="no">
                  No
                </label>
              </div>
            </MDBCol>
          </MDBRow>
          <hr />

          {!form.address?.isSame && (
            <MDBRow>
              <MDBCol md="12">
                <AddressSelect
                  label="Permanent Address"
                  address={form.address?.permanent}
                  handleChange={(key, value) =>
                    handleChange(key, value, "permanent")
                  }
                />
              </MDBCol>
            </MDBRow>
          )}
          <h5>Account Information:</h5>

          <MDBRow>
            <MDBCol className="border border-black p-3 m-2">
              <MDBInput
                type="email"
                label="Email"
                value={form.email}
                required
                onChange={({ target }) => handleChange("email", target.value)}
              />
              <MDBInput
                type="password"
                label="Password"
                value={form.password}
                required
                onChange={({ target }) =>
                  handleChange("password", target.value)
                }
              />
              <MDBInput
                type="password"
                label="Confirm Password"
                value={form.confirmPassword}
                required
                onChange={({ target }) =>
                  handleChange("confirmPassword", target.value)
                }
              />
              <h6 className="text-danger m-0 p-0" id="warningMessage">
                {defaultDisplayInWarning}
              </h6>
            </MDBCol>
          </MDBRow>
        </MDBModalBody>
        <MDBModalFooter>
          <MDBBtn
            type="submit"
            disabled={isLoading}
            color="primary"
            className="mb-2 float-right"
          >
            {!willCreate ? "Update" : "Submit"}
          </MDBBtn>
        </MDBModalFooter>
      </form>
    </MDBModal>
  );
}
