import React, { useState } from "react";
import Swal from "sweetalert2";
import { UPDATE } from "../../../../services/redux/slices/registrar/applicants";
import { MDBBtn, MDBIcon, MDBTable } from "mdbreact";
import PaginationButtons from "../../../../widgets/pagination/buttons";
import handlePagination from "../../../../widgets/pagination";
import Rows from "../../../../widgets/pagination/rows";
import handleMultiSeclect from "../../../../widgets/multipleSelect";
import { fullName } from "../../../../services/utilities";
import { useDispatch, useSelector } from "react-redux";

function Table({
  applicants,
  setOrderIndex,
  orderIndex,
  setContainer,
  container,
}) {
  const { token } = useSelector(({ auth }) => auth),
    [page, setPage] = useState(1),
    [max, setMax] = useState(5),
    [isSelectAll, setIsSelectAll] = useState(false),
    dispatch = useDispatch();

  const handleApproved = (_id) => {
    Swal.fire({
      title: "Are you sure?",
      text: "You want to activate this account",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Activate it!",
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(UPDATE({ token, data: { _id } }));
        Swal.fire({
          title: "Success!",
          text: "Applicant has been approve.",
          icon: "success",
        });
      }
    });
  };

  return (
    <>
      <MDBTable responsive hover>
        <thead>
          <tr>
            <th>
              <input
                className="form-check-input"
                type="checkbox"
                checked={isSelectAll}
                disabled
                onChange={() => setIsSelectAll(!isSelectAll)}
                id="checkbox"
              />
              <label
                htmlFor="checkbox"
                className="form-check-label mr-2 label-table"
              />
            </th>
            <th
              className="th-lg cursor-pointer"
              onClick={() =>
                setOrderIndex((prev) => {
                  if (prev > 1) return 0;

                  return prev + 1;
                })
              }
            >
              Name&nbsp;
              <MDBIcon
                icon="sort"
                title="Sort by Name"
                className={`${!!orderIndex && "text-primary"}`}
              />
            </th>
            <th className="th-lg">Email</th>
          </tr>
        </thead>
        <tbody>
          {applicants.length > 0 ? (
            handlePagination(applicants, page, max).map((applicant, index) => {
              const { _id, fullName: name, email } = applicant;
              return (
                <tr key={_id}>
                  <td>
                    <input
                      className="form-check-input"
                      type="checkbox"
                      id={index}
                      checked={
                        isSelectAll
                          ? true
                          : container.some(({ _id }) => _id === applicant._id)
                      }
                      onChange={() => {
                        setContainer(handleMultiSeclect(container, applicant));
                        setIsSelectAll(false);
                      }}
                    />
                    <label
                      htmlFor={index}
                      className="form-check-label mr-2 label-table"
                    />
                  </td>
                  <td style={{ fontWeight: 400 }}>{fullName(name)}</td>
                  <td style={{ fontWeight: 400 }}>{email}</td>
                  <td>
                    <MDBBtn
                      size="sm"
                      rounded
                      color="primary"
                      onClick={() => handleApproved(_id)}
                    >
                      <MDBIcon icon="user-check" />
                    </MDBBtn>
                  </td>
                </tr>
              );
            })
          ) : (
            <tr>
              <td>No Records</td>
            </tr>
          )}
        </tbody>
      </MDBTable>
      {applicants.length > 0 && (
        <>
          <Rows setMax={setMax} max={max} />
          <PaginationButtons
            page={page}
            setPage={setPage}
            array={applicants}
            max={max}
          />
        </>
      )}
    </>
  );
}

export default Table;
