import React, { useEffect, useState } from "react";

import { MDBBadge, MDBListGroup, MDBListGroupItem } from "mdbreact";

function SwitchView({ active = "jhs", setActive, array = [] }) {
  const [progress, setProgress] = useState([]);
  useEffect(() => {
    const isSenior = array.some(({ progress }) => progress === "shs");
    const isJunior = array.some(({ progress }) => progress === "jhs");
    const shs = array.filter(({ progress }) => progress === "shs");
    const jhs = array.filter(({ progress }) => progress === "jhs");

    if (isSenior && !isJunior) {
      setProgress([
        { text: "Senior High School", value: "shs", count: shs.length },
      ]);
    } else if (isJunior && !isSenior) {
      setProgress([
        { text: "Junior High School", value: "jhs", count: jhs.length },
      ]);
    } else if (isJunior && isSenior) {
      setProgress([
        { text: "Junior High School", value: "jhs", count: jhs.length },
        { text: "Senior High School", value: "shs", count: shs.length },
      ]);
    }
  }, [array]);

  return (
    <MDBListGroup
      style={{ height: "40px", overflowX: "auto", whiteSpace: "nowrap" }}
      className="d-flex flex-row"
    >
      {progress.map(({ text, value, count }, index) => {
        const isActive = active === value;
        return (
          <MDBListGroupItem
            onClick={() => setActive(value)}
            key={index}
            style={{ minWidth: "100px" }}
            className={`d-flex justify-content-between  align-items-center rounded py-2 mx-2 h-100 cursor-pointer ${
              isActive && "bg-primary text-white"
            }`}
          >
            <b className="mr-2">{text}</b>

            <MDBBadge
              color={isActive ? "light" : "primary"}
              className="pt-1"
              pill
            >
              {count}
            </MDBBadge>
          </MDBListGroupItem>
        );
      })}
    </MDBListGroup>
  );
}

export default SwitchView;
