import React from "react";
import { MDBCol, MDBCollapseHeader } from "mdbreact";
import Tracks from "../../services/fakeDb/track";
import "./header.css";
import { useSelector } from "react-redux";
export function CollapseHeader({
  activeId,
  didHoverId,
  index,
  obj,
  setActiveId,
  setIsSelectAll = () => {},
  setContainer = () => {},
  isSchedule = false,
}) {
  const textColor =
    activeId !== index
      ? didHoverId === index
        ? "text-primary"
        : "text-black"
      : "text-white";

  const bgBorder = //bg and border
    activeId === index
      ? " bg-info transition"
      : didHoverId === index
      ? "rounded border border-info bg-light ease-out"
      : "bg-light ease-out";

  const header = `${obj.lvl} ${obj?.section ? "- " + obj.section : ""} `;

  return (
    <MDBCollapseHeader
      className={bgBorder}
      onClick={() => {
        setActiveId((prev) => (prev === index ? -1 : index));
        setIsSelectAll(false);
        setContainer([]);
      }}
    >
      <div className="d-flex justify-content-between ">
        <label className={textColor}>{`Grade ${header}`}</label>

        <div className="d-flex align-items-center">
          <label
            className={`d-flex justify-content-end mt-2 ${textColor} mr-2`}
          >
            {obj.strand ? Tracks.getStrandByAbbr(obj.strand).abbr : ""}

            {!isSchedule ? ` (${obj.value?.length}) ` : ""}
          </label>
          <i
            style={{
              transform: `rotate(${activeId === index ? 0 : 90}deg)`,
            }}
            className={`fa fa-angle-down transition-all ${textColor}`}
          />
        </div>
      </div>
    </MDBCollapseHeader>
  );
}

export function TeacherHeaderBatch({
  params = { sy: "", progress: "", semester: "" },
  setParams,
}) {
  const { information } = useSelector(({ auth }) => auth);

  const container = Object.values(
    information.previousBatches.reduce((acc, item) => {
      acc[item.sy] = item;
      return acc;
    }, {})
  );

  container.sort((a, b) => {
    let yearA = parseInt(a.sy.split("-")[0]);
    let yearB = parseInt(b.sy.split("-")[0]);
    return yearB - yearA;
  });

  return (
    <MDBCol size="6">
      <select
        className="form-control bg-light"
        value={params?.sy || container[0]?.sy}
        onChange={(e) => setParams({ ...params, sy: e.target.value })}
      >
        <option value={""} disabled>
          Select a batch
        </option>
        {container.map(({ sy, _id, index }) => (
          <option key={`${_id}-${index}`} value={sy}>
            SY - {sy}
          </option>
        ))}
      </select>
    </MDBCol>
  );
}

export function HeaderBatch({
  batch = { sy: "", progress: "", semester: "" },
  setBatch,
}) {
  const { information } = useSelector(({ auth }) => auth);

  const container = Object.values(
    information.previousBatches.reduce((acc, item) => {
      acc[item.sy] = item;
      return acc;
    }, {})
  );

  container.sort((a, b) => {
    let yearA = parseInt(a.sy.split("-")[0]);
    let yearB = parseInt(b.sy.split("-")[0]);
    return yearB - yearA;
  });

  return (
    <div className="d-flex align-items-center justify-content-center w-50">
      <MDBCol size="6">
        <select
          className="form-control"
          value={batch.sy}
          onChange={(e) => setBatch({ ...batch, sy: e.target.value })}
        >
          <option value={""}>Select a batch</option>
          {container.map(({ sy, _id }) => (
            <option key={_id} value={sy}>
              SY - {sy}
            </option>
          ))}
        </select>
      </MDBCol>
      <MDBCol size="3">
        <select
          className="form-control"
          value={batch.progress}
          onChange={(e) => setBatch({ ...batch, progress: e.target.value })}
        >
          <option value={"jhs"}> JHS</option>
          <option value={"shs"}>SHS</option>
        </select>
      </MDBCol>
      {batch.progress === "shs" && (
        <MDBCol size="4">
          <select
            className="form-control"
            value={batch.semester}
            onChange={(e) => setBatch({ ...batch, semester: e.target.value })}
          >
            <option value={"1st"}>1ST SEM</option>
            <option value={"2nd"}>2ND SEM</option>
          </select>
        </MDBCol>
      )}
    </div>
  );
}
