import Swal from "sweetalert2";

/** 
 * An automatic DataTable.
 *
 * @component
 * @param {boolean} dispatch - this is your dispatch function to dispatch your action
 * @param {array} DESTROY - this is your action
 * @param {string} token - this is your action
 * @param {boolean} array - this is your array of id 
 * @param {boolean} toast - this is your array of id 
 * @param {string} title - Table Title.
 
 */
function MultipleDestroy(
  dispatch,
  DESTROY,
  token,
  array,
  showToast,
  setContainer
) {
  const handleDestroy = () => {
    if (array.length > 0) {
      Swal.fire({
        focusDeny: true,
        icon: "question",
        title: `Are you sure you want to delete this ${array.length} items?`,
        text: "This action is irreversible.",
        confirmButtonText: '<span class="text-dark">Cancel</span>',
        confirmButtonColor: "#fff",
        showDenyButton: true,
        denyButtonText: "Proceed",
        denyButtonColor: "#3B71CA",
      }).then((res) => {
        if (res.isDenied) {
          dispatch(
            DESTROY({
              data: array,
              token,
            })
          );
          setContainer([]);
        } else {
          setContainer(array);
          return Swal.fire({
            title: "Changes are not Saved!",
            icon: "warning",
            showConfirmButton: false,
            timer: 2500,
            timerProgressBar: true,
          }).then(() => false);
        }
      });
    } else {
      showToast("Please Select  Item", {
        appearance: "warning",
      });

      return false;
    }
  };

  return handleDestroy();
}

export default MultipleDestroy;
