import React, { useEffect, useState } from "react";
import {
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBIcon,
  MDBModalHeader,
  MDBInput,
  MDBRow,
  MDBCol,
  MDBTypography,
} from "mdbreact";

import { useToasts } from "react-toast-notifications";
import { useDispatch, useSelector } from "react-redux";
import {
  SAVE,
  UPDATE,
} from "../../../../services/redux/slices/pricipal/subjects";
import { isEqual } from "lodash";
import { isValid } from "../../../../services/utilities";
import SelectLevel from "../../../../components/SelectLevel";

const _form = {
  name: "",
  lvl: 7,
  time: {
    hours: 1,
    mins: 0,
  },
  strand: "ABM",
  specialization: "Academics track",
  semester: "1st",
};

export default function Modal({
  show,
  toggle,
  selected,
  willCreate,
  collections,
  previewForm,
  setPreviewForm,
}) {
  const { token } = useSelector(({ auth }) => auth),
    { isLoading } = useSelector(({ subjects }) => subjects),
    [form, setForm] = useState(_form),
    [isDuplicate, setIsDuplicate] = useState(false),
    dispatch = useDispatch(),
    { addToast } = useToasts();

  useEffect(() => {
    if (!willCreate && selected._id) setForm(selected);
  }, [willCreate, selected]);

  useEffect(() => {
    if (form.lvl) {
      setIsDuplicate(isValid(collections, form, selected.name) || false);
    }
  }, [form, collections, willCreate, selected]);

  useEffect(() => {
    setForm((prev) => ({ ...prev, ...previewForm }));
  }, [previewForm]);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!willCreate) {
      if (isEqual(form, selected)) {
        addToast("No changes found, skipping update.", {
          appearance: "info",
        });
      } else {
        dispatch(
          UPDATE({
            data: form,
            token,
          })
        );
      }
    } else {
      dispatch(SAVE({ data: form, token }));
    }
    setPreviewForm((prev) => {
      const newForm = {
        ...prev,
        level: form.lvl,
        specialization: form.specialization,
        strand: form.strand,
        semester: form.semester,
      };
      return newForm;
    });
    setForm(_form);
    toggle();
  };

  const handleClose = () => {
    setForm(_form);
    toggle();
  };

  return (
    <MDBModal
      isOpen={show}
      toggle={toggle}
      backdrop
      disableFocusTrap={false}
      size="lg"
    >
      <MDBModalHeader
        toggle={handleClose}
        className="light-blue darken-3 white-text"
      >
        <MDBIcon icon="user" className="mr-2" />
        {!willCreate ? "Update" : "Create"} a Subject
      </MDBModalHeader>
      <MDBModalBody className="mb-0">
        <form onSubmit={handleSubmit}>
          <MDBRow>
            <SelectLevel
              form={form}
              setForm={setForm}
              disableSemester={false}
            />

            <MDBCol>
              <MDBInput
                type="text"
                label="Name"
                value={form.name}
                onChange={(e) =>
                  setForm({ ...form, name: e.target.value.toUpperCase() })
                }
                required
              />
            </MDBCol>
          </MDBRow>

          <div
            className={`d-flex justify-content-${
              isDuplicate ? "between" : "end"
            } mb-1-half mt-4 align-items-center`}
          >
            {isDuplicate && (
              <MDBTypography
                variant="h2"
                className="mb-0 text-black-50"
                noteColor="danger"
                note
                noteTitle="Warning: "
              >
                Sorry But this subject is Already Exist
              </MDBTypography>
            )}
            <MDBBtn
              type="submit"
              disabled={isLoading || isDuplicate}
              color="primary"
              className="mb-2 float-right"
            >
              {!willCreate ? "Update" : "Submit"}
            </MDBBtn>
          </div>
        </form>
      </MDBModalBody>
    </MDBModal>
  );
}
