import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { ENDPOINT, axioKit } from "../../utilities";
import { DECRYPT } from "../../utilities/encryption/securedData";

const name = "auth",
  maxPage = Number(localStorage.getItem("maxPage")) || 5,
  token = localStorage.getItem("token") || "",
  email = localStorage.getItem("email") || "",
  image = `${ENDPOINT}/assets/profile/${email}.jpg`,
  card = "",
  psa = "",
  sign = "",
  picture = "";

const initialState = {
  id: "",
  auth: {},
  role: {},
  searchFound: "",
  information: {},
  route: "",
  token,
  email,
  image,
  code: "",
  card,
  psa,
  sign,
  picture,
  maxPage,
  progress: {
    requirements: [],
    percentage: 0,
  },
  progressBar: -1,
  isSuccess: false,
  isLoading: false,
  message: "",
};

export const LOGIN = createAsyncThunk(`${name}/login`, (form, thunkAPI) => {
  try {
    return axioKit.login(form.email, form.password);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const SEARCH = createAsyncThunk(
  `${name}/search`,
  ({ data, token = "" }, thunkAPI) => {
    try {
      return axioKit.universal("auth/search", token, data);
    } catch (error) {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();

      return thunkAPI.rejectWithValue(message);
    }
  }
);

export const SEND_CODE = createAsyncThunk(
  `${name}/send_code`,
  ({ data, token = "" }, thunkAPI) => {
    try {
      return axioKit.universal("auth/send_code", token, data);
    } catch (error) {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();

      return thunkAPI.rejectWithValue(message);
    }
  }
);

