import React, { useEffect, useState } from "react";
import { MDBIcon, MDBTable } from "mdbreact";
import handleMultiSeclect from "../../../../widgets/multipleSelect";
import Rows from "../../../../widgets/pagination/rows";
import PaginationButtons from "../../../../widgets/pagination/buttons";
import handlePagination from "../../../../widgets/pagination";
import SORTING from "../../../../widgets/sorting";

const Table = ({
  lvlKey,
  subjects = [],
  isSelectAll,
  setIsSelectAll,
  collections,
  didSearch,
  activeId,
  container,
  setContainer,
}) => {
  const [max, setMax] = useState(5),
    [storageSubjects, setStorageSubjects] = useState([]),
    [page, setPage] = useState(1);

  useEffect(() => {
    if (isSelectAll) {
      if (didSearch) {
        setContainer(subjects);
      } else {
        setContainer(subjects);
      }
    } else {
      setContainer((prev) => (prev.length === subjects.length ? [] : prev));
    }
    setStorageSubjects(SORTING(subjects));
  }, [isSelectAll, subjects, setContainer, didSearch, collections, activeId]);

  return (
    <>
      <MDBTable responsive hover key={`collapse-${lvlKey}`}>
        <thead>
          <tr>
            <th key={lvlKey} className="th-lg">
              <input
                className="form-check-input"
                type="checkbox"
                checked={isSelectAll}
                onChange={() => setIsSelectAll(!isSelectAll)}
                id={`collapse-${lvlKey}`}
              />
              <label
                htmlFor={`collapse-${lvlKey}`}
                className="form-check-label mr-2 label-table"
              />
            </th>
            <th className="th-lg cursor-pointer">
              Name&nbsp;
              <MDBIcon icon="sort" title="Sort by Name" />
            </th>
            <th className="th-lg">Grade Level</th>

            {didSearch && (
              <>
                <th className="th-lg">Grade Level</th>
              </>
            )}
            <th />
          </tr>
        </thead>
        <tbody>
          {storageSubjects.length > 0 ? (
            handlePagination(storageSubjects, page, max)?.map(
              (subject, index) => {
                const { _id, name, strand, lvl } = subject;

                const isChecked =
                  isSelectAll ||
                  container.some(({ _id }) => _id === subject._id);
                return (
                  <tr key={`${_id}-${lvlKey}-${index}`}>
                    <td>
                      <input
                        className="form-check-input"
                        type="checkbox"
                        id={`${_id}-${lvlKey}`}
                        checked={isChecked}
                        onChange={() => {
                          setContainer(handleMultiSeclect(container, subject));
                          setIsSelectAll(false);
                        }}
                      />
                      <label
                        htmlFor={`${_id}-${lvlKey}`}
                        className="form-check-label mr-2 label-table"
                      />
                    </td>
                    <td style={{ fontWeight: 400 }}>{name}</td>
                    <td style={{ fontWeight: 400 }}>{`Grade - ${lvl} ${
                      lvl > 10 ? `, ${strand}` : ""
                    }`}</td>

                    <td style={{ fontWeight: 400 }}>
                      {didSearch &&
                        `Grade-${lvl}${strand ? ` , ${strand}` : ""}`}
                    </td>
                  </tr>
                );
              }
            )
          ) : (
            <tr>
              <td>No Records.</td>
            </tr>
          )}
        </tbody>
      </MDBTable>
      <Rows max={max} setMax={setMax} />
      <PaginationButtons
        page={page}
        setPage={setPage}
        max={max}
        array={storageSubjects}
      />
    </>
  );
};

export default Table;
