import React, { useEffect, useState } from "react";
import moment from "moment";
import {
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBIcon,
  MDBModalHeader,
  MDBRow,
  MDBCol,
  MDBDatePicker,
} from "mdbreact";
import { useToasts } from "react-toast-notifications";
import { useDispatch, useSelector } from "react-redux";
import {
  SAVE,
  UPDATE,
} from "../../../../services/redux/slices/resources/batches";
import { isEqual } from "lodash";

const _form = {
  sy: "",
  semester: "",
  progress: "jhs",
  enrollment: {
    start: moment().toDate(),
    end: moment().add(1, "days").toDate(),
  },
  classes: {
    start: moment().add(2, "days").toDate(),
    end: moment().add(3, "days").toDate(),
  },
};

const SEMESTER = ["1st", "2nd"];

export default function Modal({ show, toggle, selected, willCreate }) {
  const { token } = useSelector(({ auth }) => auth),
    { isLoading, collections } = useSelector(({ batches }) => batches),
    dispatch = useDispatch(),
    { addToast } = useToasts(),
    [batches, setBatches] = useState([]),
    [form, setForm] = useState(_form),
    [SY, setSy] = useState([]);

  useEffect(() => {
    if (!willCreate && selected._id) {
      setForm({
        ...selected,
        enrollment: {
          ...selected.enrollment,
          start: new Date(selected.enrollment.start),
          end: new Date(selected.enrollment.end),
        },
        classes: {
          ...selected.classes,
          start: new Date(selected.classes.start),
          end: new Date(selected.classes.end),
        },
      });
      return;
    }
    setForm(_form);
    setSy([]);
  }, [willCreate, selected]);

  useEffect(() => {
    setBatches(collections);
  }, [collections]);

  const handleSubmit = (e) => {
    e.preventDefault();

    const enrollment = {
      start: moment(new Date(form.enrollment?.start)).format("MMMM DD, YYYY"),
      end: moment(new Date(form.enrollment?.end)).format("MMMM DD, YYYY"),
    };

    const classes = {
      start: moment(new Date(form.classes?.start)).format("MMMM DD, YYYY"),
      end: moment(new Date(form.classes?.end)).format("MMMM DD, YYYY"),
    };

    console.table(form);

    if (!willCreate) {
      if (isEqual(form, selected)) {
        addToast("No changes found, skipping update.", {
          appearance: "info",
        });
      } else {
        dispatch(
          UPDATE({
            data: { ...form, enrollment, classes },
            token,
          })
        );
      }
    } else {
      dispatch(
        SAVE({
          data: { ...form, status: "WAITING", enrollment, classes },
          token,
        })
      );
    }

    setForm(_form);
    toggle();
  };

  const handleChange = (field, value) => {
    setForm((prevForm) => ({ ...prevForm, [field]: value }));
  };

  const handleDateChange = (field, subField) => (value) => {
    // Validate if the value is a valid date before updating the state
    if (moment(value, moment.ISO_8601, true).isValid()) {
      setForm({
        ...form,
        [field]: {
          ...form[field],
          [subField]: value,
        },
      });
    }
  };

  const formatDate = (date) => {
    if (moment(date, moment.ISO_8601, true).isValid()) {
      return moment(date).format("YYYY-MM-DD");
    } else {
      return "";
    }
  };

  useEffect(() => {
    const currentYear = parseInt(moment().format("YYYY"));
    let years = [];
    let currYear = currentYear; // 2024
    let afterYear = currentYear + 1; //2025

    for (let i = 0; i < 4; i++) {
      years.push({ value: currYear + "-" + afterYear });
      afterYear = afterYear + 1;
      currYear = currYear + 1;
    }

    const Sy = years
      .filter((sy) => {
        if (form.progress === "jhs") {
          return !batches.some(
            (batch) => batch.sy === sy.value && batch.progress === "jhs"
          );
        }
        return !batches.some(
          (batch) => batch.sy === sy.value && batch.progress === "shs"
        );
      })
      .sort((a, b) => {
        const yearA = parseInt(a.value.split("-")[0]);
        const yearB = parseInt(b.value.split("-")[0]);
        return yearA - yearB;
      });

    if (Sy.length > 0) {
      if (willCreate) {
        setSy([...Sy.map((sy) => ({ value: sy.value }))]);

        setForm((prev) => ({
          ...prev,
          sy: Sy[0].value,
        }));
        return;
      }
    } else {
      setSy([]);
      setForm(_form);
    }

    if (!willCreate) {
      Sy.unshift({ value: selected.sy });
      setSy([...Sy.map((sy) => ({ value: sy.value }))]);
    }
  }, [batches, form.progress, willCreate, selected.sy]);

  return (
    <MDBModal isOpen={show} toggle={toggle} backdrop size="lg">
      <MDBModalHeader
        toggle={() => toggle()}
        className="light-blue darken-3 white-text"
      >
        <MDBIcon icon="user" className="mr-2" />
        {willCreate ? "Create" : "Update"} a Batch
      </MDBModalHeader>
      <MDBModalBody className="mb-0">
        <form onSubmit={handleSubmit}>
          <MDBRow className="d-flex align-items-center">
            <MDBCol>
              <select
                className="form-control"
                name="sy"
                value={form.sy}
                onChange={(e) => handleChange(e.target.name, e.target.value)}
                disabled={!willCreate && form.status !== "WAITING"}
              >
                {SY.length > 0 &&
                  SY.map((sy, index) => (
                    <option key={`${index}-${sy.value}`} value={sy.value}>
                      {sy.value}
                    </option>
                  ))}

                {SY.length <= 0 && willCreate && (
                  <option disabled value="">
                    No Available School Year
                  </option>
                )}
              </select>
            </MDBCol>

            <MDBCol>
              <select
                className="form-control"
                value={form.progress}
                onChange={(e) => setForm({ ...form, progress: e.target.value })}
                disabled={!willCreate && form.status !== "WAITING"}
              >
                <option value={"jhs"}>Junior High School</option>
                <option value={"shs"}>Senior High School</option>
              </select>
            </MDBCol>

            {form.progress === "shs" && (
              <MDBCol>
                <select
                  className="form-control"
                  name="semester"
                  value={form.semester}
                  onChange={(e) => handleChange(e.target.name, e.target.value)}
                >
                  <option value="">Semester</option>
                  {SEMESTER.map((sem, index) => (
                    <option value={sem} key={`${index}-${sem}`}>
                      {sem}
                    </option>
                  ))}
                </select>
              </MDBCol>
            )}
          </MDBRow>
          <MDBRow>
            <MDBCol>
              <MDBRow className="d-flex align-items-center">
                <MDBCol md="5">
                  <div>Enrollment Start:</div>
                </MDBCol>
                <MDBCol>
                  <MDBDatePicker
                    getValue={handleDateChange("enrollment", "start")}
                    value={form.enrollment.start}
                    minDate={formatDate(moment().toDate())}
                  />
                </MDBCol>
              </MDBRow>
            </MDBCol>
            <MDBCol>
              <MDBRow className="d-flex align-items-center">
                <MDBCol md="5">
                  <div>Enrollment End:</div>
                </MDBCol>
                <MDBCol>
                  <MDBDatePicker
                    getValue={handleDateChange("enrollment", "end")}
                    value={form.enrollment.end}
                    minDate={formatDate(moment(form.enrollment.start).toDate())}
                  />
                </MDBCol>
              </MDBRow>
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol>
              <MDBRow className="d-flex align-items-center">
                <MDBCol md="5">
                  <div>Class Start:</div>
                </MDBCol>
                <MDBCol>
                  <MDBDatePicker
                    getValue={handleDateChange("classes", "start")}
                    value={form.classes.start}
                    minDate={formatDate(moment(form.enrollment.end).toDate())}
                  />
                </MDBCol>
              </MDBRow>
            </MDBCol>
            <MDBCol>
              <MDBRow className="d-flex align-items-center">
                <MDBCol md="5">
                  <div>Class End:</div>
                </MDBCol>
                <MDBCol>
                  <MDBDatePicker
                    getValue={handleDateChange("classes", "end")}
                    value={form.classes.end}
                    minDate={formatDate(moment(form.classes.start).toDate())}
                  />
                </MDBCol>
              </MDBRow>
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol className="d-flex justify-content-end">
              <MDBBtn
                color="primary"
                type="submit"
                disabled={isLoading || (SY.length <= 0 && willCreate)}
              >
                {willCreate ? "Create" : "Update"}
              </MDBBtn>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBModalBody>
    </MDBModal>
  );
}
