import React, { useState } from "react";
import Swal from "sweetalert2";
import { MDBBtn, MDBBtnGroup, MDBIcon, MDBTable } from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import { UPDATE } from "../../../../services/redux/slices/admission/employees";
import { fullName } from "../../../../services/utilities";
import PaginationButtons from "../../../../widgets/pagination/buttons";
import handlePagination from "../../../../widgets/pagination";
import Rows from "../../../../widgets/pagination/rows";

function Table({ employees, setOrderIndex, orderIndex }) {
  const { token } = useSelector(({ auth }) => auth),
    [page, setPage] = useState(1),
    [max, setMax] = useState(5),
    dispatch = useDispatch();

  const handleBanned = (_id) => {
    Swal.fire({
      title: "Are you sure?",
      text: "You want to banned this employee!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, banned it!",
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(UPDATE({ data: { _id, wasBanned: true }, token }));
      }
    });
  };

  const handleBackToActive = (_id) => {
    Swal.fire({
      title: "Are you sure?",
      text: "You want to active this employee!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Active it!",
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(UPDATE({ data: { _id, wasBanned: false }, token }));
      }
    });
  };
  return (
    <>
      <MDBTable responsive hover>
        <thead>
          <tr>
            <th
              className="th-lg cursor-pointer"
              onClick={() =>
                setOrderIndex((prev) => {
                  if (prev > 1) return 0;

                  return prev + 1;
                })
              }
            >
              Name&nbsp;
              <MDBIcon
                icon="sort"
                title="Sort by Name"
                className={`${!!orderIndex && "text-primary"}`}
              />
            </th>
            <th className="th-lg cursor-pointer">Position</th>

            <th className="th-lg cursor-pointer">Access</th>
            <th className="th-lg cursor-pointer">Email</th>
          </tr>
        </thead>
        <tbody>
          {employees.length > 0 ? (
            handlePagination(employees, page, max).map((employee) => {
              const {
                _id,
                fullName: name,
                email,
                position,
                wasBanned,
              } = employee;
              return (
                <tr key={_id}>
                  <td style={{ fontWeight: 400 }}>
                    {fullName(name).toUpperCase()}
                  </td>
                  <td style={{ fontWeight: 400 }}>{position || "-"}</td>
                  <td style={{ fontWeight: 400 }}>{"TEACHER"}</td>
                  <td style={{ fontWeight: 400 }}>{email}</td>
                  <td>
                    <MDBBtnGroup>
                      <MDBBtn size="sm" rounded color="primary">
                        <MDBIcon icon="key" />
                      </MDBBtn>
                      {!wasBanned ? (
                        <MDBBtn
                          size="sm"
                          rounded
                          color="danger"
                          onClick={() => handleBanned(_id)}
                        >
                          <MDBIcon icon="user-alt-slash" />
                        </MDBBtn>
                      ) : (
                        <MDBBtn
                          size="sm"
                          rounded
                          color="success"
                          onClick={() => handleBackToActive(_id)}
                        >
                          <MDBIcon fas icon="user-check" />
                        </MDBBtn>
                      )}
                    </MDBBtnGroup>
                  </td>
                </tr>
              );
            })
          ) : (
            <tr>
              <td>No Records</td>
            </tr>
          )}
        </tbody>
      </MDBTable>
      <Rows setMax={setMax} max={max} />
      <PaginationButtons
        page={page}
        setPage={setPage}
        array={employees}
        max={max}
      />
    </>
  );
}

export default Table;
