import React, { useEffect, useState } from "react";
import ImageCard from "./imageCard";
import ViewImage from "./modal";

import "./style.css";
import { ENDPOINT } from "../../services/utilities";

/**
 * Image component for displaying and handling image uploads.
 *
 * @param {Object} userDataImage - object of {email, _id, filesData{has2x2, hasPsa, hasCard, hasSignature}}
 * @param {Function} handleChange - function to handle image change
 * @param {boolean} viewOnly - flag to indicate if images are for viewing only
 * @returns JSX element
 */

const Image = ({ userDataImage, handleChange, viewOnly = false }) => {
  const [modal, setModal] = useState(false);
  const [currentImage, setCurrentImage] = useState(null);
  const [imagePreviews, setImagePreviews] = useState({
    studentPicture: "",
    signature: "",
    PSA: "",
    card: "",
  });

  const openModal = (imageName) => {
    setCurrentImage(imagePreviews[imageName]);
    setModal(true);
    document.body.classList.add("body-no-scroll");
  };

  const closeModal = () => {
    setModal(false);
    setCurrentImage("");
    document.body.classList.remove("body-no-scroll");
  };

  const returnLink = (email, _id, src) => {
    return `${ENDPOINT}/assets/documents/${email}-${_id}/${src}-${email}.jpg`;
  };

  const hasAlreadyFiles = (hasImg) => {
    if (hasImg) {
      return true;
    }
    return "";
  };

  // if one of this object dont have a attribute this will cause error thats why i use to store it in a variable
  const dataImage = userDataImage?.studentPicture;
  const dataSignature = userDataImage?.signature;
  const dataPsa = userDataImage?.PSA;
  const dataCard = userDataImage?.card;
  const dataFilesPic = userDataImage.filesData?.has2x2;
  const dataFilesSign = userDataImage.filesData?.hasSignature;
  const dataFilesPsa = userDataImage.filesData?.hasPsa;
  const dataFilesCard = userDataImage.filesData?.hasCard;

  useEffect(() => {
    if (!viewOnly) {
      setImagePreviews((prev) => ({
        ...prev,
        studentPicture: dataImage
          ? dataImage
          : hasAlreadyFiles(dataFilesPic) &&
            returnLink(
              userDataImage.email,
              userDataImage._id,
              "studentPicture"
            ),

        signature: dataSignature
          ? dataSignature
          : hasAlreadyFiles(dataFilesSign) &&
            returnLink(userDataImage.email, userDataImage._id, "signature"),

        PSA: dataPsa
          ? dataPsa
          : hasAlreadyFiles(dataFilesPsa) &&
            returnLink(userDataImage.email, userDataImage._id, "PSA"),

        card: dataCard
          ? dataCard
          : hasAlreadyFiles(dataFilesCard) &&
            returnLink(userDataImage.email, userDataImage._id, "card"),
      }));
      return;
    }

    if (viewOnly) {
      if (dataFilesPic) {
        setImagePreviews((prev) => ({
          ...prev,
          studentPicture: returnLink(
            userDataImage.email,
            userDataImage._id,
            "studentPicture"
          ),
        }));
      }
      if (dataFilesPsa) {
        setImagePreviews((prev) => ({
          ...prev,
          signature: returnLink(
            userDataImage.email,
            userDataImage._id,
            "signature"
          ),
        }));
      }
      if (dataFilesPsa) {
        setImagePreviews((prev) => ({
          ...prev,
          PSA: returnLink(userDataImage.email, userDataImage._id, "PSA"),
        }));
      }
      if (dataFilesCard) {
        setImagePreviews((prev) => ({
          ...prev,
          card: returnLink(userDataImage.email, userDataImage._id, "card"),
        }));
      }
      return;
    }
  }, [
    viewOnly,
    dataImage,
    dataSignature,
    dataPsa,
    dataCard,
    dataFilesPic,
    dataFilesPsa,
    dataFilesCard,
    dataFilesSign,
    userDataImage.email,
    userDataImage._id,
  ]);

  const changeHandler = (e) => {
    const { name, files } = e.target;

    if (files.length > 0) {
      const file = files[0];
      const fileUrl = URL.createObjectURL(file);
      setImagePreviews((prevPreviews) => ({
        ...prevPreviews,
        [name]: fileUrl,
      }));
    }
  };

  return (
    <>
      <ViewImage image={currentImage} show={modal} toggle={closeModal} />

      {Object.keys(imagePreviews).map((imgName) => (
        <ImageCard
          key={imgName}
          src={imagePreviews[`${imgName}`]}
          imageName={imgName}
          onChange={handleChange}
          setHandleChange={changeHandler}
          handleModalOpen={openModal}
          isView={viewOnly}
        />
      ))}
    </>
  );
};

export default Image;
