const convertTo24Hour = (hour) => {
  if (hour >= 1 && hour <= 5) {
    return hour + 12;
  }
  return hour;
};

const setHoursAndMinutes = ({
  hourEnd,
  hourStart,
  setHourInterval,
  setMinInterval,
  minStart,
  minEnd,
}) => {
  var _hourStart = convertTo24Hour(hourStart);
  var _hourEnd = convertTo24Hour(hourEnd);

  const hourInterval = _hourEnd - _hourStart;

  // para icheck kung yung overAllTime nila ay lagpas sa 1 hour
  const hourStartWithMin = _hourStart * 60 + minStart;
  const hourEndWithMin = _hourStart * 60 + minEnd;
  const overAllInterval = hourEndWithMin - hourStartWithMin;
  const isValid =
    hourInterval === 1 ? (overAllInterval >= 0 ? true : false) : true;

  // End Checker
  setHourInterval(isValid ? hourInterval : -1);
  setMinInterval(() => {
    var mins = 0;
    if (minEnd > minStart) {
      mins = minEnd - minStart;
    } else if (minStart > minEnd) {
      mins = minStart - minEnd;
    } else if (minEnd === minStart) {
      mins = 0;
    }
    return mins;
  });
};

export default setHoursAndMinutes;
