import React from "react";
import { MDBTypography } from "mdbreact";

function TimeMessage({ hourInterval, minInterVal }) {
  return (
    <>
      {hourInterval > 0 ? (
        <MDBTypography
          variant="h2"
          className="mb-0 text-black-50"
          noteColor="success"
          note
          noteTitle="Time Interval: "
        >
          {hourInterval} {hourInterval > 1 ? "Hours" : "Hour"} and {minInterVal}{" "}
          Minutes
        </MDBTypography>
      ) : (
        <MDBTypography
          variant="h2"
          className="mb-0 text-black-50"
          noteColor="danger"
          note
          noteTitle="Warning: "
        >
          <span>The minimum gap for time is 1 hour</span>
        </MDBTypography>
      )}
    </>
  );
}

export default TimeMessage;
