import {
  MDBBtn,
  MDBBtnGroup,
  MDBCard,
  MDBCardBody,
  MDBIcon,
  MDBTable,
  MDBView,
} from "mdbreact";
import React, { useEffect, useState } from "react";
import { fullName } from "../../../../services/utilities";

function SchedTable({ array = [], props }) {
  const [schedules, setSchedules] = useState([]);

  useEffect(() => {
    const container = [];

    for (let index = 0; index < array.length; index++) {
      const element = array[index];
      const _schedules = element.schedules;

      if (_schedules?.length > 0) {
        for (let index = 0; index < _schedules?.length; index++) {
          const schedule = _schedules[index];
          container.push({ ...element, schedules: schedule });
        }
      }
    }
    setSchedules(container);
  }, [props.willCreate, array]);

  const handleUpdate = (_id, instructor, subject, days, startTime, endTime) => {
    props.setForm((prev) => ({
      ...prev,
      instructor,
      subject,
      _id,
      oldDays: days,
    }));
    props.setPickDays(days);
    props.setHourStart(startTime.hours);
    props.setMinStart(startTime.min);
    props.setHourEnd(endTime.hours);
    props.setMinEnd(endTime.min);
  };

  return (
    <MDBCard narrow>
      <MDBView
        cascade
        className="gradient-card-header blue-gradient py-2 mx-4 d-flex justify-content-between align-items-center"
      >
        List of your selected Schedule
      </MDBView>
      <MDBCardBody
        style={{
          maxHeight:
            props.willCreate || props?.form?.instructor ? "432px" : "700px",
          overflowY: "auto",
        }}
      >
        <MDBTable>
          <thead>
            <tr>
              <th>#</th>
              <th>Instructor</th>
              <th>Subject</th>
              <th>Day</th>
              <th className="text-center">Time</th>
              <th className="text-center">Action</th>
            </tr>
          </thead>
          <tbody>
            {schedules.map(
              ({ instructor, subject, schedules, _id = "" }, index) => {
                const { time } = schedules;
                const { start, end } = time || {};
                return (
                  <tr key={index}>
                    <td> {index + 1}</td>
                    <td className="font-weight-bold">
                      {fullName(instructor.fullName)}
                    </td>
                    <td className="font-weight-bold">{subject?.name}</td>
                    <td className="font-weight-bold">
                      {schedules.days.join(",")}
                    </td>
                    <td className="font-weight-bold text-center">
                      {start.hours}: {start.min === 0 ? "00" : start.min}{" "}
                      {start.period} - {end.hours}:{" "}
                      {end.min === 0 ? "00" : end.min} {end.period}
                    </td>
                    <td className="text-center">
                      <MDBBtnGroup>
                        <MDBBtn
                          size="sm"
                          color="danger"
                          rounded
                          onClick={() =>
                            props.handleRemoveSchedule({
                              subject: subject._id,
                              instructor: instructor._id,
                              _id,
                              days: schedules.days,
                            })
                          }
                        >
                          <MDBIcon
                            icon="trash"
                            size="1x"
                            className="cursor-pointer"
                          />
                        </MDBBtn>
                        {!props.willCreate && (
                          <MDBBtn
                            size="sm"
                            color="primary"
                            rounded
                            onClick={() =>
                              handleUpdate(
                                _id,
                                instructor._id,
                                subject._id,
                                schedules.days,
                                start,
                                end
                              )
                            }
                          >
                            <MDBIcon
                              icon="pencil-alt"
                              size="1x"
                              className="cursor-pointer"
                            />
                          </MDBBtn>
                        )}
                      </MDBBtnGroup>
                    </td>
                  </tr>
                );
              }
            )}
          </tbody>
        </MDBTable>
      </MDBCardBody>
    </MDBCard>
  );
}

export default SchedTable;
