import checkerFunction from "../../../../../widgets/checkSchedule";
const { scheduleIsValid, subjectIsDuplicate } = checkerFunction;
const validate = {
  subject: ({ schedules, form, pickDays }) => {
    const sections = schedules.filter(
      ({ section, subject }) =>
        section?._id === form.section && subject._id === form.subject
    );

    var subjectConflict = subjectIsDuplicate(sections, pickDays);

    return subjectConflict;
  },
  instructor: ({
    schedules,
    pickDays,
    hourStart,
    hourEnd,
    minStart,
    minEnd,
    form,
  }) => {
    const instrucScheds = schedules.filter(
      ({ instructor }) => instructor?._id === form?.instructor
    );

    var instructorConflict = scheduleIsValid(
      instrucScheds,
      pickDays,
      hourStart,
      hourEnd,
      minStart,
      minEnd,
      form
    );

    return instructorConflict;
  },
  section: ({
    schedules,
    pickDays,
    hourStart,
    hourEnd,
    minStart,
    minEnd,
    form,
  }) => {
    const sectionScheds = schedules.filter(
      ({ section }) => section?._id === form.section
    );
    var sectionConflict = scheduleIsValid(
      sectionScheds,
      pickDays,
      hourStart,
      hourEnd,
      minStart,
      minEnd,
      form
    );

    return sectionConflict;
  },
  schedulePick: ({
    pickDays,
    hourStart,
    hourEnd,
    minStart,
    form,
    minEnd,
    schedulePick,
  }) => {
    var { isConflict } = scheduleIsValid(
      schedulePick,
      pickDays,
      hourStart,
      hourEnd,
      minStart,
      minEnd,
      form
    );

    return isConflict;
  },
  subjectPick: ({ schedules, pickDays, form }) => {
    const collections = schedules.filter(
      ({ subject }) => subject._id === form.subject
    );

    var isDuplicate = subjectIsDuplicate(collections, pickDays);

    return isDuplicate;
  },
};

export default validate;
