import React, { useEffect, useState } from "react";
import { MDBTable, MDBRow, MDBCol } from "mdbreact";
import { useDispatch, useSelector } from "react-redux";

import { GRADES } from "../../services/redux/slices/registrar/students";

import Grades from "./grades";
import Swal from "sweetalert2";

function Table({ selected, isStudent }) {
  const { token } = useSelector(({ auth }) => auth);
  const [studentGrades, setStudentGrades] = useState([]);
  const [activeId, setActiveId] = useState("");
  const dispatch = useDispatch();

  const borderTH = "border border-black text-center ";
  const colSpanHeader = selected.lvl > 10 ? "2" : "4";
  const finalsStorage = [];
  useEffect(() => {
    setStudentGrades(
      isStudent ? selected.grades : selected?.transcript?.grades
    );
  }, [selected, isStudent]);

  const handleSubmit = (newGrade, setGrade, title, gradeId, transcriptID) => {
    Swal.fire({
      title: "Are you sure?",
      text: "You want to update the grade for this student!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Update it!",
    }).then((result) => {
      if (result.isConfirmed) {
        const _grades = [...studentGrades];
        const index = _grades.findIndex(({ _id }) => _id === gradeId);
        _grades[index] = {
          ..._grades[index],
          [title]: newGrade,
        };
        setGrade(newGrade);
        setStudentGrades(_grades);
        dispatch(
          GRADES({
            token,
            data: { transcriptID, grades: _grades },
          })
        );
      }
    });
  };
  return (
    <MDBTable responsive>
      <thead>
        <tr className={borderTH} style={{ backgroundColor: "#d9d9d9" }}>
          <th className={`${borderTH} font-weight-bold th-sm`}>Subjects</th>
          <th
            className={`${borderTH} font-weight-bold `}
            colSpan={colSpanHeader}
          >
            <MDBRow>
              <MDBCol md="12">
                <h6 className="text-center font-weight-bold">Quarter</h6>
              </MDBCol>
            </MDBRow>
            <MDBRow>
              {selected.lvl < 10 ? (
                <>
                  <MDBCol className="ml-2">1</MDBCol>
                  <MDBCol className="ml-2">2</MDBCol>
                  <MDBCol>3</MDBCol>
                  <MDBCol>4</MDBCol>
                </>
              ) : selected.semester === "1st" ? (
                <>
                  <MDBCol className="ml-2">1</MDBCol>
                  <MDBCol className="ml-2">2</MDBCol>
                </>
              ) : (
                <>
                  <MDBCol className="ml-2">3</MDBCol>
                  <MDBCol className="ml-2">4</MDBCol>
                </>
              )}
            </MDBRow>
          </th>
          <th className={`${borderTH} font-weight-bold`}>Final Grade</th>
        </tr>
      </thead>
      <tbody className={borderTH}>
        {studentGrades?.length > 0 ? (
          studentGrades?.map((grade, index) => {
            const { _id } = isStudent ? selected : selected.transcript;
            const {
              firstQuarter = "--",
              secondQuarter = "--",
              thirdQuarter = "--",
              fourthQuarter = "--",
              subject,
            } = grade;
            var remarks = "--";
            var finals = 0;

            const done1st = firstQuarter > 0 ? true : false;
            const done2nd = secondQuarter > 0 ? true : false;
            const done3rd = thirdQuarter > 0 ? true : false;
            const done4th = fourthQuarter > 0 ? true : false;

            if (Number(selected.lvl) <= 10) {
              if (done1st && !done2nd && !done3rd && !done4th) {
                remarks = firstQuarter >= 75 ? "PASSED" : "FAILED";
              } else if (done1st && done2nd && !done3rd && !done4th) {
                remarks = secondQuarter >= 75 ? "PASSED" : "FAILED";
              } else if (done1st && done2nd && done3rd && !done4th) {
                remarks = thirdQuarter >= 75 ? "PASSED" : "FAILED";
              } else if (done1st && done2nd && done3rd && done4th) {
                const _finals =
                  (firstQuarter +
                    secondQuarter +
                    thirdQuarter +
                    fourthQuarter) /
                  4;
                remarks = _finals >= 75 ? "PASSED" : "FAILED";
                finals = _finals;
              }
            } else {
              if (selected.semester === "1st") {
                if (done1st && !done2nd) {
                  remarks = firstQuarter >= 75 ? "PASSED" : "FAILED";
                } else if (done2nd && done1st) {
                  const _finals = (firstQuarter + secondQuarter) / 2;
                  remarks = _finals >= 75 ? "PASSED" : "FAILED";
                  finals = _finals;
                }
              } else if (selected.semester === "2nd") {
                if (done3rd && !done4th) {
                  remarks = thirdQuarter >= 75 ? "PASSED" : "FAILED";
                } else if (done3rd && done4th) {
                  const _finals = (thirdQuarter + fourthQuarter) / 2;
                  remarks = _finals >= 75 ? "PASSED" : "FAILED";
                  finals = _finals;
                }
              }
            }

            if (finals > 0) {
              finalsStorage.push(Math.ceil(finals));
            }
            console.log(remarks);
            return (
              <tr key={index}>
                <td
                  className={
                    " d-flex justify-content-start font-weight-bold text-nowrap"
                  }
                >
                  <h6> {subject?.name}</h6>
                </td>
                {selected.lvl <= 10 ? (
                  <>
                    <td
                      className={borderTH}
                      style={{
                        width: "120px",
                      }}
                    >
                      <Grades
                        activeId={activeId}
                        setActiveId={setActiveId}
                        id={subject._id}
                        isStudent={isStudent}
                        transcriptID={_id}
                        handleSubmit={handleSubmit}
                        obj={{
                          title: "firstQuarter",
                          id: grade._id,
                          grade: firstQuarter,
                        }}
                      />
                    </td>
                    <td
                      className={borderTH}
                      style={{
                        width: "120px",
                      }}
                    >
                      <Grades
                        activeId={activeId}
                        setActiveId={setActiveId}
                        handleSubmit={handleSubmit}
                        isStudent={isStudent}
                        id={subject._id}
                        transcriptID={_id}
                        obj={{
                          title: "secondQuarter",
                          id: grade._id,
                          grade: secondQuarter,
                        }}
                      />
                    </td>
                    <td
                      className={borderTH}
                      style={{
                        width: "120px",
                      }}
                    >
                      <Grades
                        activeId={activeId}
                        setActiveId={setActiveId}
                        handleSubmit={handleSubmit}
                        isStudent={isStudent}
                        id={subject._id}
                        transcriptID={_id}
                        obj={{
                          title: "thirdQuarter",
                          id: grade._id,
                          grade: thirdQuarter,
                        }}
                      />
                    </td>
                    <td
                      className={borderTH}
                      style={{
                        width: "120px",
                      }}
                    >
                      <Grades
                        activeId={activeId}
                        setActiveId={setActiveId}
                        isStudent={isStudent}
                        id={subject._id}
                        handleSubmit={handleSubmit}
                        transcriptID={_id}
                        obj={{
                          title: "fourthQuarter",
                          id: grade._id,
                          grade: fourthQuarter,
                        }}
                      />
                    </td>
                  </>
                ) : selected.semester === "1st" ? (
                  <>
                    <td
                      className={borderTH}
                      style={{
                        width: "120px",
                      }}
                    >
                      <Grades
                        activeId={activeId}
                        setActiveId={setActiveId}
                        id={subject._id}
                        transcriptID={_id}
                        handleSubmit={handleSubmit}
                        isStudent={isStudent}
                        obj={{
                          title: "firstQuarter",
                          id: grade._id,
                          grade: firstQuarter,
                        }}
                      />
                    </td>
                    <td
                      className={borderTH}
                      style={{
                        width: "120px",
                      }}
                    >
                      <Grades
                        activeId={activeId}
                        setActiveId={setActiveId}
                        handleSubmit={handleSubmit}
                        isStudent={isStudent}
                        id={subject._id}
                        transcriptID={_id}
                        obj={{
                          title: "secondQuarter",
                          id: grade._id,
                          grade: secondQuarter,
                        }}
                      />
                    </td>
                  </>
                ) : (
                  <>
                    <td
                      className={borderTH}
                      style={{
                        width: "120px",
                      }}
                    >
                      <Grades
                        activeId={activeId}
                        setActiveId={setActiveId}
                        handleSubmit={handleSubmit}
                        isStudent={isStudent}
                        id={subject._id}
                        transcriptID={_id}
                        obj={{
                          title: "thirdQuarter",
                          id: grade._id,
                          grade: thirdQuarter,
                        }}
                      />
                    </td>
                    <td
                      className={borderTH}
                      style={{
                        width: "120px",
                      }}
                    >
                      <Grades
                        activeId={activeId}
                        setActiveId={setActiveId}
                        id={subject._id}
                        isStudent={isStudent}
                        handleSubmit={handleSubmit}
                        transcriptID={_id}
                        obj={{
                          title: "fourthQuarter",
                          id: grade._id,
                          grade: fourthQuarter,
                        }}
                      />
                    </td>
                  </>
                )}
                <td className={`${borderTH}`}>
                  <h6> {finals > 0 ? Math.ceil(finals) : "--"}</h6>
                </td>
                {/* <td
                className={`${borderTH} ${
                  remarks === "PASSED" ? "text-success" : "text-danger"
                }`}
              >
                {remarks.toUpperCase()}
              </td> */}
              </tr>
            );
          })
        ) : (
          <tr>
            <td>No Subjects</td>
          </tr>
        )}
        {studentGrades?.length > 0 && (
          <tr>
            <td className={`${borderTH} `} colSpan={selected.lvl > 10 ? 3 : 5}>
              <div className=" d-flex justify-content-end align-items-center">
                <h6 className="font-weight-bold">
                  General Average{selected.lvl > 10 ? " for the Semester" : ""}
                </h6>
              </div>
            </td>
            <td colSpan={2}>
              {finalsStorage.length === studentGrades.length ? (
                (() => {
                  let total = 0;
                  finalsStorage.forEach((number) => {
                    total += number;
                  });
                  const result = total / studentGrades.length;
                  return isNaN(result) ? (
                    <span>No valid result</span>
                  ) : (
                    <h6>{result}</h6>
                  );
                })()
              ) : (
                <h6>--</h6>
              )}
            </td>
          </tr>
        )}
      </tbody>
    </MDBTable>
  );
}

export default Table;
