import { MDBIcon } from "mdbreact";
import React, { useEffect, useState } from "react";

function Grades({
  activeId,
  setActiveId,
  id,
  obj,
  handleSubmit,
  transcriptID,
  isStudent,
}) {
  const [grade, setGrade] = useState(0);

  useEffect(() => {
    setGrade(obj.grade);
  }, [obj]);

  return (
    <div>
      {activeId === `${id}-${obj.title}` ? (
        <div>
          <input
            style={{ width: "50px" }}
            type="number"
            className="mr-1"
            min={0}
            value={String(grade)}
            onChange={(e) => setGrade(Number(e.target.value))}
          />
          <MDBIcon
            icon="times"
            size="lg"
            onClick={() => {
              setActiveId("");
            }}
            className="text-center mr-2 cursor-pointer"
            style={{ color: "red" }}
          ></MDBIcon>
          <MDBIcon
            icon="check"
            size="lg"
            onClick={() => {
              handleSubmit(grade, setGrade, obj.title, obj.id, transcriptID);
              setActiveId("");
            }}
            className="text-center cursor-pointer m-0 p-o"
            style={{ color: "green" }}
          ></MDBIcon>
        </div>
      ) : (
        <h6
          onClick={() => setActiveId(`${id}-${obj.title}`)}
          className={`cursor-pointer  disabled `}
        >
          {grade === 0 ? "--" : grade}
        </h6>
      )}
    </div>
  );
}

export default Grades;
