import { MDBRow } from "mdbreact";
import React from "react";
import Campus from "./campus";
import Enrollees from "./enrollees";
import Enrollments from "./enrollment";
import { School } from "../../../../../services/fakeDb";

export default function Statistics({ fullView }) {
  const { isPublic } = School;

  return (
    <MDBRow className="w-100 mx-auto" style={{ flex: 1 }}>
      {!isPublic && <Campus />}
      <Enrollments />
      <Enrollees fullView={fullView} />
    </MDBRow>
  );
}
