import React from "react";
import { MDBRow, MDBCol } from "mdbreact";

function Level({
  levels,
  level,
  setLevel,
  sections,
  section,
  setSection,
  strands,
  strand,
  setStrand,
  subjects,
  subject,
  setSubject,
  semester,
  setSemester,
  semesters,
}) {
  return (
    <MDBRow>
      <MDBCol>
        <label className="w-100">
          <span className="text-primary">Grade Level</span>
          <select
            className="form-control "
            value={level}
            onChange={(e) => setLevel(Number(e.target.value))}
          >
            {levels.length > 0 &&
              levels.map((level, index) => (
                <option key={index} value={level}>
                  {level}
                </option>
              ))}
          </select>
        </label>
      </MDBCol>
      {level > 10 && (
        <>
          <MDBCol>
            <label className="w-100">
              <span className="text-primary">Strand</span>

              <select
                className="form-control "
                value={strand}
                onChange={(e) => setStrand(e.target.value)}
              >
                {strands.length > 0 &&
                  strands.map((str, index) => (
                    <option key={index} value={str}>
                      {str}
                    </option>
                  ))}
              </select>
            </label>
          </MDBCol>
        </>
      )}
      <MDBCol>
        <label className="w-100">
          <span className="text-primary">Section</span>
          <select
            className="form-control "
            value={section}
            onChange={(e) => setSection(e.target.value)}
          >
            {sections.length > 0 &&
              sections.map(({ name, _id }, index) => (
                <option key={index} value={_id}>
                  {name}
                </option>
              ))}
          </select>
        </label>
      </MDBCol>
      {level > 10 && (
        <MDBCol>
          <label className="w-100">
            <span className="text-primary">Semester</span>
            <select
              className="form-control "
              value={semester}
              onChange={(e) => setSemester(e.target.value)}
            >
              {semesters.length > 0 &&
                semesters.map((_semester, index) => (
                  <option key={index} value={_semester}>
                    {_semester}
                  </option>
                ))}
            </select>
          </label>
        </MDBCol>
      )}
      <MDBCol>
        <label className="w-100">
          <div className="d-flex align-items-center">
            <span className="text-primary">Subject</span>
          </div>
          <select
            className="form-control "
            value={subject}
            onChange={(e) => setSubject(e.target.value)}
          >
            {subjects.length > 0 &&
              subjects.map(({ name, _id }, index) => (
                <option key={index} value={_id}>
                  {name}
                </option>
              ))}
          </select>
        </label>
      </MDBCol>
    </MDBRow>
  );
}

export default Level;
