import React, { useEffect, useState } from "react";
import { MDBIcon, MDBTable } from "mdbreact";
import handleMultiSeclect from "../../../../widgets/multipleSelect";
import Rows from "../../../../widgets/pagination/rows";
import PaginationButtons from "../../../../widgets/pagination/buttons";
import handlePagination from "../../../../widgets/pagination";
import { fullName } from "../../../../services/utilities";
import SORTING from "../../../../widgets/sorting";

const Table = ({
  lvlKey,
  sections = [],
  setContainer,
  container,
  isSelectAll,
  setIsSelectAll,
  didSearch,
  collections,
}) => {
  const [max, setMax] = useState(5),
    [storageSections, setStorageSections] = useState([]),
    [page, setPage] = useState(1);

  useEffect(() => {
    if (isSelectAll) {
      if (didSearch) {
        setContainer(sections);
      } else {
        setContainer(collections);
      }
    } else {
      setContainer((prev) => (prev.length === sections.length ? [] : prev));
    }
    setStorageSections(SORTING(sections));
  }, [isSelectAll, sections, setContainer, didSearch, collections]);

  return (
    <>
      <MDBTable responsive hover key={`collapse-${lvlKey}`}>
        <thead>
          <tr>
            <th key={lvlKey}>
              <input
                className="form-check-input"
                type="checkbox"
                checked={isSelectAll}
                onChange={() => setIsSelectAll(!isSelectAll)}
                id={`collapse-${lvlKey}`}
              />
              <label
                htmlFor={`collapse-${lvlKey}`}
                className="form-check-label mr-2 label-table"
              />
            </th>
            <th className="th-lg cursor-pointer">
              Name&nbsp;
              <MDBIcon icon="sort" title="Sort by Name" />
            </th>
            <th className="th-lg">Grade Level</th>
            <th className="th-lg">Capacity</th>
            <th className="th-lg">Adviser</th>
            {didSearch && (
              <>
                <th className="th-lg">Grade Level</th>
              </>
            )}
          </tr>
        </thead>
        <tbody>
          {sections.length > 0 ? (
            handlePagination(storageSections, page, max)?.map((section) => {
              const { _id, name, lvl, strand, capacity, adviser } = section;
              const isChecked =
                isSelectAll || container.some(({ _id }) => _id === section._id);
              return (
                <tr key={`${_id}-${lvlKey}`}>
                  <td>
                    <input
                      className="form-check-input"
                      type="checkbox"
                      id={_id}
                      checked={isChecked}
                      onChange={() => {
                        setContainer(handleMultiSeclect(container, section));
                        setIsSelectAll(false);
                      }}
                    />
                    <label
                      htmlFor={_id}
                      className="form-check-label mr-2 label-table"
                    />
                  </td>
                  <td style={{ fontWeight: 400 }}>{name}</td>
                  <td style={{ fontWeight: 400 }}>{`Grade - ${lvl} ${
                    lvl > 10 ? `, ${strand}` : ""
                  }`}</td>

                  <td style={{ fontWeight: 400 }}>{capacity}</td>
                  <td style={{ fontWeight: 400 }}>
                    {fullName(adviser.fullName)}
                  </td>
                  <td style={{ fontWeight: 400 }}>
                    {didSearch && `Grade-${lvl}${strand && ` , ${strand}`}`}
                  </td>
                </tr>
              );
            })
          ) : (
            <tr>
              <td>No Records</td>
            </tr>
          )}
        </tbody>
      </MDBTable>
      <Rows max={max} setMax={setMax} />
      <PaginationButtons
        max={max}
        page={page}
        setPage={setPage}
        array={storageSections}
      />
    </>
  );
};

export default Table;
