import React, { useEffect, useState } from "react";
import { MDBAnimation, MDBBtn, MDBIcon, MDBInput } from "mdbreact";
import "./style.css";

// import { MDBModal, MDBModalBody, MDBModalHeader } from "mdbreact";
import { useHistory, useLocation } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import { LOGIN, RESET } from "../../../services/redux/slices/auth";
import Register from "../register";

export default function NewLogin() {
  const { auth, email, didLogin, message, credentials } = useSelector(
      ({ auth }) => auth
    ),
    [isLocked, setIsLocked] = useState(true),
    [show, setShow] = useState(false),
    history = useHistory(),
    location = useLocation(),
    dispatch = useDispatch();

  const toggleRegister = () => setShow(!show);

  const handleSubmit = (e) => {
    e.preventDefault();

    const { address, password } = e.target;

    dispatch(
      LOGIN({
        email: email || address.value,
        password: password.value,
      })
    );
  };

  useEffect(() => {
    if (auth._id && didLogin) {
      let basePath = "/snapshot/dashboard";
      if (["pending", "declined"].includes(credentials?.status)) {
        basePath = "/snapshot/enrollee";
        if (credentials.access) basePath = "/employment";
      }

      history.push(basePath);
      dispatch(RESET());
    }
  }, [auth, didLogin, history, location, credentials, dispatch]);

  const handleLock = () => {
    if (isLocked === false) {
      setIsLocked(true);
    } else {
      setIsLocked(false);
    }
  };

  return (
    <section className="login-contents">
      <Register show={show} toggleregister={toggleRegister} />
      <MDBAnimation type="fadeIn">
        <div className="wave wave1"></div>
        <div className="wave wave2"></div>
        <div className="wave wave3"></div>
        <div className="wave wave4"></div>
      </MDBAnimation>
      <div className="wrappers">
        <div className="box"></div>
        <MDBAnimation type="fadeIn">
          <div className="hidingboy"></div>
        </MDBAnimation>

        <div className="login-form px-2">
          <h1 className="font-weight-bold m-0 text-center ">Welcome Back!</h1>
          <h6 className="m-0 text-center">Fill out the details below</h6>

          <form onSubmit={handleSubmit}>
            {email ? (
              <label>{email}</label>
            ) : (
              <MDBInput
                style={{ borderBottom: "1px solid black" }}
                label="Enter E-mail"
                type="email"
                icon="envelope"
                name="address"
                required
              />
            )}
            <div className="isLock">
              <MDBInput
                style={{ borderBottom: "1px solid black" }}
                className="pb-0"
                label="Password"
                name="password"
                minLength={8}
                icon="lock"
                type={isLocked ? "password" : "text"}
                required
              />
              <button className="isLock-eye" type="button" onClick={handleLock}>
                <MDBIcon icon={isLocked ? "eye-slash" : "eye"} />
              </button>
            </div>
            {message && (
              <div className="d-flex justify-content-center">
                <span
                  className={`alert alert-${
                    didLogin ? "success" : "warning"
                  } text-center m-0`}
                >
                  {message}
                </span>
              </div>
            )}
            <p className="my-0 ml-5 ">
              Dont't have an account?
              <button
                type="button"
                className="register"
                onClick={toggleRegister}
              >
                Register Here.
              </button>
            </p>
            <MDBBtn type="submit" color="primary" className="float-right mt-0">
              Login
            </MDBBtn>
          </form>
        </div>
      </div>
    </section>
  );
}
