import collections from "./collections.json";

const Tracks = {
  collections,
  find: (selected) => collections.find(({ name }) => name === selected),
  getStrand: (selected) =>
    collections.find(({ name }) => name === selected)?.strands,
  getName: (abbr) => {
    for (const track of collections) {
      const foundStrand =
        track.strands && track.strands.find((strand) => strand.abbr === abbr);
      if (foundStrand) {
        return foundStrand;
      }
    }
    return null; // Return null if the strand is not found
  },
  getTrackByAbbr: (abbr) => {
    for (const trackData of collections) {
      if (trackData.strands) {
        const matchingStrand = trackData.strands.find(
          (strand) => strand.abbr === abbr
        );
        if (matchingStrand) {
          return trackData.name;
        }
      } else if (trackData[abbr]) {
        return trackData.name;
      }
    }
    return null; // Return null if no match is found
  },

  getStrandByAbbr: (abbr) => {
    for (const trackData of collections) {
      if (trackData.strands) {
        const matchingStrand = trackData.strands.find(
          (strand) => strand.abbr === abbr
        );
        if (matchingStrand) {
          return matchingStrand;
        }
      } else {
        return "No Strand";
      }
    }
    return null; // Return null if no match is found
  },
  getTrackById: (id) => {
    for (const trackData of collections) {
      if (trackData.strands) {
        const matchingStrand = trackData.strands.find(
          (strand) => strand.id === id
        );
        if (matchingStrand) {
          return trackData.name;
        }
      } else if (trackData[id]) {
        return trackData.name;
      }
    }
    return null; // Return null if no match is found
  },
  getStrandById: (id) => {
    for (const track of collections) {
      const foundStrand =
        track.strands &&
        track.strands.find((strand) => strand.id === Number(id));
      if (foundStrand) {
        return foundStrand;
      }
    }
    return null; // Return null if the strand is not found
  },
  strandsByIds: (ids) => {
    const filteredStrands = collections.reduce((result, track) => {
      if (track.strands) {
        const filteredStrandsInTrack = track.strands.filter((strand) =>
          ids.includes(strand.id)
        );
        if (filteredStrandsInTrack.length > 0) {
          result.push({
            name: track.name,
            strands: filteredStrandsInTrack,
          });
        }
      }
      return result;
    }, []);

    return filteredStrands;
  },
};

export default Tracks;
