import React from "react";
import { useSelector } from "react-redux";
import { MDBCol, MDBRow } from "mdbreact";

import "../learner.css";
import "./identity.css";

const Identify = ({ setDetails, data }) => {
  const { auth } = useSelector(({ auth }) => auth),
  { birthplace, fullName, dob, mobile, isMale } = auth,
  { fname, mname, lname, suffix } = fullName

  const handleInputChange = (key, value) => {
    setDetails({ ...data, [key]: value });
  };

  return (
    <>
      <MDBRow style={{ backgroundColor: "#0000FF" }}>
        <MDBCol>
          <h5 className="font-weight-bold text-white label mt-2 mb-2 ml-5">
            I. LEARNER INFORMATION
          </h5>
        </MDBCol>
      </MDBRow>
      <MDBRow className="m-4">
        <MDBCol sm="6" lg="3">
          <h6 className="mt-4">
            <strong> <span className="red-text">*</span>Learner Reference No.</strong>
          </h6>        
          <input
            type="text"
            placeholder="LRN"
            value={data.LRN}
            name="LRN"
            onChange={(e) => handleInputChange(e.target.name, e.target.value)}
            min={1}
            maxLength={12}
            pattern="[0-9]*" 
          />
        </MDBCol>
      </MDBRow>
      <MDBRow className="mt-4 mr-4 ml-4">
        <MDBCol>
          <h6>
            <strong>Fullname</strong> - <i>Enter your full name correctly</i>
          </h6>
        </MDBCol>
      </MDBRow>
      <MDBRow className="mb-4 mr-4 ml-4 fullName">
        <MDBCol sm="6" lg="3" className="mt-3">
          <h6> <span className="red-text">*</span>First Name</h6>

          <input
            type="text"
            className="form-control"
            placeholder="First Name"
            value={fname}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3" className="mt-3">
          <h6>Middle Name</h6>

          <input
            type="text"
            className="form-control"
            placeholder="Middle Name"
            value={mname}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3" className="mt-3">
          <h6> <span className="red-text">*</span>Last Name</h6>

          <input
            type="text"
            className="form-control"
            placeholder="Last Name"
            value={lname}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3" className="mt-3">
          <h6>Suffix</h6>

          <input
            type="text"
            className="form-control"
            placeholder="Suffix"
            value={suffix || ""}
            readOnly
          />
        </MDBCol>
      </MDBRow>

      <MDBRow className="m-4">
        <MDBCol sm="6" lg="3">
          <h6>Place of birth </h6>

          <input 
            placeholder="Place of Birth" 
            className="form-control" 
            value={birthplace} 
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6> <span className="red-text">*</span>Date of birth </h6>

          <input
            placeholder="Date of Birth"
            className="form-control"
            type="text"
            value={dob}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6> <span className="red-text">*</span>Gender</h6>
          <input 
            type="text"
            placeholder="Gender"
            className="form-control"
            value={isMale ? "Male" : "female"}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6> <span className="red-text">*</span>Phone No.</h6>
          <input 
            placeholder="Phone No." 
            className="form-control" 
            type="text" 
            value={mobile} 
            readOnly
          />
        </MDBCol>
      </MDBRow>
    </>
  );
};

export default Identify;
