import React from "react";
import Countdown from "./countdown";
import "./index.css";

export default function Students({ fullView }) {
  const female = 24,
    male = 24;

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "space-around",
        alignItems: "center",
        marginBottom: "2.3rem",
        marginTop: "2.3rem",
      }}
    >
      <div className="half-box-left">
        <div
          className="loader-left"
          style={{
            marginLeft: fullView ? "2.8rem" : "1.7rem",
          }}
        />
        <div className="text-center ml-3">
          <h5 className="mb-0 male-text-top">
            {String(male).padStart(5, "0")}
          </h5>
          <h6 className="male-text-bottom">male</h6>
        </div>
      </div>
      <Countdown />
      <div className="half-box-right">
        <div
          className="loader-right"
          style={{
            marginLeft: fullView ? "-2.8rem" : "-1.7rem",
          }}
        />
        <div className="text-center mr-3">
          <h5 className="mb-0 female-text-top">
            {String(female).padStart(5, "0")}
          </h5>
          <h6 className="female-text-bottom ">female</h6>
        </div>
      </div>
      {/* <div className="half-box-left">
        <div className="loader-left"></div>
        <div
          style={{ zIndex: "9999", textAlign: "center", lineHeight: "1.2em" }}
        >
          <p className="half-box-text-left-top">12000</p>
          <p className="half-box-text-left-bottom">Male</p>
        </div>
      </div>

      <Countdown count={String(15000).padStart(5, "0")} />

      <div className="half-box-right">
        <div className="loader-right"></div>
        <div
          style={{
            zIndex: "9999",
            textAlign: "center",
            lineHeight: "1.2em",
          }}
        >
          <p className="half-box-text-right-top">12000</p>
          <p className="half-box-text-right-bottom">Female</p>
        </div>
      </div> */}
    </div>
  );
}
