import React, { useEffect, useState } from "react";
import "./style.css";

const FlipCard = ({ prevNum }) => {
  const [didChange, setDidChange] = useState(false),
    [top, setTop] = useState("0"),
    [bottom, setBottom] = useState("0");

  useEffect(() => {
    if (prevNum !== top) {
      setDidChange(true);
      setTop(prevNum);
      setTimeout(() => {
        setBottom(prevNum);
      }, 250);
    }
  }, [prevNum, top]);

  useEffect(() => {
    if (didChange) {
      setTimeout(() => {
        setDidChange(false);
      }, 500);
    }
  }, [didChange]);

  return (
    <div
      // remove the "!" in didChange
      className={`flip-card flip-card-text-md ${!didChange && "flip"}`}
      data-value={top}
    >
      <div className="flip-card-top">{top}</div>
      <div className="flip-card-bottom">{bottom}</div>
    </div>
  );
};

export default function Countdown() {
  const total = 500;

  return (
    <section className="mb-2">
      <div className="text-center">
        <div className="d-flex justify-content-center">
          {String(total)
            .padStart(5, "0")
            .split("")
            .map((top, index) => {
              return <FlipCard key={`countDown-${index}`} prevNum={top} />;
            })}
        </div>
      </div>
    </section>
  );
}
