import React from "react";
import { MDBRow, MDBCol } from "mdbreact";
import "./learner.css";
const Parents = ({ selected }) => {
  const { father, mother, guardian } = selected;
  return (
    <>
      <MDBRow style={{ backgroundColor: "#0000FF" }}>
        <MDBCol>
          <h5 className="font-weight-bold text-white mt-2 label mb-2 ml-5">
            III. PARENTS AND GUARDIANS INFORMATION
          </h5>
        </MDBCol>
      </MDBRow>
      <h5 className="font-weight-bold ml-5 mt-3">Father </h5>
      <MDBRow className="mt-3 ml-4">
        <MDBCol sm="6" lg="3">
          <h6>First Name</h6>
          <input
            placeholder="First Name"
            className="form-control"
            value={father.fullName?.fname}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Middle Name</h6>

          <input
            placeholder="Middle Name"
            className="form-control"
            value={father.fullName?.mname}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Last Name</h6>

          <input
            placeholder="Last Name"
            className="form-control"
            value={father.fullName?.lname}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Phone No.</h6>

          <input
            placeholder="Phone No."
            className="form-control"
            value={father.mobile}
            readOnly
          />
        </MDBCol>
      </MDBRow>

      <h5 className="font-weight-bold ml-5 mt-3">Mother</h5>

      <MDBRow className="mt-3 ml-4">
        <MDBCol sm="6" lg="3">
          <h6>First Name</h6>

          <input
            placeholder="First Name"
            className="form-control"
            value={mother.fullName?.lname}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Middle Name</h6>

          <input
            placeholder="Middle Name"
            className="form-control"
            value={mother.fullName?.mname}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Last Name</h6>

          <input
            placeholder="Last Name"
            className="form-control"
            value={mother.fullName?.lname}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Phone No.</h6>

          <input
            placeholder="Phone No."
            className="form-control"
            value={mother.mobile}
            readOnly
          />
        </MDBCol>
      </MDBRow>

      <h5 className="font-weight-bold ml-5 mt-3">Guardian </h5>
      <MDBRow className="mt-3 ml-4">
        <MDBCol sm="6" lg="3">
          <h6>First Name</h6>

          <input
            placeholder="First Name"
            className="form-control"
            value={guardian.fullName?.fname}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Middle Name</h6>

          <input
            placeholder="Middle Name"
            className="form-control"
            value={guardian.fullName?.mname}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Last Name</h6>

          <input
            placeholder="Last Name"
            className="form-control"
            value={guardian.fullName?.lname}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Phone No.</h6>

          <input
            placeholder="Phone No."
            className="form-control"
            value={guardian.mobile}
            readOnly
          />
        </MDBCol>
      </MDBRow>
    </>
  );
};

export default Parents;
