import React from "react";
import "./index.css";

export default function Status() {
  const dashboard = { status: { enrolled: 25, pending: 25, rejected: 20 } };
  const { status = {} } = dashboard;

  return (
    <div className="status-container mt-3">
      {Object.entries(status)?.map(([key, value]) => {
        return (
          <div className="status-loader" key={`studentTypes-${key}`}>
            <div className="status-load-inner">
              <p className="status-load-inner-text-top">
                {String(value).padStart(5, "0")}
              </p>
              <p className="status-load-inner-text-bottom">{key}</p>
            </div>
            <span className="status-loader-span status-animation"></span>
          </div>
        );
      })}
    </div>
  );
}
