const isValid = (collections, form, oldName) => {
  if (form.lvl && form.name && !form.strand) {
    return collections.some(
      ({ lvl, name }) =>
        lvl === form.lvl &&
        name.replace(/\s/g, "") === form.name.replace(/\s/g, "") &&
        form.name.replace(/\s/g, "") !== oldName?.replace(/\s/g, "")
    );
  } else if (form.lvl && form.name && form.strand) {
    return collections.some(
      ({ lvl, name, strand, semester }) =>
        lvl === form.lvl &&
        name.replace(/\s/g, "") === form.name.replace(/\s/g, "") &&
        form.strand === strand &&
        semester === form.semester &&
        form.name.replace(/\s/g, "") !== oldName?.replace(/\s/g, "")
    );
  }
  return false;
};

export default isValid;
