import { MDBIcon } from "mdbreact";
import React, { useEffect, useState } from "react";

function Grades({
  activeId,
  setActiveId,
  id,
  obj,
  handleSubmit,
  grades,
  transcriptID,
}) {
  const [grade, setGrade] = useState(0);

  useEffect(() => {
    setGrade(obj.grade);
  }, [obj]);

  return (
    <>
      {activeId === `${id}-${obj.title}` ? (
        <div className=" m-0">
          <input
            style={{ width: "60px" }}
            type="number"
            className="mr-1"
            min={0}
            max={100} // Ito ang maximum na pwede
            value={String(Math.min(grade, 100))} // I-check kung ang grade ay hindi hihigit sa 100
            onChange={(e) => setGrade(Number(e.target.value))}
          />
          <MDBIcon
            icon="times"
            size="lg"
            onClick={() => {
              setActiveId("");
            }}
            className="text-center mr-2 cursor-pointer"
            style={{ color: "red" }}
          ></MDBIcon>
          <MDBIcon
            icon="check"
            size="lg"
            onClick={() => {
              handleSubmit(
                grade,
                setGrade,
                obj.title,
                obj.id,
                grades,
                transcriptID,
                obj.remarks,
                obj.finals
              );
              setActiveId("");
            }}
            className="text-center cursor-pointer m-0 p-o"
            style={{ color: "green" }}
          ></MDBIcon>
        </div>
      ) : (
        <h6
          onClick={() => setActiveId(`${id}-${obj.title}`)}
          className={`cursor-pointer font-weight-bold 
             ${grade > 0 ? "disabled" : ""} `}
        >
          {grade === 0 ? "--" : grade}
        </h6>
      )}
    </>
  );
}

export default Grades;
