import React from "react";
import { MDBBtn, MDBIcon } from "mdbreact";

const Search = ({
  handleSearch,
  didSearch = false,
  setDidSearch,
  setShow,
  array,
  setArray,
  handleUpdate,
  handleExcel,
  handleDelete,
  handleView,
  isRegistrar = false,
  isTeacher = false,
  disableMultipleApproved = true,
}) => {
  return (
    <form
      id="requirements-inline-search"
      onSubmit={handleSearch}
      className="form-inline ml-2"
    >
      <div className="form-group md-form py-0 mt-0 ">
        <input
          className="form-control w-80 placeholder-white text-white"
          type="text"
          placeholder="Search..."
          name="searchKey"
          required
        />
        <MDBBtn
          onClick={() => {
            if (!didSearch) return;

            setDidSearch(false);
            document.getElementById("requirements-inline-search").reset();
            setArray(array);
          }}
          type={didSearch ? "button" : "submit"}
          size="sm"
          color="white"
          rounded
          outline
          className="d-inline ml-2 px-2"
        >
          <MDBIcon icon={didSearch ? "times" : "search"} />
        </MDBBtn>
        {isRegistrar ? (
          <>
            {!disableMultipleApproved && (
              <MDBBtn
                outline
                color="white"
                rounded
                size="sm"
                className="px-2"
                title="Multiple Approved"
                onClick={handleView}
              >
                <MDBIcon icon="users" className="mt-0" />
              </MDBBtn>
            )}
          </>
        ) : !isTeacher ? (
          <>
            <MDBBtn
              outline
              color="white"
              rounded
              size="sm"
              className="px-2"
              title="Create a Batch"
              onClick={() => setShow(true)}
            >
              <MDBIcon icon="plus" className="mt-0" />
            </MDBBtn>
            <MDBBtn
              outline
              color="white"
              rounded
              size="sm"
              className="px-2 m-0"
              onClick={handleUpdate}
            >
              <MDBIcon icon="pencil-alt" className="mt-0" />
            </MDBBtn>
            <MDBBtn
              outline
              color="white"
              rounded
              size="sm"
              className="px-2"
              onClick={handleDelete}
            >
              <MDBIcon icon="trash" className="mt-0" />
            </MDBBtn>
          </>
        ) : (
          <MDBBtn
            size="sm"
            rounded
            color="white"
            onClick={handleExcel}
            outline
            className="px-2"
            title="Export in excel"
          >
            <MDBIcon icon="file-excel" />
          </MDBBtn>
        )}
      </div>
    </form>
  );
};

export default Search;
