import React from "react";
import {
  MDBCard,
  MDBCardBody,
  MDBCardImage,
  MDBCol,
  MDBIcon,
  MDBRow,
} from "mdbreact";

import logo from "../../assets/logo/logo.png";
import deped from "../../assets/logo/deped.png";
import { fullName } from "../../services/utilities";

import Table from "./table";

function Card({
  setIsViewCard,
  selected,
  isStudent = false,
  batches = [],
  batch = {},
  isViewAdviser = false,
  setBatch,
}) {
  console.log(selected);
  return (
    <>
      {!isStudent ? (
        <MDBCard style={{ borderRadius: "10px" }}>
          <MDBCardBody>
            <MDBRow className="m-0 p-0">
              <MDBCol md="12" className="d-flex justify-content-end m-0 p-0">
                <MDBIcon
                  icon="times"
                  size="1x"
                  onClick={() => setIsViewCard(false)}
                  className="cursor-pointer"
                />
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol className="d-flex justify-content-end">
                <MDBCardImage src={logo} style={{ height: "100px" }} />
              </MDBCol>
              <MDBCol className="text-center" md="4">
                <h5
                  className="m-0 "
                  style={{ fontFamily: "Old English Text MT" }}
                >
                  Republic of the Philippines
                </h5>
                <h4
                  className="m-0 "
                  style={{ fontFamily: "Old English Text MT" }}
                >
                  Department of Education
                </h4>
                <h6
                  className="mt-0 font-weight-bold"
                  style={{ fontFamily: "Times New Roman" }}
                >
                  Region III
                </h6>
                <h6
                  className="mt-0 font-weight-bold"
                  style={{ fontFamily: "Times New Roman" }}
                >
                  Division of nueva ecija
                </h6>
                <h3
                  className="mt-0 font-weight-bold"
                  style={{ fontFamily: "Times New Roman" }}
                >
                  Leonor M. Bautista National High School
                </h3>
                <h6
                  className="mt-0 font-weight-bold"
                  style={{ fontFamily: "Times New Roman" }}
                >
                  General Tinio Nueva Ecija
                </h6>
              </MDBCol>

              <MDBCol>
                <MDBCardImage src={deped} style={{ height: "100px" }} />
              </MDBCol>
            </MDBRow>

            <MDBRow className="mt-2">
              <MDBCol md="0" className="ml-3">
                <h6>LRN:</h6>
              </MDBCol>
              <MDBCol md="2" className="ml-2">
                <h6>
                  <strong> {selected?.LRN} </strong>
                </h6>
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="0" className="ml-3">
                <h6>Name:</h6>
              </MDBCol>
              <MDBCol md="11">
                <h6>
                  <strong>
                    {fullName(selected?.student?.fullName)?.toUpperCase()}
                  </strong>
                </h6>
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="0" className="ml-3">
                <h6>Grade:</h6>
              </MDBCol>
              <MDBCol md="11">
                <h6>
                  <strong>{`${selected.lvl} ${
                    selected.strand ? selected?.strand : ""
                  } - ${selected.section?.name}`}</strong>
                </h6>
              </MDBCol>
            </MDBRow>
            {!isViewAdviser && (
              <MDBRow>
                <MDBCol md="0" className="ml-3">
                  <h6>Adviser:</h6>
                </MDBCol>
                <MDBCol md="11" className="m-0 p-0 ml-1">
                  <h6>
                    <strong>
                      {fullName(
                        selected?.section?.adviser?.fullName
                      )?.toUpperCase()}
                    </strong>
                  </h6>
                </MDBCol>
              </MDBRow>
            )}

            <MDBRow>
              <MDBCol className="text-center">
                <h6 className="font-weight-bold mt-2">
                  REPORT ON LEARNING PROGRESS AND ACHIEVEMENT
                </h6>
                <h6>
                  Academic School year: <strong> {selected?.batch?.sy} </strong>
                  {selected.lvl > 10 && selected.semester === "1st" && (
                    <strong className="font-weight-bold">First Semester</strong>
                  )}
                  {selected.lvl > 10 && selected.semester === "2nd" && (
                    <strong className="font-weight-bold">
                      Second Semester
                    </strong>
                  )}
                </h6>
              </MDBCol>
            </MDBRow>

            <Table selected={selected} isStudent={isStudent} />
          </MDBCardBody>
        </MDBCard>
      ) : (
        <>
          <MDBCardBody className="m-0 p-0">
            <MDBRow>
              <MDBCol className="d-flex justify-content-end">
                <MDBCardImage src={logo} style={{ height: "100px" }} />
              </MDBCol>
              <MDBCol className="text-center" md="4">
                <h5 className="m-0" style={{ fontFamily: "Times New Roman" }}>
                  Republic of the Philippines
                </h5>
                <h4 className="m-0" style={{ fontFamily: "Sans" }}>
                  Department of Education
                </h4>
                <h5 className="m-0">Region III</h5>
                <h5>Division of Nueva Ecija</h5>
                <h4
                  className="font-weight-bold mt-0 m-0 "
                  style={{ fontFamily: "Open Sans" }}
                >
                  Leonor M. Bautista National High School
                </h4>
                <h5>General Tinio Nueva Ecija</h5>
              </MDBCol>
              <MDBCol>
                <MDBCardImage src={deped} style={{ height: "100px" }} />
              </MDBCol>
            </MDBRow>
            <MDBRow className="mt-2">
              <MDBCol md="0" className="ml-3">
                <h6>LRN:</h6>
              </MDBCol>
              <MDBCol md="2" className="ml-2">
                <h6>
                  <strong> {selected?.LRN} </strong>
                </h6>
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="0" className="ml-3">
                <h6>Name:</h6>
              </MDBCol>
              <MDBCol md="11">
                <h6>
                  <strong>
                    {fullName(selected?.student?.fullName)?.toUpperCase()}
                  </strong>
                </h6>
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="0" className="ml-3">
                <h6>Grade:</h6>
              </MDBCol>
              <MDBCol md="11">
                <h6>
                  <strong>{`${selected.lvl} ${
                    selected.strand ? selected?.strand : ""
                  } - ${selected.section?.name}`}</strong>
                </h6>
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="0" className="ml-3">
                <h6>Adviser:</h6>
              </MDBCol>
              <MDBCol md="11" className="m-0 p-0 ml-1">
                <h6>
                  <strong>
                    {fullName(
                      selected?.section?.adviser?.fullName
                    )?.toUpperCase()}
                  </strong>
                </h6>
              </MDBCol>
            </MDBRow>
            <MDBRow className="d-flex justify-content-center">
              <MDBCol md="4">
                <select
                  className="form-control text-center "
                  value={batch}
                  onChange={(e) => setBatch(e.target.value)}
                >
                  {batches.map(({ text, value }, index) => (
                    <option key={index} value={value}>
                      SY {text}
                    </option>
                  ))}
                </select>
              </MDBCol>
            </MDBRow>

            <MDBRow>
              <MDBCol className="text-center">
                <h6 className="font-weight-bold mt-2">
                  REPORT ON LEARNING PROGRESS AND ACHIEVEMENT
                </h6>
              </MDBCol>
            </MDBRow>

            <Table selected={selected} isStudent={true} />
          </MDBCardBody>
        </>
      )}
    </>
  );
}

export default Card;
