import React from "react";
import { useSelector } from "react-redux";
import { MDBRow, MDBCol } from "mdbreact";

import "../learner.css";

import AddressSelect from "../../../../../components/addressSelect";

const Address = () => {
  
  const { auth } = useSelector(({ auth }) => auth),
  { address } = auth,
  { current, permanent } = address;

  return (
    <>
      <MDBRow style={{ backgroundColor: "#0000FF" }}>
        <MDBCol>
          <h5 className="font-weight-bold text-white mt-2 label mb-2 ml-5">
            III. ADDRESS INFORMATION
          </h5>
        </MDBCol>
      </MDBRow>

      <MDBRow className="p-2 mt-4 mr-4 ml-4">
        <MDBCol>
          <AddressSelect
            label="Current Address"
            address={current}
          />
        </MDBCol>
      </MDBRow>
      <MDBRow className="p-2 mr-4 ml-4">
        <MDBCol>
          <AddressSelect
            label="Permanent Address"
            address={permanent}
          />
        </MDBCol>
      </MDBRow>
    </>
  );
};

export default Address;
