import React from "react";
import { MDBRow, MDBCol } from "mdbreact";

import "./learner.css";

import AddressSelect from "../../components/addressSelect";

const Address = ({ selected }) => {
  const { address } = selected.student;

  return (
    <>
      <MDBRow className="mt-3" style={{ backgroundColor: "#0000FF" }}>
        <MDBCol>
          <h5 className="font-weight-bold text-white mt-2 label mb-2 ml-5">
            II. ADDRESS INFORMATION
          </h5>
        </MDBCol>
      </MDBRow>

      <MDBRow className="p-2 ml-3 m-2">
        <MDBCol>
          <AddressSelect
            label="Current Address"
            address={address.current}
            view
          />
        </MDBCol>
      </MDBRow>
      <MDBRow className="p-2 ml-3 m-2">
        <MDBCol>
          <AddressSelect
            label="Permanent Address"
            address={address.permanent}
            view
          />
        </MDBCol>
      </MDBRow>
    </>
  );
};

export default Address;
