import React, { useEffect, useState } from "react";
import { MDBBtn, MDBPopover, MDBPopoverBody, MDBTable } from "mdbreact";
import { fullName } from "../../services/utilities";
import { days, times } from "../../services/fakeDb";
//initial Declare
const Scheduler = ({ array, props, isTeacher = false }) => {
  const [schedules, setSchedules] = useState([]);

  const generateRandomKey = () => {
    const characters =
      "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    let randomKey = "";
    for (let i = 0; i < 10; i++) {
      randomKey += characters.charAt(
        Math.floor(Math.random() * characters.length)
      );
    }
    return randomKey;
  };

  const formatTime = (time) => {
    let hours = Number(time.hours);
    const min = String(time.min);
    const period = time.period;

    if (hours >= 1 && hours <= 5 && period === "PM") {
      hours += 12;
    }

    return { hours, min, period };
  };

  useEffect(() => {
    setSchedules([]);
    for (let parentIndex = 0; parentIndex < array.length; parentIndex++) {
      const element = array[parentIndex];
      const schedules = element.schedules || [];
      for (let index = 0; index < schedules.length; index++) {
        const { days, time, color } = schedules[index];

        for (let daysIndex = 0; daysIndex < days?.length; daysIndex++) {
          const day = days[daysIndex];
          const newSched = {
            day,
            timeStart: formatTime(time.start),
            timeEnd: formatTime(time.end),
            subject: element.subject?.name,
            color,
            instructor: fullName(element.instructor?.fullName),
            id: `${day}-${generateRandomKey()}`,
            section: element.section || {},
            form: { ...element, days, time },
          };

          setSchedules((prev) => [newSched, ...prev]);
        }
      }
    }
  }, [array]);

  const handleHeight = (
    startTimeIndex,
    endedTimeIndex,
    timeIndex,
    startTime,
    endTime,
    color,
    gradient,
    endedGradient
  ) => {
    var _style = {
      marginTop: "0px",
      background: `linear-gradient(to bottom, ${color}, ${color})` /* Adjust colors as needed */,
      height: "100%",
      width: "98%",
      position: "absolute",
      overflow: "visible",
      boxShadow: "0px 0px 0px 0px",
    };

    if (timeIndex === endedTimeIndex) {
      if (endTime.min === "30") {
        _style.height = "62px";
      } else if (endTime.min === "45") {
        _style.height = "79px";
        _style.zIndex = 1;
      } else {
        // 33px yan talaga height niya
        if (endedGradient) {
          _style.height = "64px";
          _style.background = `linear-gradient(to bottom, ${color} 50%,${endedGradient} 50%)`;
        } else {
          _style.height = "33px";
        }
      }
    } else {
      _style.height = "64px";
    }
    if (startTimeIndex === timeIndex) {
      if (startTime.hours !== 7) {
        _style.background = `linear-gradient(to bottom, ${gradient} 50%,${color} 50%)`;
        _style.backgroundSize = "20px";
      }
    }

    return _style;
  };

  const findSchedule = (array, time) => {
    return array.find((sched) => {
      const startTime =
        sched.timeStart.hours * 60 + parseInt(sched.timeStart.min);

      const endTime = sched.timeEnd.hours * 60 + parseInt(sched.timeEnd.min);
      const currentTime = time.value * 60;
      return currentTime >= startTime && currentTime <= endTime;
    });
  };

  const showSubject = (
    subject,
    timeStart,
    timeEnd,
    section,
    obj,
    isPopover = false
  ) => {
    const startTime =
      timeStart.hours > 12 ? timeStart.hours - 12 : timeStart.hours;
    const endTime = timeEnd.hours > 12 ? timeEnd.hours - 12 : timeEnd.hours;

    const startTimeFormatted = `${startTime}:${timeStart.min.padStart(
      2,
      "0"
    )} ${timeStart.period}`;
    const endTimeFormatted = `${endTime}:${timeEnd.min.padStart(2, "0")} ${
      timeEnd.period
    }`;

    const subjectName = isTeacher ? section.name : subject;
    const hoursDiff = timeEnd.hours - timeStart.hours;
    var bottomPX = "0px";
    if (hoursDiff > 2 && timeStart.min !== "30") {
      bottomPX = "-60px";
    } else if (hoursDiff === 2 && timeStart.min !== "30") {
      bottomPX = "-30px";
    } else if (hoursDiff === 2 && timeStart.min === "30") {
      bottomPX = "10px";
    } else if (hoursDiff === 1 && timeStart.min === "0") {
      bottomPX = "-10px";
    } else if (hoursDiff > 2 && timeStart.min === "30") {
      bottomPX = "-20px";
    } else {
      bottomPX = "30px";
    }
    return (
      <div
        style={
          !isPopover
            ? {
                position: "absolute",
                left: 10,
                zIndex: 3,
                maxHeight: "100%",
                width: "90%",
                height: "50%",
                bottom: bottomPX,
                textAlign: "center",
                maxWidth: "100%",
              }
            : {}
        }
      >
        <span
          style={{ fontSize: "14px" }}
          className={`text-${isPopover ? "black" : "white"}`}
        >
          {isTeacher ? ` G${section.lvl} -` : ""} {subjectName} <br />
          {startTimeFormatted} - {endTimeFormatted}
        </span>
        <br />
        {!isPopover && hoursDiff >= 2 ? (
          <span>{isTeacher ? subject : obj.instructor}</span>
        ) : (
          ""
        )}
      </div>
    );
  };

  const renderSameStartAndEndHour = ({
    findSameStartHour,
    id,
    startTimeIndex,
    endedTimeIndex,
    timeIndex,
    timeStart,
    timeEnd,
    color,
    gradient,
    endedGradient,
  }) => {
    return (
      <td
        className="m-0 p-0 "
        key={`${id}`}
        style={{
          position: "relative",
        }}
      >
        <MDBPopover placement="right" popover clickable>
          <MDBBtn
            onClick={() => handleSetForm(findSameStartHour.form)}
            color={color}
            style={handleHeight(
              startTimeIndex,
              endedTimeIndex,
              timeIndex,
              timeStart,
              timeEnd,
              color,
              gradient,
              endedGradient
            )}
          >
            {showSubject(
              findSameStartHour.subject,
              findSameStartHour.timeStart,
              findSameStartHour.timeEnd,
              findSameStartHour.section,
              findSameStartHour
            )}
          </MDBBtn>
          <div>
            <MDBPopoverBody className="text-center">
              {showSubject(
                findSameStartHour.subject,
                findSameStartHour.timeStart,
                findSameStartHour.timeEnd,
                findSameStartHour.section,
                findSameStartHour,
                true
              )}

              {isTeacher
                ? findSameStartHour.subject.toUpperCase()
                : findSameStartHour.instructor.toUpperCase()}
            </MDBPopoverBody>
          </div>
        </MDBPopover>
      </td>
    );
  };

  const renderStartTime45Mins = (findThemin, timeIndex, startTimeIndex) => {
    const {
      timeEnd,
      section,
      subject,
      color,
      id,
      origTimeStart,
      instructor,
      form,
    } = findThemin;

    return (
      <td className="m-0 p-0 " key={`-${id}`} style={{ position: "relative" }}>
        <MDBPopover placement="right" popover clickable>
          <MDBBtn
            onClick={() => handleSetForm(form)}
            style={{
              height: timeEnd.min === "45" ? "65px" : "62px",
              width: "96%",
              marginTop: "15px",
              position: "absolute",
              overflow: "visible",
              zIndex: timeIndex === startTimeIndex ? 3 : 1,
              boxShadow: "0px 0px 0px 0px",
              background: `linear-gradient(to bottom, ${color}, ${color})`,
            }}
          >
            {timeIndex === startTimeIndex &&
              showSubject(subject, origTimeStart, timeEnd, section, findThemin)}
          </MDBBtn>
          <div>
            <MDBPopoverBody className="text-center">
              {showSubject(
                subject,
                origTimeStart,
                timeEnd,
                section,
                findThemin,
                true
              )}

              {isTeacher ? subject.toUpperCase() : instructor.toUpperCase()}
            </MDBPopoverBody>
          </div>
        </MDBPopover>
      </td>
    );
  };

  // para kapag nag update ng schedule
  const handleSetForm = (form) => {
    if (!props) return false;
    if (props.willCreate) return false;

    const { time, instructor, subject, days, section } = form || {};
    const { start, end } = time || {};
    props.setForm((prev) => ({
      ...prev,
      _id: form._id,
      instructor: instructor._id,
      subject: subject._id,
      section: section._id,
      oldDays: days,
    }));

    props.setPickDays(days);
    props.setHourStart(Number(start.hours));
    props.setMinStart(Number(start.min));
    props.setPeriodStart(start.period);
    props.setHourEnd(Number(end.hours));
    props.setMinEnd(Number(end.min));
    props.setPeriodEnd(end.period);
  };

  const HR = (day) => {
    return (
      <td className="m-0 p-0 position-relative" key={day}>
        <hr
          style={{
            marginTop: "31.2px",
            border: "#d7d7d6 solid 1px",
          }}
        />
      </td>
    );
  };

  return (
    <div>
      <MDBTable
        style={{
          border: "#d7d7d6 solid 2px",
        }}
      >
        <thead>
          <tr
            style={{
              borderBottom: "black solid 2px",
              backgroundColor: "#0000FF",
            }}
          >
            <th className="text-white font-weight-bold th-sm">Time</th>
            {days.map((day, index) => (
              <th
                className="text-center text-white  th-lg font-weight-bold "
                key={index}
              >
                {day.text.toUpperCase()}
              </th>
            ))}
          </tr>
        </thead>

        <tbody>
          {times.map((time, timeIndex) => (
            <tr
              key={`-${generateRandomKey()}-schedules`}
              style={{ border: "#d7d7d6 solid 2px" }}
            >
              <td className="d-flex align-items-center justify-content-center pr-0">
                <h6 className="text-nowrap">{time.text}</h6>
                <hr
                  style={{
                    width: "100%",
                    margin: "0",
                    marginLeft: "4px",
                    border: "#d7d7d6 solid 1px",
                  }}
                />
              </td>
              {days.map((day) => {
                const template = schedules.filter(
                  (sched) =>
                    sched.day.toLocaleLowerCase() ===
                    day.value.toLocaleLowerCase()
                );

                //para iupdate yung sched start hours niya ng + 1 kapag 45 minutes yung start hour ng schedule
                template.forEach((data) => {
                  if (data?.timeStart?.min === "45") {
                    data.origTimeStart = { ...data.timeStart };
                    data.timeStart = {
                      ...data.timeStart,
                      min: "0",
                      hours: (data.timeStart?.hours || 0) + 1,
                    };
                    data.is45Mins = true;
                  }
                });

                const schedule = findSchedule(template, time);

                if (schedule) {
                  const {
                    timeStart = {},
                    timeEnd = {},
                    subject = "",
                    id = 1,
                    color = "",
                    instructor = "",
                    section = {},
                    is45Mins = false,
                    form = {},
                  } = schedule;
                  // para kahit meron nang 1 to 2 na schedule pwede kapang mag lagay ng 2 to 3
                  const findSameEndHour = template.find(
                    ({ timeEnd: _timeEnd }) =>
                      _timeEnd.hours === timeStart.hours
                  );
                  //para kahit meron nang 1 to 2 na schedule pwede kapang mag lagay ng 12 to 1
                  const findSameStartHour = template.find(
                    ({ timeStart: _timeStart }) =>
                      timeEnd.hours === _timeStart.hours
                  );

                  const gradient = findSameEndHour
                    ? findSameEndHour.color
                    : "white";

                  const endedGradient = findSameStartHour
                    ? findSameStartHour.timeStart?.min === "0" &&
                      findSameStartHour.color
                    : "";

                  const endedTimeIndex = times.findIndex(
                    (time) => time.value === timeEnd.hours
                  );
                  const startTimeIndex = times.findIndex(
                    (time) => time.value === timeStart.hours
                  );

                  if (is45Mins) {
                    return renderStartTime45Mins(
                      schedule,
                      startTimeIndex,
                      timeIndex
                    );
                  }

                  if (
                    findSameStartHour?.section &&
                    endedTimeIndex === timeIndex &&
                    findSameStartHour.timeStart?.min === "0"
                  ) {
                    return renderSameStartAndEndHour({
                      findSameStartHour,
                      id,
                      startTimeIndex,
                      endedTimeIndex,
                      timeIndex,
                      timeStart,
                      timeEnd,
                      color,
                      gradient,
                      endedGradient,
                      subject,
                      schedule,
                    });
                  }

                  return (
                    <td className="m-0 p-0 position-relative" key={`${id}`}>
                      <MDBPopover placement="right" popover clickable>
                        <MDBBtn
                          onClick={() => handleSetForm(form)}
                          style={handleHeight(
                            startTimeIndex,
                            endedTimeIndex,
                            timeIndex,
                            timeStart,
                            timeEnd,
                            color,
                            gradient,
                            endedGradient
                          )}
                        >
                          {timeStart.min === "30" ? (
                            startTimeIndex + 1 === timeIndex ? (
                              showSubject(
                                subject,
                                timeStart,
                                timeEnd,
                                section,
                                schedule
                              )
                            ) : (
                              ""
                            )
                          ) : startTimeIndex === timeIndex ? (
                            <>
                              {showSubject(
                                subject,
                                timeStart,
                                timeEnd,
                                section,
                                schedule
                              )}
                            </>
                          ) : (
                            ""
                          )}
                        </MDBBtn>

                        <div>
                          <MDBPopoverBody className="text-center">
                            {showSubject(
                              subject,
                              timeStart,
                              timeEnd,
                              section,
                              schedule,
                              true
                            )}
                            {isTeacher
                              ? subject.toUpperCase()
                              : instructor.toUpperCase()}
                          </MDBPopoverBody>
                        </div>
                      </MDBPopover>
                    </td>
                  );
                } else {
                  return HR(day.value);
                }
              })}
            </tr>
          ))}
        </tbody>
      </MDBTable>
    </div>
  );
};

export default Scheduler;
