import React, { useEffect } from 'react'
import { MDBIcon, MDBModal, MDBModalBody, MDBModalHeader } from 'mdbreact'
import "./style.css";
// import picture from "./img/2x2.png";
// import sign from "./img/sign.png";
// import psa from "./img/psa.png";
// import birthCert from "./img/birthCert.png";

/**
 * 
 * @param {String} image - file name or image
 * @param {Boolean} show - show the modal
 * @param {Boolean} toggle - close the modal
 */
const ViewImage = ({ image, show, toggle }) => {

    useEffect(() => {
        if (show) {
            document.body.classList.add('body-modal-open');
        } else {
            document.body.classList.remove('body-modal-open');
        }
    }, [show]);

    return (
        <>
            <MDBModal toggle={() => {}} isOpen={show} centered size="lg" className='content-modal bg-trans '>
                <MDBModalHeader className='header-modal bg-trans '>
                    <MDBIcon far icon="times-circle" className='xmark' onClick={toggle}></MDBIcon>
                </MDBModalHeader>
                <MDBModalBody className='body-modal bg-trans '>
                    <img src={image} alt="Enlarged"  style={{ width: '100%', height: '100%', objectFit: 'contain' }}/>
                </MDBModalBody>
            </MDBModal>
        </>
    )
}

export default ViewImage