const StoreDefualtValueForForm = ({
  form,
  setForm,
  collections = [],
  setArray,
  isRegistrar = false,
  setIsSelectAll = () => {},
}) => {
  if (form.level === 1) return setArray(collections);
  if (collections.length === 0) return setArray([]);
  setIsSelectAll(false);

  const levels = removeDuplicate(collections, "lvl");
  levels.sort((a, b) => a - b);

  const specializations = removeDuplicate(
    collections,
    "specialization",
    form.level
  );
  const strands = removeDuplicate(collections, "strand");
  const isChangeLevel = levels.includes(form.level);

  // para malaman kung wala ba yung form level nayun sa mga levels na meron siya
  const _level = !isChangeLevel ? levels[0] : form.level;
  const _strand = form.strand || strands[0];
  const semesters = removeDuplicate(collections, "semester");
  const sections = isRegistrar
    ? removeDuplicateSection(
        collections
          .filter(({ lvl, strand }) =>
            _level > 10 ? lvl === _level && strand === _strand : lvl === _level
          )
          .map(({ section }) => section)
      )
    : [];

  const section = isRegistrar ? form?.section || sections[0]?._id : "";
  setForm({
    level: _level,
    strand: _strand,
    strands,
    specialization: form.specialization || specializations[0],
    specializations,
    semester: form.semester || semesters[0],
    semesters,
    levels,
    sections,
    section,
  });

  StoreValueForForm(
    form,
    setForm,
    setArray,
    collections,
    isRegistrar,
    sections
  );
};

const StoreValueForForm = (
  form,
  setForm,
  setArray,
  collections,
  isRegistrar,
  sections = []
) => {
  const specializations = removeDuplicate(
    collections,
    "specialization",
    form.level
  );
  var newSpecialization = form.specialization;

  const isChangeLvl = specializations.includes(form.specialization);

  if (!isChangeLvl) {
    newSpecialization = specializations[0];
  }

  const strandStorage =
    collections.filter(
      (sp) => sp.specialization === newSpecialization && sp.lvl === form.level
    ) || [];

  const strands = removeDuplicate(strandStorage, "strand");

  var newStrand = form.strand;

  const isChangeSpecialization = collections
    .filter(({ strand: st }) => st === form.strand)
    .some(({ specialization: sp }) => sp === newSpecialization);

  //para malaman kung nag change ba ng specialization
  if (!isChangeSpecialization || strands.includes(newStrand) === false) {
    newStrand = strands[0];
  }
  // pag registar sinasama ko yugn section para dun ako kukuha ng semester
  const section = isRegistrar
    ? handleSection(collections, newStrand, form, sections)
    : "";

  const semesters = Array.from(
    new Set(
      collections
        .filter((sm) =>
          isRegistrar
            ? sm.strand === newStrand &&
              sm.lvl === form.level &&
              sm.section?._id === section
            : sm.strand === newStrand && sm.lvl === form.level
        )
        .map(({ semester: sem }) => sem)
    )
  );

  var newSemester = form.semester;

  //para icheck kung wala bang ganung semester sa strand nayun
  if (!isChangeSpecialization || !semesters.includes(form.semester)) {
    newSemester = semesters[0];
  }

  //
  //kapag yung level ay naging senior high isasama yung strand tas semester sa filtering
  const isShs = form.level > 10 ? true : false;
  const container = collections.filter(
    ({ lvl, strand: str, semester: sem, section: sec = { _id: "" } }) => {
      // Common condition for both cases
      const commonCondition = lvl === form.level;

      if (!isRegistrar) {
        // Additional condition for non-registrar case
        return isShs
          ? commonCondition && newStrand === str && sem === newSemester
          : commonCondition;
      } else {
        // Additional condition for registrar case
        return isShs
          ? commonCondition &&
              newStrand === str &&
              sem === newSemester &&
              section === sec?._id
          : commonCondition && section === sec?._id;
      }
    }
  );

  setForm((prev) => ({
    ...prev,
    section,
    specializations,
    semester: newSemester || form.semester,
    strands,
    strand: newStrand,
    specialization: newSpecialization || specializations[0],
    semesters: semesters || [],
  }));

  setArray(container || []);
};

const removeDuplicate = (array, key, lvl) => {
  if (key === "specialization") {
    return (
      Array.from(
        new Set(
          array
            .filter((obj) => obj.lvl > 10 && obj.lvl === lvl)
            .map((obj) => obj[key])
            .filter(Boolean)
        )
      ) || []
    );
  } else {
    return (
      Array.from(new Set(array.map((obj) => obj[key]).filter(Boolean))) || []
    );
  }
};

const removeDuplicateSection = (array) => {
  const idMap = new Map();
  const uniqueData = [];
  array.forEach((item) => {
    // Kung wala pang existing na _id, idagdag ang item sa uniqueData at sa idMap
    if (!idMap.has(item?._id)) {
      idMap.set(item?._id, true);
      uniqueData.push(item);
    }
  });
  return uniqueData;
};

const handleSection = (collections, newStrand, form, sections) => {
  const _newCollections = collections.filter(({ lvl, strand }) =>
    form.level > 10
      ? lvl === form.level && strand === newStrand
      : lvl === form.level
  );

  const isChangeSection = _newCollections.some(
    ({ section }) => section?._id === form.section
  );

  return !isChangeSection ? sections[0]?._id : form?.section;
};

export default { StoreDefualtValueForForm, StoreValueForForm };
