import React, { useState, useEffect } from "react";
import {
  MDBBtn,
  MDBCol,
  MDBContainer,
  MDBIcon,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBRow,
} from "mdbreact";
import { useToasts } from "react-toast-notifications";
import { useDispatch, useSelector } from "react-redux";
import { CUSTOMALERT, SAVE } from "../../../services/redux/slices/users";
import moment from "moment";
import PersonalDetails from "./personalDetails";
import AddressDetails from "./address";
import AccoutDetails from "./accoutDetails";
import Steps from "../../../components/steps";

const _address = {
    current: {
      region: "REGION III (CENTRAL LUZON)",
      province: "NUEVA ECIJA",
      city: "CABANATUAN CITY",
      barangay: "",
      zip: "",
      street: "",
      hn: "",
    },
    permanent: {
      region: "REGION III (CENTRAL LUZON)",
      province: "NUEVA ECIJA",
      city: "CABANATUAN CITY",
      barangay: "",
      zip: "",
      street: "",
      hn: "",
    },
  },
  initialData = {
    fullName: {
      fname: "",
      mname: "",
      lname: "",
      suffix: "",
    },
    address: _address,
    dob: new Date(),
    birthplace: "",
    mobile: "",
    isMale: false,
    email: "",
    password: "",
  };

const allSteps = [
  { icon: "user", step: 1, done: "" },
  { icon: "home", step: 2, done: "" },
  { icon: "user", step: 3, done: "" },
];

export default function Register({ show, toggleregister }) {
  const { message, isLoading, isSuccess } = useSelector(({ users }) => users),
    dispatch = useDispatch(),
    { addToast } = useToasts(),
    [formData, setFormData] = useState(initialData),
    [currentStep, setCurrentStep] = useState(1),
    [errorMsg, SetErrorMessage] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    const { password, confirmPassword } = e.target;

    if (currentStep === 3) {
      if (password.value === confirmPassword.value) {
        const dob = moment(new Date(formData.dob)).format("MMMM DD, YYYY");
        dispatch(SAVE({ ...formData, dob, role: "65c51a3ead9f1bca084c3be4" }));
        toggleregister();
        setFormData(initialData);
      } else {
        dispatch(CUSTOMALERT("Passwords does not match."));
      }
    }
  };

  const handleChange = (key, value) => {
      if (key === "isMale") {
        return setFormData({ ...formData, [key]: !formData.isMale });
      }
      setFormData({ ...formData, [key]: value });
    },
    handleObjChange = (obj, key, value) => {
      const _obj = { ...formData[obj] };
      _obj[key] = value;
      setFormData({ ...formData, [obj]: _obj });
    };

  const isStepValid = (step) => {
    let isValid = false;
    switch (step) {
      case 1:
        isValid = ["fname", "lname"].every((field) =>
          formData.fullName[field].trim()
        );
        break;
      case 2:
        const current = ["region", "province", "city", "barangay"].every(
            (field) => formData.address.current[field].trim()
          ),
          permanent = ["region", "province", "city", "barangay"].every(
            (field) => formData.address.permanent[field].trim()
          );
        isValid = current && permanent;
        break;
      case 4:
        isValid = ["mobile", "email", "password"].every((field) =>
          formData[field].trim()
        );
        break;
      default:
        isValid = false;
        break;
    }

    if (!isValid) {
      SetErrorMessage(
        "Oops! It seems you missed some required fields. Please fill them in."
      );
      setTimeout(() => {
        SetErrorMessage("");
      }, 2000);
      return isValid;
    }
    return isValid;
  };

  const handleNext = () => {
    if (currentStep > 3) {
      return setCurrentStep((prev) => prev - 1);
    }

    if (isStepValid(currentStep)) {
      allSteps[currentStep - 1].done = true;
      setCurrentStep((prev) => prev + 1);
    } else {
      allSteps[currentStep - 1].done = false;
      SetErrorMessage(
        "Oops! It seems you missed some required fields. Please fill them in."
      );
      setTimeout(() => {
        SetErrorMessage("");
      }, 2000);
    }
  };

  const handlePrev = () => {
    if (currentStep > 1) {
      setCurrentStep((prev) => prev - 1);
    }
  };

  useEffect(() => {
    if (isSuccess) {
      if (show) {
        setCurrentStep(1);
        addToast(message, {
          appearance: "success",
        });
      }
    } else if (!isSuccess) {
      if (message) {
        SetErrorMessage(message);
        setTimeout(() => SetErrorMessage(""), 2000);
      }
    }
  }, [isSuccess, message, addToast, toggleregister, show]);

  const renderPage = () => {
    switch (currentStep) {
      case 1:
        return (
          <PersonalDetails
            userDetails={formData}
            setDate={setFormData}
            handleObjChange={handleObjChange}
            handleChange={handleChange}
          />
        );
      case 2:
        return (
          <AddressDetails
            addressInfo={formData}
            handleObjChange={handleObjChange}
          />
        );
      case 3:
        return <AccoutDetails handleChange={handleChange} />;
      default:
        return;
    }
  };

  const handleNavigation = (step) => {
    if (isStepValid(Math.max(step - 1, 1))) {
      allSteps[Math.max(step - 2, 0)].done = true;
      setCurrentStep(step);
    } else {
      allSteps[currentStep - 1].done = false;
      SetErrorMessage(
        "Oops! It seems you missed some required fields. Please fill them in."
      );
      setTimeout(() => {
        SetErrorMessage("");
      }, 2000);
    }
  };

  return (
    <MDBModal
      size={"lg"}
      isOpen={show}
      backdrop={true}
      toggle={toggleregister}
      cascading
    >
      <MDBModalHeader
        toggle={() => toggleregister()}
        className={"light-blue darken-3 white-text mb-0"}
      >
        <div className="text-center">
          <h3 className="white-text">
            <MDBIcon icon="user" className="white-text" /> Register
          </h3>
        </div>
      </MDBModalHeader>
      <MDBModalBody className="m-0 pl-0 pr-0 pt-0">
        <MDBRow className="flex-center mt-3 pl-0 pr-0 mr-0 ml-0">
          <MDBCol>
            <MDBContainer className="classic-tabs mb-0 pl-0 pr-0 mr-0 ml-0">
              <Steps
                activeStep={currentStep}
                _steps={allSteps}
                _onClick={(step) => handleNavigation(step)}
              />

              <form
                onSubmit={handleSubmit}
                id="registration-form"
                autoComplete="off"
                className="bg-white pr-3"
              >
                {renderPage()}

                {errorMsg && (
                  <div className={`alert alert-${"warning"} text-center`}>
                    {errorMsg}
                  </div>
                )}
                <MDBRow>
                  <MDBCol>
                    {currentStep > 1 && (
                      <MDBBtn color="primary" rounded onClick={handlePrev}>
                        Previous
                      </MDBBtn>
                    )}
                  </MDBCol>
                  <MDBCol className="d-flex justify-content-end">
                    {currentStep === 3 && (
                      <MDBBtn
                        disabled={isLoading}
                        type="submit"
                        color="primary"
                        rounded
                      >
                        Sign up
                      </MDBBtn>
                    )}

                    {currentStep !== 3 && (
                      <MDBBtn
                        disabled={isLoading}
                        color="primary"
                        rounded
                        onClick={handleNext}
                      >
                        Next
                      </MDBBtn>
                    )}
                  </MDBCol>
                </MDBRow>
              </form>
            </MDBContainer>
          </MDBCol>
        </MDBRow>
      </MDBModalBody>
    </MDBModal>
  );
}
