import React, { useState } from "react";
import {
  MDBContainer,
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarToggler,
  MDBCollapse,
  MDBNavbarNav,
  MDBNavItem,
  MDBNavLink,
} from "mdbreact";
import Login from "./login";
import { School } from "../../services/fakeDb";
import Banner from "./banner";
import Features from "./features";
import "./index.css";
// import Register from "../sessions/registration";
import Mission from "./mission";
import NewLogin from "./newLogin";
import Register from "./register";

export default function Home() {
  const [collapseID, setCollapseID] = useState(false),
    [login, setLogin] = useState(false),
    [show, setShow] = useState(false);

  const handleLink = () => {
    console.log("test");
  };

  const toggleLogin = () => setLogin(!login);
  const toggleregister = () => setShow(!show);

  return (
    <>
      <Login show={login} toggleLogin={toggleLogin} />
      <Register show={show} toggleregister={toggleregister} />

      <div id="landing">
        <MDBNavbar
          dark
          expand="md"
          fixed="top"
          scrolling
          transparent
          style={{ minWidth: "768px" }}
        >
          <MDBContainer>
            <MDBNavbarBrand href="#" onClick={() => handleLink("landing")}>
              <strong className="white-text">{School.name} </strong>
            </MDBNavbarBrand>
            <MDBNavbarToggler onClick={() => setCollapseID(!collapseID)} />
            <MDBCollapse id="navbarCollapse" isOpen={collapseID} navbar>
              <MDBNavbarNav right>
                <MDBNavItem>
                  <MDBNavLink onClick={toggleregister} to="#">
                    <h5> Register</h5>
                  </MDBNavLink>
                </MDBNavItem>
                <MDBNavItem>
                  <MDBNavLink onClick={toggleLogin} to="#">
                    <h5> Login</h5>
                  </MDBNavLink>
                </MDBNavItem>
              </MDBNavbarNav>
            </MDBCollapse>
          </MDBContainer>
        </MDBNavbar>

        <Banner show={show} />
        <Features />
        <NewLogin />
        <Mission />
      </div>
    </>
  );
}
