import React, { useEffect, useState } from "react";
import { MDBCard, MDBCardBody, MDBView } from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import {
  BROWSE,
  DESTROY,
} from "../../../../services/redux/slices/admission/employees";
import { useToasts } from "react-toast-notifications";
import Search from "../../../../widgets/search";
import Modal from "./modal";
import Table from "./table";
import MultipleDestroy from "../../../../widgets/multipleDestroy";
import TableLoading from "../../../../widgets/tableLoading";

export default function Empployees() {
  const { token } = useSelector(({ auth }) => auth),
    { collections, message, isSuccess, isLoading } = useSelector(
      ({ employees }) => employees
    ),
    [employees, setEmployees] = useState([]),
    [container, setContainer] = useState([]),
    [show, setShow] = useState(false),
    [orderIndex, setOrderIndex] = useState(0),
    [willCreate, setWillCreate] = useState(true),
    [selected, setSelected] = useState({}),
    [didSearch, setDidSearch] = useState(false),
    dispatch = useDispatch(),
    { addToast } = useToasts();

  useEffect(() => {
    if (message) {
      addToast(message, {
        appearance: isSuccess ? "success" : "error",
      });
    }
  }, [isSuccess, message, addToast]);

  useEffect(() => {
    if (token) {
      dispatch(BROWSE({ token }));
    }
  }, [dispatch, token]);
  useEffect(() => {
    setEmployees(collections);
  }, [collections]);
  useEffect(() => {
    if (!show) {
      setContainer([]);
    }
  }, [show]);

  const handleUpdate = () => {
    if (container.length === 1) {
      setSelected(container[0]);
      setWillCreate(false);
      setShow(true);
    } else {
      addToast("Please Select 1 Item", {
        appearance: "warning",
      });
    }
  };

  const handleSearch = (e) => {
    e.preventDefault();

    const key = e.target.searchKey.value.toUpperCase();

    setEmployees(
      collections.filter(({ name }) => name.toUpperCase().includes(key))
    );

    setDidSearch(true);
  };

  const handleDelete = () => {
    MultipleDestroy(
      dispatch,
      DESTROY,
      token,
      container,
      addToast,
      setContainer
    );
  };

  return (
    <>
      <MDBCard narrow className="pb-3">
        <MDBView
          cascade
          className="gradient-card-header blue-gradient py-2 mx-4 d-flex justify-content-between align-items-center"
        >
          <span className="ml-3 ">Employee List</span>

          <Search
            didSearch={didSearch}
            handleSearch={handleSearch}
            setShow={setShow}
            array={collections}
            setArray={setEmployees}
            setDidSearch={setDidSearch}
            handleUpdate={handleUpdate}
            handleDelete={handleDelete}
          />
        </MDBView>
        <MDBCardBody>
          {isLoading ? (
            <TableLoading />
          ) : (
            <Table
              employees={employees}
              setSelected={setSelected}
              setOrderIndex={setOrderIndex}
              orderIndex={orderIndex}
              setContainer={setContainer}
            />
          )}
        </MDBCardBody>
      </MDBCard>
      <Modal
        show={show}
        toggle={() => {
          if (!willCreate) {
            setWillCreate(true);
            setSelected({});
          }
          setShow(false);
        }}
        selected={selected}
        willCreate={willCreate}
      />
    </>
  );
}
