import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { axioKit, bulkPayload } from "../../../utilities";
import { DECRYPT } from "../../../utilities/encryption/securedData";
const name = "registrar/Schedulers";

const initialState = {
  collections: [],
  sectionsAndsubjects: [],
  instructors: [],
  progress: 0,
  isSuccess: false,
  isLoading: false,
  message: "",
};

export const BROWSE = createAsyncThunk(
  `${name}/browse`,
  ({ token, data }, thunkAPI) => {
    try {
      return axioKit.universal(`${name}/browse`, token, data);
    } catch (error) {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();

      return thunkAPI.rejectWithValue(message);
    }
  }
);

