export const reduxSlice = createSlice({
  name,
  initialState,
  reducers: {
    RESET: (state, data) => {
      state.sections = [];
      state.isSuccess = false;
      state.message = "";
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(BROWSE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(BROWSE.fulfilled, (state, action) => {
        const { payload } = action.payload;
        state.collections = DECRYPT(payload);
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(BROWSE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(INSTRUCTORS.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(INSTRUCTORS.fulfilled, (state, action) => {
        const { payload } = action.payload;
        state.instructors = DECRYPT(payload);
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(INSTRUCTORS.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(SECTIONSANDSUBJECTS.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(SECTIONSANDSUBJECTS.fulfilled, (state, action) => {
        const { payload } = action.payload;
        state.sectionsAndsubjects = DECRYPT(payload);
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(SECTIONSANDSUBJECTS.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(SAVE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(SAVE.fulfilled, (state, action) => {
        const { success, payload } = action.payload;

        const collections = [...state.collections];
        const container = DECRYPT(payload);

        for (let index = 0; index < container.length; index++) {
          const element = container[index];

          const existingIndex = collections.findIndex(
            ({ instructor, section, subject }) =>
              instructor._id === element.instructor?._id &&
              section._id === element?.section?._id &&
              element.subject?._id === subject._id
          );

          if (existingIndex > -1) {
            collections[existingIndex] = element;
          } else {
            collections.push(element);
          }
        }

        state.collections = collections;
        state.message = success;
        state.isSuccess = true;
        state.isLoading = false;
      })
      .addCase(SAVE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(UPDATE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(UPDATE.fulfilled, (state, action) => {
        const { success, payload } = action.payload;
        const { updatedSchedules = [], deletedSchedules = [] } =
          DECRYPT(payload);
        const _collections = [...state.collections];

        if (updatedSchedules.length > 0) {
          for (const indexUpdated in updatedSchedules) {
            const element = updatedSchedules[indexUpdated];
            const index = _collections.findIndex(
              ({ _id }) => _id === element._id
            );
            console.log(index);
            _collections[index] = element;
          }
        }

        if (deletedSchedules.length > 0) {
          for (const indexDeleted in deletedSchedules) {
            const element = deletedSchedules[indexDeleted];
            const index = _collections.findIndex(
              ({ _id }) => _id === element._id
            );
            _collections.splice(index, 1);
          }
        }

        state.collections = _collections;
        state.message = success;
        state.isSuccess = true;
        state.isLoading = false;
      })
      .addCase(UPDATE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(DESTROY.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(DESTROY.fulfilled, (state, action) => {
        const { success, payload } = action.payload;
        const rooms = DECRYPT(payload);
        bulkPayload(state, rooms, false);
        state.showModal = false;
        state.message = success;
        state.isSuccess = true;
        state.isLoading = false;
      })
      .addCase(DESTROY.rejected, (state, action) => {
        const { error } = action;
        state.showModal = false;
        state.message = error.message;
        state.isLoading = false;
      });
  },
});

export const { RESET } = reduxSlice.actions;

export default reduxSlice.reducer;
