import React, { useEffect, useState } from "react";
import { MDBCard, MDBCardBody, MDBView } from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import {
  BROWSE,
  DESTROY,
} from "../../../../services/redux/slices/pricipal/sections";
import { useToasts } from "react-toast-notifications";
import Table from "./table";
import Modal from "./modal";
import Search from "../../../../widgets/search";
import MultipleDestroy from "../../../../widgets/multipleDestroy";
import TableLoading from "../../../../widgets/tableLoading";
import handleForm from "../../../../widgets/form";
import Level from "../../../../widgets/form/level";
const { StoreDefualtValueForForm } = handleForm;
const _form = {
  levels: [],
  level: 0,
  specializations: [],
  specialization: "",
  strands: [],
  strand: "",
  semesters: [],
  semester: "",
};
export default function Sections() {
  const { token } = useSelector(({ auth }) => auth),
    { collections, message, isSuccess } = useSelector(
      ({ sections }) => sections
    ),
    [sections, setSections] = useState([]),
    [container, setContainer] = useState([]),
    [form, setForm] = useState(_form),
    [isSelectAll, setIsSelectAll] = useState(false),
    [show, setShow] = useState(false),
    [willCreate, setWillCreate] = useState(true),
    [selected, setSelected] = useState({}),
    [didSearch, setDidSearch] = useState(false),
    [isShowAll, setIsShowAll] = useState(false),
    dispatch = useDispatch(),
    { addToast } = useToasts();

  useEffect(() => {
    if (message) {
      addToast(message, {
        appearance: isSuccess ? "success" : "error",
      });
    }
  }, [isSuccess, message, addToast]);

  useEffect(() => {
    if (token) {
      dispatch(BROWSE({ token }));
    }
  }, [dispatch, token]);

  useEffect(() => {
    StoreDefualtValueForForm({
      form: {
        level: form.level,
        strand: form.strand,
        semester: form.semester,
        specialization: form.specialization,
      },
      setForm,
      collections,
      setArray: setSections,
      isShowAll,
    });
  }, [
    collections,
    form.level,
    form.strand,
    form.specialization,
    form.semester,
    isShowAll,
  ]);

  const handleDelete = () => {
    MultipleDestroy(
      dispatch,
      DESTROY,
      token,
      container,
      addToast,
      setContainer
    );
  };

  const handleUpdate = () => {
    if (container.length === 1) {
      setSelected(container[0]);
      setWillCreate(false);
      setShow(true);
    } else {
      addToast("Please Select 1 Item", {
        appearance: "warning",
      });
    }
  };

  const handleSearch = (e) => {
    e.preventDefault();

    const key = e.target.searchKey.value.toUpperCase();

    setSections(
      collections.filter(({ name }) => name.toUpperCase().includes(key))
    );

    setDidSearch(true);
  };

  return (
    <>
      <MDBCard narrow className="pb-3">
        <MDBView
          cascade
          className="gradient-card-header blue-gradient py-2 mx-4 d-flex justify-content-between align-items-center"
        >
          <span className="ml-3 ">Section List</span>
          <Search
            handleSearch={handleSearch}
            didSearch={didSearch}
            setDidSearch={setDidSearch}
            array={sections}
            setArray={setSections}
            setShow={setShow}
            handleDelete={handleDelete}
            handleUpdate={handleUpdate}
          />
        </MDBView>
        <MDBCardBody>
          {collections.length === 0 ? (
            <TableLoading />
          ) : (
            <>
              {!didSearch ? (
                <>
                  <Level
                    form={form}
                    setForm={setForm}
                    disableSemester={true}
                    isShowAll={isShowAll}
                    setIsShowAll={setIsShowAll}
                  />
                  <Table
                    collections={sections}
                    isSelectAll={isSelectAll}
                    setIsSelectAll={setIsSelectAll}
                    lvlKey={form.level}
                    sections={sections}
                    setWillCreate={setWillCreate}
                    container={container}
                    setContainer={setContainer}
                  />
                </>
              ) : (
                <Table
                  didSearch={didSearch}
                  sections={sections}
                  container={container}
                  setContainer={setContainer}
                  isSelectAll={isSelectAll}
                  setIsSelectAll={setIsSelectAll}
                />
              )}
            </>
          )}
        </MDBCardBody>
      </MDBCard>

      <Modal
        collections={collections}
        show={show}
        previewForm={{
          lvl: form.level,
          strand: form.strand,
          specialization: form.specialization,
        }}
        toggle={() => {
          if (!willCreate) {
            setWillCreate(true);
            setSelected({});
          }
          setShow(false);
        }}
        selected={selected}
        willCreate={willCreate}
        setPreviewForm={setForm}
      />
    </>
  );
}
