import Applicants from "./applicants";
import Dashboard from "./dashboard";
import Enrollees from "./enrollees";
import Schedules from "./schedule";
import Students from "./students";
import StudentDrops from "./studentsDrop";
const access = [
  {
    name: "Dashboard",
    path: "/dashboard",
    icon: "tachometer-alt",
    component: Dashboard,
  },
  {
    name: "Enrollees",
    path: "/enrollees",
    icon: "users",
    children: [
      {
        name: "Pending",
        path: "/pending",
        component: Enrollees,
        props: "PENDING",
      },
      {
        name: "Approved",
        path: "/approved",
        props: "APPROVED",
        component: Enrollees,
      },

      {
        name: "Rejected",
        path: "/rejected",
        props: "REJECT",
        component: Enrollees,
      },
    ],
  },

  {
    name: "Students",
    path: "/students",
    icon: "user-graduate",
    component: Students,
  },

  {
    name: "Students Drop",
    path: "/drop",
    icon: "user-times",
    component: StudentDrops,
  },

  {
    name: "Schedules",
    path: "/schedules",
    icon: "chalkboard-teacher",
    component: Schedules,
  },

  {
    name: "Activation Accounts",
    path: "/applicants",
    icon: "user-nurse",
    component: Applicants,
  },
];

export default access;
