import React, { useState } from "react";
import { MDBTable } from "mdbreact";
import Grades from "./grades";
import { fullName } from "../../../../services/utilities";
import Swal from "sweetalert2";
import handlePagination from "../../../../widgets/pagination";
import Rows from "../../../../widgets/pagination/rows";
import PaginationButtons from "../../../../widgets/pagination/buttons";
import { UPDATE } from "../../../../services/redux/slices/teacher/classes";
import { useDispatch, useSelector } from "react-redux";

function Table({
  students = [],
  subject = { _id: "" },
  classID,
  semester = "",
  level,
}) {
  const { token } = useSelector(({ auth }) => auth);
  const [activeId, setActiveId] = useState("");
  const [page, setPage] = useState(1);
  const [max, setMax] = useState(5);
  const dispatch = useDispatch();

  const handleSubmit = (
    newGrade,
    setGrade,
    title,
    gradeId,
    grades,
    transcriptID
  ) => {
    Swal.fire({
      title: "Are you sure?",
      text: "You want to submit the grade for this student!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Submit it!",
    }).then((result) => {
      if (result.isConfirmed) {
        const _grades = [...grades];
        const index = _grades.findIndex(({ _id }) => _id === gradeId);
        _grades[index] = {
          ..._grades[index],
          [title]: newGrade,
        };
        setGrade(newGrade);
        dispatch(
          UPDATE({
            token,
            data: {
              transcriptID,
              grades: {
                ..._grades[index],
                [title]: newGrade,
              },
              classID,
            },
          })
        );
      }
    });
  };

  return (
    <>
      <MDBTable>
        <thead>
          <tr>
            <th>#</th>
            <th>LRN</th>
            <th>Name</th>
            {level > 10 ? (
              <>
                {semester === "1st" && (
                  <>
                    <th className="th-sm">1ST </th>
                    <th className="th-sm ">2ND </th>
                  </>
                )}
                {semester === "2nd" && (
                  <>
                    <th className="th-sm"> 3RD </th>
                    <th className="th-sm">4TH </th>
                  </>
                )}
              </>
            ) : (
              <>
                <th className="th-sm">1ST </th>
                <th className="th-sm ">2ND </th>
                <th className="th-sm"> 3RD </th>
                <th className="th-sm">4TH </th>
              </>
            )}
            <th className="th-sm">Finals</th>
            <th className="th-sm">Remarks</th>
          </tr>
        </thead>
        <tbody>
          {students?.length > 0 ? (
            handlePagination(students, page, max)?.map((transcript, index) => {
              const { student, grades: gs, _id } = transcript;
              const grades = gs?.find(
                ({ subject: sub }) => sub === subject._id
              );
              const done1stQuarter = grades.firstQuarter === 0 ? false : true;
              const done2ndQuarter = grades.secondQuarter === 0 ? false : true;
              const done3rdQuarter = grades.thirdQuarter === 0 ? false : true;
              const done4thQuarter = grades.fourthQuarter === 0 ? false : true;
              const firstQuarter = grades.firstQuarter;
              const secondQuarter = grades.secondQuarter;
              const thirdQuarter = grades.thirdQuarter;
              const fourthQuarter = grades.fourthQuarter;
              var remarks = "--";
              var finals = 0;
              if (level <= 10) {
                // if (
                //   done1stQuarter &&
                //   !done2ndQuarter &&
                //   !done3rdQuarter &&
                //   !done4thQuarter &&
                //   firstQuarter >= 75
                // ) {
                //   remarks = "--";
                // } else if (
                //   done1stQuarter &&
                //   done2ndQuarter &&
                //   !done3rdQuarter &&
                //   !done4thQuarter &&
                //   secondQuarter >= 75
                // ) {
                //   remarks = "PASSED";
                // } else if (
                //   done1stQuarter &&
                //   done2ndQuarter &&
                //   done3rdQuarter &&
                //   !done4thQuarter &&
                //   thirdQuarter >= 75
                // ) {
                //   remarks = "PASSED";
                // } else {
                //   remarks = "FAILED";
                // }

                if (
                  done1stQuarter &&
                  done2ndQuarter &&
                  done3rdQuarter &&
                  done4thQuarter
                ) {
                  const _finals =
                    (firstQuarter +
                      secondQuarter +
                      thirdQuarter +
                      fourthQuarter) /
                    4;
                  finals = _finals;
                  remarks = _finals >= 75 ? "PASSED" : "FAILED";
                }
              } else if (level > 10) {
                if (semester === "1st") {
                  // if (
                  //   done1stQuarter &&
                  //   !done2ndQuarter &&
                  //   !done3rdQuarter &&
                  //   !done4thQuarter
                  // ) {
                  //   remarks = firstQuarter >= 75 ? "PASSED" : "FAILED";
                  // } else

                  if (
                    done1stQuarter &&
                    done2ndQuarter &&
                    !done3rdQuarter &&
                    !done4thQuarter
                  ) {
                    const _finals = (firstQuarter + secondQuarter) / 2;

                    finals = _finals;
                    remarks = _finals >= 75 ? "PASSED" : "FAILED";
                  }
                } else {
                  // if (
                  //   !done1stQuarter &&
                  //   !done2ndQuarter &&
                  //   done3rdQuarter &&
                  //   !done4thQuarter
                  // ) {
                  //   remarks = thirdQuarter >= 75 ? "PASSED" : "FAILED";
                  // } else

                  if (
                    !done1stQuarter &&
                    !done2ndQuarter &&
                    done3rdQuarter &&
                    done4thQuarter
                  ) {
                    const _finals = (thirdQuarter + fourthQuarter) / 2;
                    finals = _finals;
                    remarks = _finals >= 75 ? "PASSED" : "FAILED";
                  }
                }
              }

              return (
                <tr key={index}>
                  <td>{index + 1}</td>
                  <td className="font-weight-bold">
                    {student.lrn || "107089070021"}
                  </td>
                  <td className="text-nowrap font-weight-bold">
                    {fullName(student.fullName)}
                  </td>
                  {level > 10 ? (
                    <>
                      {semester === "1st" ? (
                        <>
                          <td>
                            <Grades
                              id={student._id}
                              activeId={activeId}
                              transcriptID={_id}
                              setActiveId={setActiveId}
                              handleSubmit={handleSubmit}
                              grades={gs}
                              obj={{
                                grade: grades.firstQuarter,
                                title: "firstQuarter",
                                id: grades._id,
                                remarks,
                                finals,
                              }}
                            />
                          </td>
                          <td>
                            <Grades
                              id={student._id}
                              transcriptID={_id}
                              grades={gs}
                              activeId={activeId}
                              setActiveId={setActiveId}
                              handleSubmit={handleSubmit}
                              obj={{
                                grade: grades.secondQuarter,
                                title: "secondQuarter",
                                id: grades._id,
                                remarks,
                                finals,
                              }}
                            />
                          </td>
                        </>
                      ) : (
                        <>
                          <td>
                            <Grades
                              id={student._id}
                              transcriptID={_id}
                              activeId={activeId}
                              grades={gs}
                              handleSubmit={handleSubmit}
                              setActiveId={setActiveId}
                              obj={{
                                grade: grades.thirdQuarter,
                                title: "thirdQuarter",
                                id: grades._id,
                                remarks,
                                finals,
                              }}
                            />
                          </td>
                          <td>
                            <Grades
                              id={student._id}
                              grades={gs}
                              transcriptID={_id}
                              activeId={activeId}
                              handleSubmit={handleSubmit}
                              setActiveId={setActiveId}
                              obj={{
                                grade: grades.fourthQuarter,
                                title: "fourthQuarter",
                                id: grades._id,
                                remarks,
                                finals,
                              }}
                            />
                          </td>
                        </>
                      )}
                    </>
                  ) : (
                    <>
                      <td>
                        <Grades
                          id={student._id}
                          activeId={activeId}
                          transcriptID={_id}
                          setActiveId={setActiveId}
                          handleSubmit={handleSubmit}
                          grades={gs}
                          obj={{
                            grade: grades.firstQuarter,
                            remarks,
                            finals,
                            title: "firstQuarter",
                            id: grades._id,
                          }}
                        />
                      </td>
                      <td>
                        <Grades
                          id={student._id}
                          transcriptID={_id}
                          grades={gs}
                          activeId={activeId}
                          setActiveId={setActiveId}
                          handleSubmit={handleSubmit}
                          obj={{
                            grade: grades.secondQuarter,
                            remarks,
                            finals,
                            title: "secondQuarter",
                            id: grades._id,
                          }}
                        />
                      </td>
                      <td>
                        <Grades
                          id={student._id}
                          transcriptID={_id}
                          activeId={activeId}
                          grades={gs}
                          handleSubmit={handleSubmit}
                          setActiveId={setActiveId}
                          obj={{
                            grade: grades.thirdQuarter,
                            title: "thirdQuarter",
                            id: grades._id,
                            remarks,
                            finals,
                          }}
                        />
                      </td>
                      <td>
                        <Grades
                          id={student._id}
                          grades={gs}
                          transcriptID={_id}
                          activeId={activeId}
                          handleSubmit={handleSubmit}
                          setActiveId={setActiveId}
                          obj={{
                            grade: grades.fourthQuarter,
                            title: "fourthQuarter",
                            id: grades._id,
                            remarks,
                            finals,
                          }}
                        />
                      </td>
                    </>
                  )}
                  <td>
                    <h6 className="font-weight-bold">
                      {finals === 0 ? "--" : Math.ceil(finals)}
                    </h6>
                  </td>
                  <td>
                    <h6
                      className={`font-weight-bold ${
                        remarks === "PASSED" ? "text-success" : "text-danger"
                      }`}
                    >
                      {remarks}
                    </h6>
                  </td>
                </tr>
              );
            })
          ) : (
            <tr>
              <td>Wait for the enrollees </td>
            </tr>
          )}
        </tbody>
      </MDBTable>
      <Rows max={max} setMax={setMax} />
      <PaginationButtons
        page={page}
        setPage={setPage}
        max={max}
        array={students}
      />
    </>
  );
}

export default Table;
