import { jsPDF } from "jspdf";
import logo from "../../../../assets/logo/logo.png";
import deped from "../../../../assets/logo/deped.png";
import "jspdf-autotable";
import { fullName } from "../../../../services/utilities";

const headers = [
  {
    text: "Repulic of the phiippines",
    font: {
      style: "Times",
      fontWeight: "normal",
      size: 10,
      color: "#333333",
    },
    height: 15,
  },
  {
    text: "Department of Education",
    font: {
      style: "Times",
      fontWeight: "bold",
      size: 12,
      color: "#333333",
    },
    height: 19,
  },
  {
    text: "Region III",
    font: {
      style: "Times",
      fontWeight: "normal",
      size: 11,
      color: "#333333",
    },
    height: 23,
  },
  {
    text: "Division of Nueva Ecija",
    font: {
      style: "Times",
      fontWeight: "normal",
      size: 11,
      color: "#333333",
    },
    height: 28,
  },
  {
    text: "Leonor M. Bautista National High School",
    font: {
      style: "Times",
      fontWeight: "bold",
      size: 12,
      color: "#333333",
    },
    height: 33,
  },

  {
    text: "General Tinio Nueva Ecija",
    font: {
      style: "Times",
      fontWeight: "normal",
      size: 12,
      color: "#333333",
    },
    height: 38,
  },

  {
    text: "CERTIFICATE OF ENROLLMENT",
    font: {
      style: "Times",
      fontWeight: "bold",
      size: 15,
      color: "#333333",
    },
    height: 55,
  },
];

const currentDate = new Date();

const day = currentDate.getDate();
const monthIndex = currentDate.getMonth();
const year = currentDate.getFullYear();

const months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

const getDaySuffix = (day) => {
  if (day >= 11 && day <= 13) {
    return `${day}th`;
  }
  switch (day % 10) {
    case 1:
      return `${day}st`;
    case 2:
      return `${day}nd`;
    case 3:
      return `${day}rd`;
    default:
      return `${day}th`;
  }
};

// Construct the date string
const date = `${getDaySuffix(day)} day of ${months[monthIndex]} ${year}`;

const CEO = (student, section, batch) => {
  const doc = new jsPDF("p", "mm", "a4");
  doc.setProperties({
    title: "Certificate of Enrollment",
    author: "Leonor M. Bautista National High School",
    subject: "Certificate",
    keywords: "enrollment, certificate",
  });

  const pageWidth = doc.internal.pageSize.width;

  //img,type,x,y,width,height
  doc.addImage(logo, "JPEG", 35, 15, 17, 15);
  doc.addImage(deped, "JPEG", 155, 15, 17, 15);
  for (let index = 0; index < headers.length; index++) {
    const header = headers[index];
    const { font, height, text } = header;
    const { style, fontWeight, size, color } = font;
    doc.setFont(style, fontWeight);
    doc.setFontSize(size);
    doc.setTextColor(color);
    doc.text(text, pageWidth / 2, height, {
      align: "center",
    });
  }

  doc.setFont("Times", "normal");
  doc.setFontSize(12);
  doc.setTextColor("#000000");

  doc.text(
    "This is to certify that according to the records of this office",
    43,
    70
  );
  doc.setFont("Times", "bold");
  doc.text(student, 145, 70);

  doc.setFont("Times", "normal");
  const { lvl, strand, adviser } = section;
  doc.text(
    `of Grade-${lvl} ${strand ? strand : ""}, ${
      section?.name
    } is enrolled for School Year ${batch}`,
    30,
    77
  );

  doc.setFont("Times", "normal");
  doc.text(
    "This certification is issued upon the request of the berien interested party for whatever",
    43,
    90
  );

  doc.setFont("Times", "normal");
  doc.text("legal purpose it may serve.", 30, 97);

  doc.setFont("Times", "normal");
  doc.text(`Issued this ${date} at Leonor M. Bautista National.`, 43, 110);

  doc.setFont("Times", "bold");
  doc.text(fullName(adviser?.fullName), 190, 140, { align: "right" });

  doc.setFont("times", "normal");
  doc.setFontSize(11);
  doc.text(`Adviser`, 175, 144, {
    align: "right",
  });

  // Principal's Name
  doc.setFont("Times", "normal");
  doc.text("Noted by:", 20, 153, {
    align: "left",
  });

  const principalName = "ABELLO JULIA A.";
  doc.setFont("Times", "bold");
  doc.text(principalName, 20, 165, {
    align: "left",
  });

  doc.setFont("times", "normal");
  doc.setFontSize(11);
  doc.text("Principal", 27, 169, {
    align: "left",
  });

  doc.save(`certificate.pdf`);
};

export default CEO;
