import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { axioKit, bulkPayload } from "../../../utilities";
import { DECRYPT } from "../../../utilities/encryption/securedData";
const name = "registrar/Students";

const initialState = {
  collections: [],
  sections: [],
  progress: 0,
  isSuccess: false,
  isLoading: false,
  message: "",
  sectionsWithStudents: [],
};

export const BROWSE = createAsyncThunk(
  `${name}/browse`,
  ({ token, data }, thunkAPI) => {
    try {
      return axioKit.universal(`${name}/browse`, token, data);
    } catch (error) {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();

      return thunkAPI.rejectWithValue(message);
    }
  }
);

