import React from "react";
import { MDBCol, MDBRow } from "mdbreact";
import { fullName as converter } from "../../services/utilities";
import "./learner.css";

const Identify = ({ selected, props = "" }) => {
  const { registrar } = selected;
  const { birthplace, fullName, dob, isMale, mobile } = selected?.student;
  const { fname, mname, lname, suffix = "" } = fullName;

  return (
    <>
      {props === "REJECT" && (
        <div className="mt-5">
          <MDBRow>
            <MDBCol md="12" className="d-flex align-items-center">
              <h6>Asssist By:</h6>
              <h6 className="font-weight-bold ml-2">
                {converter(registrar?.approvedBy?.fullName).toUpperCase()}
              </h6>
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol className="d-flex align-items-center">
              <h6>Issue:</h6>
              <h6 className="font-weight-bold ml-2">{registrar?.message}</h6>
            </MDBCol>
          </MDBRow>
        </div>
      )}
      <MDBRow
        className={props === "REJECT" ? "mt-3" : "mt-5"}
        style={{ backgroundColor: "#0000FF" }}
      >
        <MDBCol>
          <h5 className="font-weight-bold text-white label mt-2 mb-2 ml-5">
            I. LEARNER INFORMATION
          </h5>
        </MDBCol>
      </MDBRow>

      <MDBRow className="ml-3 fullName">
        <MDBCol sm="6" lg="3" className="mt-3">
          <h6>First Name</h6>

          <input
            type="text"
            className="form-control"
            placeholder="First Name"
            value={fname.toUpperCase()}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3" className="mt-3">
          <h6>Middle Name</h6>

          <input
            type="text"
            className="form-control"
            placeholder="Middle Name"
            value={mname.toUpperCase()}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3" className="mt-3">
          <h6>Last Name</h6>

          <input
            type="text"
            className="form-control"
            placeholder="Last Name"
            value={lname.toUpperCase()}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3" className="mt-3">
          <h6>Suffix</h6>

          <input
            type="text"
            className="form-control"
            placeholder="Suffix"
            value={suffix || ""}
            readOnly
          />
        </MDBCol>
      </MDBRow>

      <MDBRow className="ml-3 mt-4">
        <MDBCol sm="6" lg="3">
          <h6>Place of birth </h6>

          <input
            placeholder="Place of Birth"
            className="form-control"
            value={birthplace.toUpperCase()}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Date of birth </h6>

          <input
            placeholder="Date of Birth"
            className="form-control"
            type="text"
            value={dob.toUpperCase()}
            readOnly
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Gender</h6>

          <select
            className="form-control"
            value={isMale ? "male" : "female"}
            readOnly
          >
            <option value={"male"}>Male</option>
            <option value={"female"}>Female</option>
          </select>
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Phone No.</h6>

          <input
            placeholder="Phone No."
            className="form-control"
            type="text"
            value={mobile}
            readOnly
          />
        </MDBCol>
      </MDBRow>
    </>
  );
};

export default Identify;
