const sheduleAction = ({
  array = [],
  instructors,
  subjects,
  form = {},
  action = "UPDATE", // REMOVE UPDATE DELETE  --THE REMOVE IS FOR THE REMOVE THE YOU WANT TO UPDATE IN MAIN COLLECTIO
}) => {
  const updatedSchedules = [...array];
  const index = updatedSchedules.findIndex(({ _id }) => _id === form._id);
  const schedules = [...updatedSchedules[index]?.schedules];
  const schedIndex = updatedSchedules[index]?.schedules?.findIndex(
    ({ days }) => days.join(",") === form.oldDays.join(",")
  );

  if (schedIndex <= -1) return false;
  if (action === "REMOVE") {
    schedules.splice(schedIndex, 1);
    const newSchedule = {
      ...updatedSchedules[index],
      schedules,
    };
    updatedSchedules[index] = newSchedule;
    return updatedSchedules;
  }

  const schedule = updatedSchedules[index]?.schedules[schedIndex];
  const { time = {} } = schedule || {};

  const updatedStart = {
    hours: form?.hourStart,
    min: form?.minStart,
    period: form?.periodStart,
  };

  const updatedEnd = {
    hours: form?.hourEnd,
    min: form?.minEnd,
    period: form?.periodEnd,
  };

  const updatedSchedule = {
    ...schedule,
    time: {
      ...time,
      start: updatedStart,
      end: updatedEnd,
    },
    days: form.pickDays,
  };

  if (action === "UPDATE") {
    console.log(schedules);
    schedules[schedIndex] = updatedSchedule;
  } else {
    schedules.splice(schedIndex, 1);
  }

  if (schedules?.length > 0) {
    const newSchedule = {
      ...updatedSchedules[index],
      instructor: instructors.find(({ _id }) => _id === form.instructor),
      subject: subjects.find(({ _id }) => _id === form.subject),
      schedules,
    };

    updatedSchedules[index] = newSchedule;
    return { collections: updatedSchedules, obj: newSchedule };
  } else {
    updatedSchedules.splice(index, 1);
    return {
      collections: updatedSchedules,
      objDeleted: array[index],
    };
  }
};

export default sheduleAction;
