const SORTING = (array = []) => {
  if (array.length === 0) return array;
  const sortingArray = [...array];
  sortingArray.sort((a, b) => {
    if (a.lvl && b.lvl) {
      if (a.lvl < b.lvl) return -1;
      if (a.lvl > b.lvl) return 1;
      return 0;
    } else {
      return 0;
    }
  });

  return sortingArray;
};

export default SORTING;
