import React, { useEffect, useState } from "react";
import { GoogleLogin } from "react-google-login";
import { gapi } from "gapi-script";
import "./login.css";
import {
  MDBBtn,
  MDBCol,
  MDBIcon,
  MDBInput,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBRow,
} from "mdbreact";

import { useHistory, useLocation } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import {
  LOGIN,
  RESET,
  SETROUTE,
  SIGNIN_WITH_GOOGLE,
} from "../../../services/redux/slices/auth";
import { PresetImage } from "../../../services/utilities";
import ForgotPasswordV2 from "../forgot v2";

//INITIAL STATE
const clientID =
  "844598184851-gtolc10ql8kgjeftm9uu1o5s794o9hal.apps.googleusercontent.com";

export default function Login({ show, toggleLogin }) {
  const { auth, email, isLoading, isSuccess, message, image } = useSelector(
      ({ auth }) => auth
    ),
    [isLocked, setIsLocked] = useState(true),
    [headerTitle, setHeaderTitle] = useState("Login"),
    [isForgot, setIsForgot] = useState(false),
    [baseEmail, setBaseEmail] = useState(""),
    history = useHistory(),
    location = useLocation(),
    dispatch = useDispatch();

  const handleSubmit = (e) => {
    e.preventDefault();

    const { password } = e.target;

    dispatch(
      LOGIN({
        email: email || baseEmail,
        password: password.value,
      })
    );
  };

  useEffect(() => {
    if (auth._id && isSuccess) {
      history.push("/dashboard");
      dispatch(SETROUTE("Dashboard"));
      dispatch(RESET());
    }
  }, [auth, isSuccess, history, location, dispatch]);

  useEffect(() => {
    function start() {
      gapi.client.init({
        clientId: clientID,
        scope: "",
      });
    }
    gapi.load("client:auth2", start);
  }, []);

  const onSuccess = (res) => {
    const { email, googleId: googleID } = res.profileObj;
    dispatch(SIGNIN_WITH_GOOGLE({ email, googleID }));
  };

  const onFailure = (res) => {
    console.log("Failure", res);
  };

  const handleLock = () => {
    if (isLocked === false) {
      setIsLocked(true);
    } else {
      setIsLocked(false);
    }
  };

  const handleClose = () => {
    toggleLogin();
    setIsForgot(false);
  };

  return (
    <MDBModal
      size={email ? "sm" : isForgot ? "lg" : "md"}
      isOpen={show}
      backdrop={true}
      cascading
      className={`${email && "modal-avatar"}`}
    >
      <MDBModalHeader
        toggle={email ? null : handleClose}
        className={`${email ? "mx-auto" : "light-blue darken-3 white-text"}`}
      >
        {email ? (
          <img
            src={image}
            onError={(e) => (e.target.src = PresetImage(auth.isMale))}
            alt={`avatar-${auth._id}`}
            className="rounded-circle img-responsive"
          />
        ) : (
          <> {headerTitle}</>
        )}
      </MDBModalHeader>
      <MDBModalBody className="mb-0">
        {!isForgot ? (
          <>
            <form onSubmit={handleSubmit}>
              {email ? (
                <div className="text-center">{email}</div>
              ) : (
                <MDBInput
                  label="E-mail Address"
                  type="email"
                  value={baseEmail}
                  onChange={({ target }) => setBaseEmail(target.value)}
                  icon="envelope"
                  name="address"
                  required
                />
              )}
              <div className="isLock">
                <MDBInput
                  style={{ borderBottom: "1px solid black" }}
                  className="pb-0"
                  label="Password"
                  name="password"
                  minLength={8}
                  icon="lock"
                  type={isLocked ? "password" : "text"}
                  required
                />
                <button
                  className="isLock-eye"
                  type="button"
                  onClick={handleLock}
                >
                  <MDBIcon icon={isLocked ? "eye-slash" : "eye"} />
                </button>
              </div>

              {message && (
                <div
                  className={`alert alert-${
                    isSuccess ? "success" : "warning"
                  } text-center`}
                >
                  {message}
                </div>
              )}

              <div
                className={`text-center mb-1-half ${
                  email && "d-flex justify-content-between"
                }`}
              >
                {email && (
                  <MDBBtn
                    block
                    disabled={isLoading}
                    onClick={() => {
                      localStorage.removeItem("token");
                      localStorage.removeItem("email");
                      window.location.reload();
                    }}
                    size="sm"
                    rounded
                    type="button"
                  >
                    logout
                  </MDBBtn>
                )}
                <MDBBtn
                  disabled={isLoading}
                  type="submit"
                  color="info"
                  className="mb-2 mt-3"
                  block
                >
                  {isLoading ? (
                    <MDBIcon icon="spinner" spin />
                  ) : email ? (
                    "proceed"
                  ) : (
                    "Sign in"
                  )}
                </MDBBtn>
                <MDBRow>
                  <MDBCol md="12" className="d-flex justify-content-end mt-2">
                    <button
                      className="forgot"
                      type="button"
                      onClick={() => setIsForgot(true)}
                    >
                      Forgot Password?
                    </button>
                  </MDBCol>
                </MDBRow>
                <div className="divider">
                  <span className="divider-text">Signin with google</span>
                </div>
                <MDBRow>
                  <MDBCol md="12">
                    <GoogleLogin
                      className="container fluid text-center "
                      clientId={clientID}
                      buttonText="Sign In With Google"
                      onSuccess={onSuccess}
                      onFailure={onFailure}
                      cookiePolicy={"single_host_origin"}
                    />
                  </MDBCol>
                </MDBRow>
                <h6 className="mt-4 text-black">
                  By clicking sign in with google, you agree to our Terms of
                  Service and Privacy Policy{" "}
                </h6>
                <br></br>
              </div>
            </form>
          </>
        ) : (
          <ForgotPasswordV2
            setIsForgot={setIsForgot}
            setBaseEmail={setBaseEmail}
            setHeaderTitle={setHeaderTitle}
          />
        )}
      </MDBModalBody>
    </MDBModal>
  );
}
