import React from "react";
import "./index.css";

export default function StudentType() {
  const types = { test: "New" };

  return (
    <div className="studentType-container">
      {Object.entries(types)?.map(([key, value]) => {
        return (
          <div key={`studentType-${key}`} className="studentType-content">
            <div className="studentType-text">
              <span>{String(value).padStart(4, "0")}</span>
              <span>{key}</span>
            </div>
            <div className="studentType-loader studentType-animation"></div>
          </div>
        );
      })}
    </div>
  );
}
