import { MDBCol, MDBInput, MDBRow } from 'mdbreact'
import React, { useState } from 'react'

const Health = ({ setDetails, data}) => {
  const [disability, setDisability] = useState(data.hasDisability.toString()),
   [disabilities, setDisabilities] = useState(data.disabilities)

  const handleHasDisablity = (value) => {
    setDetails({...data, hasDisability: value});
    setDisability(value);

    if(value === "false"){
      setDetails({...data, hasDisability: "false", disabilities: []})
      setDisabilities([]);
    }

  };
  
  const handleDisabilities = (value, checked) => {
    if (checked && disability === "true") {
      if(value === "blind" && disabilities.includes("low vision")){
        let deceased = disabilities.filter(item => item !== "low vision")
        setDetails({...data, disabilities: deceased})
        setDisabilities(deceased);
        return
      }else if (value === "low vision" && disabilities.includes("blind")){
        let deceased = disabilities.filter(item => item !== "blind")
        setDetails({...data, disabilities: deceased})
        setDisabilities(deceased);
        return
      }
      setDisabilities([...disabilities, value]);
      setDetails({...data, disabilities: [...disabilities, value]})
    } else {
      let deceased = disabilities.filter(item => item !== value)
      setDetails({...data, disabilities: deceased})
      setDisabilities(deceased);
    }
  };

  return (
    <>
      <MDBRow style={{ backgroundColor: "#0000FF" }}>
        <MDBCol>
          <h5 className="font-weight-bold text-white mt-2 label mb-2 ml-5">
            II. Health Survey
          </h5>
        </MDBCol>
      </MDBRow>

      <MDBRow className="ml-4 mt-4">
        <MDBCol>          
          <h6><span className="red-text">*</span><strong>Is the child Learner with Disability?</strong></h6>
        </MDBCol>
      </MDBRow>
      <MDBRow className="ml-4">
        <MDBCol className="p-0" md="2">
          <MDBInput 
            label="Yes"
            type="checkbox"
            value="true"
            name="disability"
            checked={disability === 'true'}
            onChange={(e) => handleHasDisablity(e.target.value)}
            id="wDisability"
          />
        </MDBCol>
        <MDBCol className="p-0" md="10">
          <MDBInput 
            label="No"
            type="checkbox"
            value="false"
            name="disability"
            checked={disability === 'false'}
            onChange={(e) => handleHasDisablity(e.target.value)}
            id="nDisability"
          />
        </MDBCol>
      </MDBRow>

      <MDBRow className="ml-4 mt-4">
        <MDBCol>          
          <h6><strong>If Yes, specify the type of disability:</strong></h6>
        </MDBCol>
      </MDBRow>

      <MDBRow>
        <MDBCol md='6'>
          <MDBRow className="m-4">
            <MDBCol>          
              <h6><strong>Visual Impairment</strong></h6>
            </MDBCol>
          </MDBRow>
          <MDBRow className="ml-4">
            <MDBCol md="4">
              <MDBInput 
                label="a. Blind"
                type="checkbox"
                value="blind"
                name="visual"
                checked={disabilities.includes("blind") && !disabilities.includes("low vision")}
                onChange={(e) => handleDisabilities(e.target.value, e.target.checked)}
                id="blind"
              />
            </MDBCol>
          
            <MDBCol md="8">
              <MDBInput 
                label="a. Low vision"
                type="checkbox"
                value="low vision"
                name="visual"
                checked={disabilities.includes("low vision") && !disabilities.includes("blind")}
                onChange={(e) => handleDisabilities(e.target.value, e.target.checked)}
                id="vision"
              />
            </MDBCol>
          </MDBRow>
        </MDBCol>

        <MDBCol  md='6'>
          <MDBRow className="m-4">
            <MDBCol className="p-0">
              <h6><strong>Special Health Problem/Chronic Disease</strong></h6>
            </MDBCol>
          </MDBRow>
          <MDBRow className="ml-4">
            <MDBCol>
                <MDBInput 
                  label="a. Cancer"
                  type="checkbox"
                  value="cancer"
                  name="diceased"
                  checked={disabilities.includes("cancer")}
                  onChange={(e) => handleDisabilities(e.target.value, e.target.checked)}
                  id="cancer"
                />
            </MDBCol>
          </MDBRow>

        </MDBCol>
      </MDBRow>

      <MDBRow className="m-4">
        <MDBCol className="p-0" md="3">
          <MDBInput 
            label="Hearing Impairment"
            type="checkbox"
            value="hearing impairment"
            name="diceased"
            checked={disabilities.includes("hearing impairment")}
            onChange={(e) => handleDisabilities(e.target.value, e.target.checked)}
            id="hearing"
          />
        </MDBCol>

        <MDBCol className="p-0" md="3">
          <MDBInput 
            label="Learning Disability"
            type="checkbox"
            value="learning disability"
            name="diceased"
            checked={disabilities.includes("learning disability")}
            onChange={(e) => handleDisabilities(e.target.value, e.target.checked)}
            id="learning"
          />
        </MDBCol>

        <MDBCol className="p-0" md="3">
          <MDBInput 
            label="Intelectual Disability"
            type="checkbox"
            value="intelectual disability"
            name="diceased"
            checked={disabilities.includes("intelectual disability")}
            onChange={(e) => handleDisabilities(e.target.value, e.target.checked)}
            id="intelectual"
          />
        </MDBCol>

        <MDBCol className="p-0" md="3">
          <MDBInput 
            label="Autism Spectrum Disorder"
            type="checkbox"
            value="autism spectrum disorder"
            name="diceased"
            checked={disabilities.includes("autism spectrum disorder")}
            onChange={(e) => handleDisabilities(e.target.value, e.target.checked)}
            id="autism"
          />
        </MDBCol>
      </MDBRow>

      <MDBRow className="m-4">
        <MDBCol className="p-0" md="3">
          <MDBInput 
            label="Emotional-Behavioral Disorder"
            type="checkbox"
            value="emotional-behavioral disorder"
            name="diceased"
            checked={disabilities.includes("emotional-behavioral disorder")}
            onChange={(e) => handleDisabilities(e.target.value, e.target.checked)}
            id="emotional"
          />
        </MDBCol>
        
        <MDBCol className="p-0" md="3">
          <MDBInput 
            label="Orthopedic/ Physical Handicap"
            type="checkbox"
            value="orthopedic/physical handicap"
            name="diceased"
            checked={disabilities.includes("orthopedic/physical handicap")}
            onChange={(e) => handleDisabilities(e.target.value, e.target.checked)}
            id="orthopedic"
          />
        </MDBCol>
        
        <MDBCol className="p-0" md="3">
          <MDBInput 
            label="Speech/ Language Disoder"
            type="checkbox"
            value="speech/language disoder"
            name="diceased"
            checked={disabilities.includes("speech/language disoder")}
            onChange={(e) => handleDisabilities(e.target.value, e.target.checked)}
            id="speech"
          />
        </MDBCol>
        
        <MDBCol className="p-0" md="3">
          <MDBInput 
            label="Celebral Paisy"
            type="checkbox"
            value="celebral paisy"
            name="diceased"
            checked={disabilities.includes("celebral paisy")}
            onChange={(e) => handleDisabilities(e.target.value, e.target.checked)}
            id="celebral"
          />
        </MDBCol>
      </MDBRow>
      <MDBRow className="m-4">
        <MDBCol className="p-0" md="3">
          <MDBInput 
            label="Multiple Disorder"
            type="checkbox"
            value="multiple disorder"
            name="diceased"
            checked={disabilities.includes("multiple disorder")}
            onChange={(e) => handleDisabilities(e.target.value, e.target.checked)}
            id="multiple"
          />
        </MDBCol>
      </MDBRow>
    </>
  )
}

export default Health