import React, { useEffect, useState } from "react";
import {
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBIcon,
  MDBModalHeader,
  MDBInput,
  MDBRow,
  MDBCol,
  MDBTypography,
} from "mdbreact";

import { useToasts } from "react-toast-notifications";
import { useDispatch, useSelector } from "react-redux";
import {
  SAVE,
  UPDATE,
  ADVISERS,
} from "../../../../services/redux/slices/pricipal/sections";
import { isEqual } from "lodash";
import { fullName, isValid } from "../../../../services/utilities";
import SelectLevel from "../../../../components/SelectLevel";
import CustomSelect from "../../../../components/customSelect";
const _form = {
  name: "",
  lvl: 7,
  strand: "ABM",
  capacity: 30,
  adviser: "",
  specialization: "Academics track",
  semester: "1st",
};

export default function Modal({
  show,
  toggle,
  selected,
  willCreate,
  collections,
  previewForm,
  setPreviewForm,
}) {
  const { token } = useSelector(({ auth }) => auth),
    { isLoading, advisers } = useSelector(({ sections }) => sections),
    [form, setForm] = useState(_form),
    [isDuplicate, setIsDuplicate] = useState(false),
    dispatch = useDispatch(),
    { addToast } = useToasts();

  useEffect(() => {
    if (!willCreate && selected._id) setForm(selected);
  }, [willCreate, selected]);

  useEffect(() => {
    if (form.lvl) {
      setIsDuplicate(isValid(collections, form, selected.name));
    }
  }, [form, collections, willCreate, selected]);

  useEffect(() => {
    if (show) {
      dispatch(ADVISERS({ token }));
    }
  }, [show, dispatch, token]);

  useEffect(() => {
    setForm((prev) => ({ ...prev, ...previewForm }));
  }, [previewForm]);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!willCreate) {
      if (isEqual(form, selected)) {
        addToast("No changes found, skipping update.", {
          appearance: "info",
        });
      } else {
        dispatch(
          UPDATE({
            data: form,
            token,
          })
        );
      }
    } else {
      dispatch(SAVE({ data: form, token }));
    }
    setPreviewForm((prev) => {
      const newForm = {
        ...prev,
        level: form.lvl,
        specialization: form.specialization,
        strand: form.strand,
        semester: form.semester,
      };
      return newForm;
    });
    setForm(_form);
    toggle();
  };

  const handleClose = () => {
    setForm(_form);
    toggle();
  };
  return (
    <MDBModal
      isOpen={show}
      toggle={toggle}
      backdrop
      disableFocusTrap={false}
      size="lg"
    >
      <MDBModalHeader
        toggle={handleClose}
        className="light-blue darken-3 white-text"
      >
        <MDBIcon icon="user" className="mr-2" />
        {!willCreate ? "Update" : "Create"} a Sections
      </MDBModalHeader>
      <MDBModalBody className="mb-0">
        <form onSubmit={handleSubmit}>
          <MDBRow className="d-flex align-items-center">
            <SelectLevel form={form} setForm={setForm} />
          </MDBRow>
          <MDBRow>
            <MDBCol>
              <MDBInput
                type="text"
                label="Name"
                value={form.name}
                onChange={(e) =>
                  setForm({ ...form, name: e.target.value.toUpperCase() })
                }
                required
              />
            </MDBCol>
            <MDBCol>
              <MDBInput
                label="Capacity"
                type="number"
                value={String(form.capacity)}
                onChange={(e) => {
                  const value = e.target.value.replace(/\./g, ""); // Remove decimal points
                  setForm({ ...form, capacity: Number(value) });
                }}
              />
            </MDBCol>
            <MDBCol>
              <CustomSelect
                choices={advisers.map(({ fullName: name, _id }) => {
                  return { _id, fullname: fullName(name) };
                })}
                texts="fullname"
                values="_id"
                label={"Adviser"}
                preValue={form.adviser?._id || ""}
                onChange={(adviser) => setForm({ ...form, adviser })}
              />
            </MDBCol>
          </MDBRow>

          <div
            className={`d-flex justify-content-${
              isDuplicate ? "between" : "end"
            } mb-1-half mt-4 align-items-center`}
          >
            {isDuplicate && (
              <MDBTypography
                variant="h2"
                className="mb-0 text-black-50"
                noteColor="danger"
                note
                noteTitle="Warning: "
              >
                Sorry But this section is Already Exist
              </MDBTypography>
            )}
            <MDBBtn
              type="submit"
              disabled={isLoading || isDuplicate}
              color="primary"
              className="mb-2 float-right"
            >
              {!willCreate ? "Update" : "Submit"}
            </MDBBtn>
          </div>
        </form>
      </MDBModalBody>
    </MDBModal>
  );
}
