import React, { useEffect, useState } from "react";
import { MDBBtn, MDBBtnGroup, MDBIcon, MDBTable } from "mdbreact";
import handleMultiSeclect from "../../../../widgets/multipleSelect";
import { fullName, handlePagination } from "../../../../services/utilities";
import Rows from "../../../../widgets/pagination/rows";
import PaginationButtons from "../../../../widgets/pagination/buttons";

const Table = ({
  lvlKey,
  students = [],
  setContainer,
  container,
  isSelectAll,
  setIsSelectAll,
  activeId,
  didSearch,
  collections,
  isViewAll,
  progress,
  setIsView,
  setSelected,
  setIsViewCard,
}) => {
  const [max, setMax] = useState(5);
  const [page, setPage] = useState(1);

  useEffect(() => {
    if (isSelectAll) {
      if (didSearch) {
        setContainer(students);
      } else {
        setContainer(collections[activeId].value);
      }
    } else {
      setContainer((prev) => (prev.length === students.length ? [] : prev));
    }
  }, [isSelectAll, students, setContainer, didSearch, collections, activeId]);
  return (
    <div>
      <MDBTable
        responsive
        hover
        key={`collapse-${lvlKey}`}
        className="overflow-hidden "
      >
        <thead>
          <tr>
            <th key={lvlKey}>
              <input
                className="form-check-input"
                type="checkbox"
                disabled
                checked={isSelectAll}
                onChange={() => setIsSelectAll(!isSelectAll)}
                id={`collapse-${lvlKey}`}
              />
              <label
                htmlFor={`collapse-${lvlKey}`}
                className="form-check-label mr-2 label-table"
              />
            </th>
            <th className="th-lg cursor-pointer">
              Name&nbsp;
              <MDBIcon icon="sort" title="Sort by Name" />
            </th>
            <th className="th-lg cursor-pointer">
              Grade Level&nbsp;
              <MDBIcon icon="sort" title="Sort by Name" />
            </th>
            <th className="th-lg cursor-pointer">
              Section&nbsp;
              <MDBIcon icon="sort" title="Sort by Name" />
            </th>

            {lvlKey > 10 && (
              <th className="cursor-pointer">
                Semester&nbsp;
                <MDBIcon icon="sort" title="Sort by Name" />
              </th>
            )}

            {isViewAll && progress === "shs" && (
              <th className="cursor-pointer">
                Semester&nbsp;
                <MDBIcon icon="sort" title="Sort by Name" />
              </th>
            )}
            {didSearch && (
              <>
                <th className="th-lg">Semester</th>
              </>
            )}
            <th className="text-center">Action</th>
          </tr>
        </thead>
        <tbody>
          {students.length > 0 ? (
            handlePagination(students, page, max)?.map((enrollee) => {
              const {
                _id,
                lvl,
                strand,
                student,
                enrolleeId,
                section,
                semester,
              } = enrollee;
              const isChecked =
                isSelectAll ||
                container.some(({ _id }) => _id === enrollee._id);
              return (
                <tr key={`${enrolleeId}-${lvlKey}`}>
                  <td>
                    <input
                      className="form-check-input"
                      type="checkbox"
                      id={_id}
                      checked={isChecked}
                      onChange={() => {
                        setContainer(
                          handleMultiSeclect(container, enrollee, false)
                        );
                        setIsSelectAll(false);
                      }}
                    />
                    <label
                      htmlFor={_id}
                      className="form-check-label mr-2 label-table"
                    />
                  </td>
                  <td style={{ fontWeight: 400 }}>
                    {fullName(student.fullName)}
                  </td>
                  <td style={{ fontWeight: 400 }}>{`Grade-${lvl}${
                    strand && ` , ${strand}`
                  }`}</td>
                  <td style={{ fontWeight: 400 }}> {section.name}</td>
                  {lvlKey > 10 && (
                    <td style={{ fontWeight: 400 }}> {semester} sem</td>
                  )}
                  {isViewAll && progress === "shs" && (
                    <td style={{ fontWeight: 400 }}> {semester} sem</td>
                  )}
                  {didSearch && (
                    <td style={{ fontWeight: 400 }}>{semester} sem</td>
                  )}
                  <td className="text-center">
                    <MDBBtnGroup>
                      <MDBBtn
                        size="sm"
                        rounded
                        color="warning"
                        title="Credentials"
                        onClick={() => {
                          setIsView(true);
                          setSelected(enrollee);
                        }}
                      >
                        <MDBIcon icon="eye" />
                      </MDBBtn>
                      <MDBBtn
                        size="sm"
                        rounded
                        color="primary"
                        title="Card"
                        onClick={() => {
                          setIsViewCard(true);
                          setSelected(enrollee);
                        }}
                      >
                        <MDBIcon icon="money-check" />
                      </MDBBtn>
                    </MDBBtnGroup>
                  </td>
                </tr>
              );
            })
          ) : (
            <tr>
              <td>No Records</td>
            </tr>
          )}
        </tbody>
      </MDBTable>
      {students.length > 0 && (
        <>
          <Rows max={max} setMax={setMax} />
          <PaginationButtons
            page={page}
            setPage={setPage}
            max={max}
            array={students}
          />
        </>
      )}
    </div>
  );
};

export default Table;
