import { MDBCol, MDBInput, MDBRow } from "mdbreact";
import React, { useState } from "react";

const AccoutDetails = ({ handleChange }) => {
  const [isLocked, setIsLocked] = useState({
    password: true,
    confirmPassword: true,
  });

  return (
    <>
      <MDBRow className="p-2 ml-3 m-2">
        <MDBCol>
          <MDBInput
            name="mobile"
            label={
              <span
                dangerouslySetInnerHTML={{
                  __html:
                    '<span class="red-text font-weight-bold">*</span>Mobile number',
                }}
              />
            }
            icon="mobile"
            type="text"
            labelClass="black-text"
            iconClass="black-text"
            onChange={(e) => handleChange("mobile", e.target.value)}
            required
            min={1}
            maxLength={11}
            pattern="[0-9]*"
          />
        </MDBCol>
      </MDBRow>
      <MDBRow className="p-2 ml-3 m-2">
        <MDBCol>
          <MDBInput
            label={
              <span
                dangerouslySetInnerHTML={{
                  __html:
                    '<span class="red-text font-weight-bold">*</span>E-mail Address',
                }}
              />
            }
            icon="envelope"
            type="email"
            labelClass="black-text"
            iconClass="black-text"
            onChange={(e) => handleChange("email", e.target.value)}
            required
            name="email"
          />
        </MDBCol>
      </MDBRow>
      <MDBRow className="p-2 ml-3 m-2">
        <MDBCol>
          <MDBInput
            label={
              <span
                dangerouslySetInnerHTML={{
                  __html:
                    '<span class="red-text font-weight-bold">*</span>Password',
                }}
              />
            }
            minLength={8}
            icon={isLocked.password ? "lock" : "unlock"}
            onIconMouseEnter={() =>
              setIsLocked({ ...isLocked, password: false })
            }
            onIconMouseLeave={() =>
              setIsLocked({ ...isLocked, password: true })
            }
            type={isLocked.password ? "password" : "text"}
            onChange={(e) => handleChange("password", e.target.value)}
            labelClass="black-text"
            iconClass="black-text"
            name="password"
            required
          />
        </MDBCol>
      </MDBRow>
      <MDBRow className="p-2 ml-3 m-2">
        <MDBCol>
          <MDBInput
            label={
              <span
                dangerouslySetInnerHTML={{
                  __html:
                    '<span class="red-text font-weight-bold">*</span>Confirm your password',
                }}
              />
            }
            minLength={8}
            icon={isLocked.confirmPassword ? "lock" : "unlock"}
            onIconMouseEnter={() =>
              setIsLocked({ ...isLocked, confirmPassword: false })
            }
            onIconMouseLeave={() =>
              setIsLocked({ ...isLocked, confirmPassword: true })
            }
            type={isLocked.confirmPassword ? "password" : "text"}
            labelClass="black-text"
            iconClass="black-text"
            name="confirmPassword"
            required
          />
        </MDBCol>
      </MDBRow>
      <MDBRow className="p-2 ml-3 m-2">
        <MDBCol>
          <MDBInput
            label="I read and agree with the Terms and Conditions"
            labelClass="black-text"
            type="checkbox"
            id="agreement"
            required
          />
        </MDBCol>
      </MDBRow>
    </>
  );
};

export default AccoutDetails;
