import React, { useEffect, useState } from "react";
import { MDBBtn, MDBBtnGroup, MDBIcon, MDBTable } from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import {
  formattedDate,
  fullName,
  handlePagination,
} from "../../../../services/utilities";
import {
  UPDATE,
  RESET,
  DROP,
} from "../../../../services/redux/slices/admission/enrollees";

import handleMultiSeclect from "../../../../widgets/multipleSelect";
import Rows from "../../../../widgets/pagination/rows";
import PaginationButtons from "../../../../widgets/pagination/buttons";
import SORTING from "../../../../widgets/sorting";
import Swal from "sweetalert2";
// import APPROVED from "../../../../widgets/desicionForEnrollees/approved";
import Modal from "./modal";
import CEO from "../coe";

const Table = ({
  lvlKey,
  enrollees = [],
  setContainer,
  container,
  isSelectAll,
  setIsSelectAll,
  activeId,
  didSearch,
  isSection,
  setIsView,
  setSelected,
  // divisions,
  props,
  isShowAll,
}) => {
  const { token, auth, information } = useSelector(({ auth }) => auth),
    [show, setShow] = useState(false),
    [selectedEnrollee, setSelectedEnrollee] = useState({}),
    [max, setMax] = useState(5),
    [page, setPage] = useState(1),
    [storageEnrollees, setStorageEnrollees] = useState([]),
    dispatch = useDispatch(),
    { previousBatches } = information;

  const batch = previousBatches.find(({ status }) => status !== "DONE");
  useEffect(() => {
    if (isSelectAll) {
      if (didSearch) {
        setContainer(enrollees);
      } else {
        setContainer(enrollees);
      }
    } else {
      setContainer((prev) => (prev.length === enrollees.length ? [] : prev));
    }
    setStorageEnrollees(SORTING(enrollees));
  }, [isSelectAll, enrollees, setContainer, didSearch, activeId]);

  useEffect(() => {
    if (isShowAll) {
      dispatch(RESET());
    }
  }, [dispatch, isShowAll]);

  const toggle = () => setShow(!show);

  const handleReject = (selected) => {
    Swal.fire({
      focusDeny: true,
      icon: "warning",
      iconColor: "#FF0000",
      title:
        "<h1 style='font-size: 25px; margin-top:10px;'>Are you sure you want to reject this student?</h1>",
      text: "This action is irreversible.",
      confirmButtonText: '<span class="text-white">Reject</span>',
      showCancelButton: true,
      confirmButtonColor: "#FF0000",
      cancelButtonText: "Cancel",
      reverseButtons: true,

      html: `
        <textarea id="swal-input1" class="swal2-textarea" style="height: 150px; width:100%;" required placeholder="Reason for rejection"></textarea>
        <br>
        <div id="swal-input-validation-message" style="color: red;"></div>
      `,
      focusConfirm: true,
      preConfirm: () => {
        const inputValue = document.getElementById("swal-input1").value;
        if (!inputValue.trim()) {
          Swal.showValidationMessage("Please provide a reason for rejection.");
          return false;
        }
        return inputValue;
      },
      allowOutsideClick: () => !Swal.isLoading(), // Para hindi ma-close ang SweetAlert kapag nag-click sa labas nito
    }).then((res) => {
      if (res.isConfirmed) {
        const rejectionReason = document.getElementById("swal-input1").value;
        dispatch(
          UPDATE({
            token,
            data: [
              {
                _id: selected.enrolleeId,
                status: "REJECT",
                registrar: { approvedBy: auth._id, message: rejectionReason },
              },
            ],
          })
        );
      }
    });
  };

  const handleDisableMultipleSelect = (isShowAll, props) => {
    const _isShowAll = !isShowAll;

    if (!_isShowAll || props !== "PENDING") return false;
  };

  const handleDrop = (student) => {
    Swal.fire({
      title: "Are you sure?",
      text: "Do you want to drop this student!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Drop it!",
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(
          DROP({
            data: { student, registrar: auth._id, date: formattedDate() },
            token,
          })
        );
        Swal.fire({
          title: "Success!",
          text: "Student drop.",
          icon: "success",
        });
      }
    });
  };

  return (
    <div>
      <MDBTable
        responsive
        hover
        key={`collapse-${lvlKey}`}
        className="overflow-hidden "
      >
        <thead>
          <tr>
            <th key={lvlKey}>
              <input
                className="form-check-input"
                type="checkbox"
                disabled={isShowAll || props !== "PENDING"}
                checked={isSelectAll}
                onChange={() => setIsSelectAll(!isSelectAll)}
                id={`collapse-${lvlKey}`}
              />
              <label
                htmlFor={`collapse-${lvlKey}`}
                className="form-check-label mr-2 label-table"
              />
            </th>
            <th className="th-lg cursor-pointer">
              Name&nbsp;
              <MDBIcon icon="sort" title="Sort by Name" />
            </th>
            <th className="th-lg">Grade Level</th>
            {isSection && <th>Section</th>}
            <th className="text-center">Action</th>
            {didSearch && (
              <>
                <th className="th-lg">Grade Level</th>
              </>
            )}
          </tr>
        </thead>
        <tbody>
          {storageEnrollees.length > 0 ? (
            handlePagination(storageEnrollees, page, max)
              ?.filter(({ wasDrop = false }) => wasDrop === false)
              .map((enrollee) => {
                const {
                  _id,
                  lvl,
                  strand,
                  student,
                  enrolleeId,
                  section = {},
                } = enrollee;
                const isChecked =
                  isSelectAll ||
                  container.some(({ _id }) => _id === enrollee._id);

                return (
                  <tr key={`${enrolleeId}-${lvlKey}`}>
                    <td>
                      <input
                        className="form-check-input"
                        type="checkbox"
                        id={_id}
                        checked={isChecked}
                        onChange={() => {
                          setContainer(
                            handleMultiSeclect(
                              container,
                              enrollee,
                              handleDisableMultipleSelect(isShowAll, props)
                            )
                          );
                          setIsSelectAll(false);
                        }}
                      />
                      <label
                        htmlFor={_id}
                        className="form-check-label mr-2 label-table"
                      />
                    </td>
                    <td style={{ fontWeight: 400 }}>
                      {fullName(student.fullName)}
                    </td>
                    <td style={{ fontWeight: 400 }}>{`Grade - ${lvl} ${
                      lvl > 10 ? `, ${strand}` : ""
                    }`}</td>

                    {isSection && (
                      <td style={{ fontWeight: 400 }}>{section.name}</td>
                    )}
                    <td className="d-flex justify-content-center">
                      {props === "PENDING" ? (
                        <MDBBtnGroup>
                          <MDBBtn
                            size="sm"
                            rounded
                            color="yellow"
                            title="View"
                            onClick={() => {
                              setSelected(enrollee);
                              setIsView(true);
                            }}
                          >
                            <MDBIcon icon="eye" />
                          </MDBBtn>
                          <MDBBtn
                            size="sm"
                            rounded
                            color="primary"
                            title="Approved"
                            onClick={() => {
                              setSelectedEnrollee(enrollee);
                              setShow(true);
                            }}
                          >
                            <MDBIcon icon="user-check" />
                          </MDBBtn>
                          <MDBBtn
                            size="sm"
                            title="Reject"
                            rounded
                            color="danger"
                            onClick={() => handleReject(enrollee)}
                          >
                            <MDBIcon icon="user-times" />
                          </MDBBtn>
                        </MDBBtnGroup>
                      ) : (
                        <>
                          <MDBBtnGroup>
                            <MDBBtn
                              size="sm"
                              rounded
                              color="yellow"
                              title="View"
                              onClick={() => {
                                setSelected(enrollee);
                                setIsView(true);
                              }}
                            >
                              <MDBIcon icon="eye" />
                            </MDBBtn>
                            {props === "APPROVED" && (
                              <>
                                <MDBBtn
                                  onClick={() =>
                                    CEO(
                                      fullName(student.fullName),
                                      section,
                                      batch.sy
                                    )
                                  }
                                  size="sm"
                                  rounded
                                  title="Certificate of Enrollment"
                                  color="green"
                                >
                                  <MDBIcon icon="scroll" />
                                </MDBBtn>
                                <MDBBtn
                                  color="primary"
                                  size="sm"
                                  title="Change Section"
                                  rounded
                                  onClick={() => {
                                    setSelectedEnrollee(enrollee);
                                    setShow(true);
                                  }}
                                >
                                  <MDBIcon icon="window-restore" />
                                </MDBBtn>
                                <MDBBtn
                                  color="danger"
                                  size="sm"
                                  title="Drop"
                                  rounded
                                  onClick={() => {
                                    handleDrop(enrollee);
                                  }}
                                >
                                  <MDBIcon icon="user-times" />
                                </MDBBtn>
                              </>
                            )}
                          </MDBBtnGroup>
                        </>
                      )}
                    </td>

                    <td style={{ fontWeight: 400 }}>
                      {didSearch && `Grade-${lvl}${strand && ` , ${strand}`}`}
                    </td>
                  </tr>
                );
              })
          ) : (
            <tr>
              <td>No Records</td>
            </tr>
          )}
        </tbody>
      </MDBTable>
      <Rows max={max} setMax={setMax} />
      <Modal
        show={show}
        toggle={toggle}
        selected={selectedEnrollee}
        status={props}
      />
      <PaginationButtons
        page={page}
        setPage={setPage}
        max={max}
        array={storageEnrollees}
      />
    </div>
  );
};

export default Table;
