import {
  MDBCol,
  MDBDatePicker,
  MDBInput,
  MDBRow,
  MDBSelect,
  MDBSelectInput,
  MDBSelectOption,
  MDBSelectOptions,
} from "mdbreact";
import React from "react";
import moment from "moment";

const PersonalDetails = ({
  userDetails,
  setDate,
  handleObjChange,
  handleChange,
}) => {
  const handleDateChange = (value) => {
    if (moment(value).isValid()) {
      setDate((prevFormData) => ({
        ...prevFormData,
        dob: value,
      }));
    }
  };

  return (
    <>
      <MDBRow className="m-2">
        <MDBCol md="7">
          <MDBInput
            className="m-0"
            label={
              <span
                dangerouslySetInnerHTML={{
                  __html:
                    '<span class="red-text font-weight-bolder" style={{fontSize: 4rem}} >*</span>Firstname',
                }}
              />
            }
            type="text"
            labelClass="black-text"
            iconClass="black-text"
            value={userDetails.fullName.fname}
            onChange={(e) =>
              handleObjChange("fullName", "fname", e.target.value.toUpperCase())
            }
            required
          />
        </MDBCol>
        <MDBCol md="5">
          <MDBInput
            className="m-0"
            label="Middlename"
            type="text"
            labelClass="black-text"
            iconClass="black-text"
            value={userDetails.fullName.mname}
            onChange={(e) =>
              handleObjChange("fullName", "mname", e.target.value.toUpperCase())
            }
            required
          />
        </MDBCol>
      </MDBRow>
      <MDBRow className="m-2">
        <MDBCol md="7">
          <MDBInput
            className="m-0"
            label={
              <span
                dangerouslySetInnerHTML={{
                  __html:
                    '<span class="red-text font-weight-bold">*</span>Lastname',
                }}
              />
            }
            type="text"
            labelClass="black-text"
            iconClass="black-text"
            value={userDetails.fullName.lname}
            onChange={(e) =>
              handleObjChange("fullName", "lname", e.target.value.toUpperCase())
            }
            required
          />
        </MDBCol>
        <MDBCol md="5">
          <MDBSelect
            className="colorful-select dropdown-primary hidden-md-down text-left"
            label="Suffix"
            getValue={(e) =>
              handleObjChange("fullName", "suffix", e[0] === "N/A" ? "" : e[0])
            }
          >
            <MDBSelectInput />
            <MDBSelectOptions>
              <MDBSelectOption
                selected={!userDetails.fullName?.suffix}
                value="N/A"
              >
                N/A
              </MDBSelectOption>
              <MDBSelectOption
                selected={userDetails.fullName.suffix === "JR"}
                value="JR"
              >
                JR
              </MDBSelectOption>
              <MDBSelectOption
                selected={userDetails.fullName.suffix === "III"}
                value="III"
              >
                III
              </MDBSelectOption>
              <MDBSelectOption
                selected={userDetails.fullName.suffix === "IV"}
                value="IV"
              >
                IV
              </MDBSelectOption>
              <MDBSelectOption
                selected={userDetails.fullName.suffix === "V"}
                value="V"
              >
                V
              </MDBSelectOption>
              <MDBSelectOption
                selected={userDetails.fullName.suffix === "SR"}
                value="SR"
              >
                SR
              </MDBSelectOption>
            </MDBSelectOptions>
          </MDBSelect>
        </MDBCol>
      </MDBRow>
      <MDBRow className="d-flex align-items-center m-2">
        <MDBCol md="3" className="black-text">
          <div>
            <span className="red-text font-weight-bolder">*</span>Date of birth:
          </div>
        </MDBCol>
        <MDBCol md="4">
          <MDBDatePicker getValue={handleDateChange} value={userDetails.dob} />
        </MDBCol>
        <MDBCol md="5">
          <label className="black-text">
            <span className="red-text font-weight-bold">*</span>Gender
          </label>
          <MDBRow className="d-flex flex-direction-column">
            <MDBCol md="6">
              <MDBInput
                className="m-0"
                label="Male"
                labelClass="black-text"
                iconClass="black-text"
                type="checkbox"
                checked={userDetails.isMale}
                onChange={(e) => handleChange("isMale", e.target.value)}
                id="isMail"
              />
            </MDBCol>
            <MDBCol md="6">
              <MDBInput
                className="m-0"
                label="Female"
                iconClass="black-text"
                labelClass="black-text"
                type="checkbox"
                checked={!userDetails.isMale}
                onChange={(e) => handleChange("isMale", false)}
                id="isFemail"
              />
            </MDBCol>
          </MDBRow>
        </MDBCol>
      </MDBRow>
      <MDBRow className="m-2">
        <MDBCol>
          <MDBInput
            className="m-0"
            label="Birthplace"
            type="text"
            labelClass="black-text"
            iconClass="black-text"
            value={userDetails.birthplace}
            onChange={(e) =>
              handleChange("birthplace", e.target.value.toUpperCase())
            }
            required
          />
        </MDBCol>
      </MDBRow>
    </>
  );
};

export default PersonalDetails;
