import React from "react";
import { MDBRow, MDBCol } from "mdbreact";

function level({
  form,
  setForm,
  disableSemester,
  isRegistrar = false,
  disableSemesterInRegistar = false,
  disableShowAll = false,
}) {
  return (
    <>
      <MDBRow>
        <MDBCol>
          <select
            className="form-control bg-light "
            onChange={(e) =>
              setForm({ ...form, level: Number(e.target.value) })
            }
            value={form.level}
          >
            {form.levels.length > 0 &&
              form.levels.map((lvl, index) => (
                <option key={index} value={lvl}>
                  Grade - {lvl}
                </option>
              ))}
            {!disableShowAll && <option value={1}>Show All</option>}
          </select>
        </MDBCol>
        {form.level > 10 && (
          <>
            <MDBCol>
              <select
                className="form-control bg-light"
                onChange={(e) =>
                  setForm({ ...form, specialization: e.target.value })
                }
                value={form.specialization}
              >
                {form?.specializations?.length > 0 &&
                  form.specializations.map((sp, index) => (
                    <option key={index} value={sp}>
                      {sp}
                    </option>
                  ))}
              </select>
            </MDBCol>
            <MDBCol>
              <select
                className="form-control bg-light"
                onChange={(e) => setForm({ ...form, strand: e.target.value })}
                value={form.strand}
              >
                {form?.strands?.length > 0 &&
                  form.strands.map((st, index) => (
                    <option key={index} value={st}>
                      {st}
                    </option>
                  ))}
              </select>
            </MDBCol>
            {!disableSemester && (
              <MDBCol>
                <select
                  className="form-control bg-light"
                  onChange={(e) =>
                    setForm({ ...form, semester: e.target.value })
                  }
                  value={form.semester}
                >
                  {form?.semesters?.length > 0 &&
                    form.semesters.map((sm, index) => (
                      <option key={index} value={sm}>
                        {sm} Semester
                      </option>
                    ))}
                </select>
              </MDBCol>
            )}
          </>
        )}
        {isRegistrar && (
          <>
            <MDBCol>
              <select
                className="form-control bg-light"
                onChange={(e) => setForm({ ...form, section: e.target.value })}
                value={form.section}
              >
                {form.sections.length > 0 &&
                  form.sections.map((section, index) => (
                    <option key={index} value={section?._id}>
                      {section?.name}
                    </option>
                  ))}
              </select>
            </MDBCol>
            {!disableSemesterInRegistar && form.level > 10 && (
              <MDBCol>
                <select
                  className="form-control bg-light"
                  onChange={(e) =>
                    setForm({ ...form, semester: e.target.value })
                  }
                  value={form.semester}
                >
                  {form?.semesters?.length > 0 &&
                    form.semesters.map((sm, index) => (
                      <option key={index} value={sm}>
                        {sm} Semester
                      </option>
                    ))}
                </select>
              </MDBCol>
            )}
          </>
        )}
      </MDBRow>
    </>
  );
}

export default level;
