import { MDBCol, MDBRow } from 'mdbreact'
import React, { useEffect, useState } from 'react';
import Image from '../../../../../widgets/Image';
import { useSelector } from 'react-redux';

const Documents = ({ setDetails, data }) => {
    const { auth } = useSelector(
        ({ auth }) => auth
      ),
    [userDataImage, setUserDataImage] = useState({
        studentPicture: data.studentPicture instanceof File ? URL.createObjectURL(data.studentPicture): data.studentPicture,
        signature: data.signature instanceof File ? URL.createObjectURL(data.signature): data.signature,
        PSA: data.PSA instanceof File ? URL.createObjectURL(data.PSA): data.PSA,
        card: data.card instanceof File ? URL.createObjectURL(data.card): data.card,
    })
    const viewOnly = false;

    useEffect(() => {
        if(viewOnly){
            if(data.has2x2 || data.hasSignature || data.hasPsa || data.hasCard){
                setUserDataImage({email: auth.email, _id: auth._id, filesData: {has2x2: data.has2x2, hasSignature: data.hasSignature, hasPsa: data.hasPsa, hasCard: data.hasCard}})
                return
            }
        }
        if(!viewOnly){
            if(data.has2x2 || data.hasSignature || data.hasPsa || data.hasCard){
                setUserDataImage(previous => ({   
                    ...previous, 
                    email: auth.email, 
                    _id: auth._id, 
                    filesData: {has2x2: data.has2x2, hasSignature: data.hasSignature, hasPsa: data.hasPsa, hasCard: data.hasCard}
                }))
                return
            }
        }


    }, [data.has2x2, data.hasSignature, data.hasPsa, data.hasCard, viewOnly, auth.email, auth._id]);

    const handleChange = (e) => {
        const { name, files } = e.target;
        
        if (files.length > 0) {
            const file = files[0];
            setDetails({
                ...data,
                [name]: file,
            });

            const fileUrl = URL.createObjectURL(file)
            setUserDataImage((prev) => ({
                ...prev,
                [name]: fileUrl
            }));
        }
    };

    return (
        <>
            <MDBRow style={{ backgroundColor: "#0000FF" }}>
                <MDBCol>
                <h5 className="font-weight-bold text-white label mt-2 mb-2 ml-5">
                    V. CREDENTIALS
                </h5>
                </MDBCol>
            </MDBRow>

            <MDBRow>
                <h6 className="ml-5 mt-4 font-weight-bold label mt-2 mb-2 ml-5">
                <strong>Documents</strong> -
                <i>
                    upload all your documents.
                </i>
                </h6>
            </MDBRow>
            
            <MDBRow className='mt-4'>
                <Image 
                    userDataImage={userDataImage}
                    handleChange={handleChange}
                    viewOnly = {viewOnly}
                />
            </MDBRow>
        </>
    );
}

export default Documents