function handleMultiSeclect(array, obj, isMultiple = true) {
  var _container = [...array];
  const index = _container.findIndex((c) => obj._id === c._id);

  if (index > -1) {
    _container.splice(index, 1);
  } else {
    if (isMultiple) {
      _container.push(obj);
    } else {
      _container = [obj];
    }
  }

  return _container;
}

export default handleMultiSeclect;
