import React, { useEffect, useState } from "react";
import {
  MDBModal,
  MDBModalBody,
  MDBIcon,
  MDBModalHeader,
  MDBRow,
  MDBCol,
  MDBTable,
  MDBCard,
  MDBCardBody,
  MDBBadge,
  MDBTypography,
  MDBModalFooter,
  MDBView,
} from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import {
  SectionsWithStudents,
  CHANGESECTION,
  UPDATE,
} from "../../../../services/redux/slices/admission/enrollees";
import Swal from "sweetalert2";
import { fullName } from "../../../../services/utilities";

export default function Modal({
  show,
  toggle,
  selected,
  status = "",
  setIsView = () => {},
}) {
  const { token, information, auth } = useSelector(({ auth }) => auth),
    { sectionsWithStudents } = useSelector(({ enrollees }) => enrollees),
    [sections, setSections] = useState([]),
    [section, setSection] = useState({}),
    [students, setStudents] = useState([]),
    dispatch = useDispatch();

  useEffect(() => {
    if (show) {
      dispatch(
        SectionsWithStudents({
          token,
          data: {
            batches: information.batches,
            lvl: selected.lvl,
            strand: selected.strand || "",
          },
        })
      );
    }
  }, [information, token, dispatch, show, selected]);

  useEffect(() => {
    if (show && sectionsWithStudents.length > 0) {
      const sections = sectionsWithStudents
        .map(({ section }) => section)
        .filter((sec) => sec._id !== selected?.section?._id);

      const students = sectionsWithStudents.find(
        ({ section: sec }) => sec?._id === sections[0]?._id
      ).students;

      setSections(sections);
      setSection(sections[0]);
      setStudents(students);
    }
  }, [sectionsWithStudents, show, selected]);

  useEffect(() => {
    if (show) {
      const _students = sectionsWithStudents.find(
        ({ section: sec }) => sec?._id === section?._id
      )?.students;
      setStudents(_students || []);
    }
  }, [section, show, sectionsWithStudents]);

  const handleClose = () => {
    toggle();
  };

  const handleDrop = (e, obj) => {
    e.preventDefault();

    const { student, enrolleeId } = JSON.parse(
      e.dataTransfer.getData("text/plain")
    );

    const _html =
      status !== "PENDING"
        ? `You want to transfer <br/> <b> ${fullName(
            student.fullName
          )}</b><br/> to <br/> Grade <b>${obj.lvl} ${
            obj.strand && obj.lvl > 10 ? obj.strand : ""
          } - ${obj.name}</b>.`
        : `Do you want to assign  <br/> <b> ${fullName(
            student.fullName
          )} to <br/> Grade <b>${obj.lvl} ${
            obj.strand && obj.lvl > 10 ? obj.strand : ""
          } - ${obj.name}</b>. `;

    Swal.fire({
      focusDeny: true,
      icon: "question",
      title: "Are you sure?",
      html: _html,
      confirmButtonText: `<span class="text-dark">Cancel</span>`,
      confirmButtonColor: "#fff",
      showDenyButton: true,
      denyButtonText: `Proceed`,
      denyButtonColor: "#3B71CA",
    }).then((res) => {
      if (res.isDenied) {
        if (status !== "PENDING") {
          dispatch(
            CHANGESECTION({ token, data: { enrolleeId, section: obj._id } })
          );
        } else {
          const enrollee = {
            _id: enrolleeId,
            section: obj._id,
            status: "APPROVED",
            registrar: { approvedBy: auth._id },
          };
          dispatch(
            UPDATE({
              token,
              data: [enrollee],
            })
          );
          setIsView(false);
        }
      } else {
        Swal.fire({
          title: "Changes are not Saved!",
          icon: "warning",
          showConfirmButton: false,
          timer: 2500,
          timerProgressBar: true,
        });
      }
    });
  };
  const dragOver = (e) => {
    e.preventDefault();
  };

  return (
    <MDBModal isOpen={show} toggle={toggle} backdrop size="fluid">
      <MDBModalHeader
        toggle={handleClose}
        className="light-blue darken-3 white-text "
      >
        <MDBIcon icon="user" className="mr-2" />
        {status !== "Pending" ? "Approve Enrollee" : "Change Section"}
      </MDBModalHeader>
      <MDBModalBody className="mb-0">
        <MDBRow className="mt-0">
          <MDBCol md="12" className="text-center">
            <MDBTypography
              variant="h2"
              className="mb-0 text-black-50"
              noteColor="success"
              note
              noteTitle="Instruction: "
            >
              Drag a {status !== "Pending" ? "enrollee" : "student"} to a
              section or into the table.
            </MDBTypography>
          </MDBCol>
        </MDBRow>
        <MDBRow>
          <MDBCol
            md="4"
            className="mt-2 cursor-pointer"
            draggable="true"
            onDragStart={(e) => {
              e.dataTransfer.setData("text/plain", JSON.stringify(selected));
            }}
          >
            <h5>
              {status !== "PENDING" ? "Student" : "Enrollee"}:{" "}
              <strong>{fullName(selected?.student?.fullName)}</strong>
            </h5>
            {status === "APPROVED" ? (
              <h5>
                Current Section:&nbsp;
                <strong>
                  Grade - {selected?.section?.lvl}{" "}
                  {selected?.strand ? selected?.strand : ""} ,
                  {selected?.section?.name}
                </strong>
              </h5>
            ) : (
              <h5>
                Grade - &nbsp;
                <strong>
                  {selected?.lvl} {selected?.strand ? selected?.strand : ""}
                </strong>
              </h5>
            )}
          </MDBCol>
        </MDBRow>
        <MDBRow className="mt-4">
          <MDBCol md="4" style={{ maxHeight: "550px", overflowY: "auto" }}>
            {sections.length > 0 ? (
              sections.map((obj, index) => (
                <MDBCard
                  className="mt-1 mb-2"
                  key={index}
                  onClick={() => setSection(obj)}
                  onDragOver={dragOver}
                  onDrop={(e) => handleDrop(e, obj)}
                >
                  <MDBCardBody
                    className={`d-flex justify-content-between cursor-pointer ${
                      obj?._id === section?._id && " border border-primary"
                    }`}
                  >
                    <div>
                      <h5 className="font-weight-bold">{obj.name}</h5>
                      <h6>Adviser: {fullName(obj.adviser?.fullName)}</h6>
                    </div>
                    <div>
                      <MDBBadge pill color="primary">
                        {obj.student}
                      </MDBBadge>
                    </div>
                  </MDBCardBody>
                </MDBCard>
              ))
            ) : (
              <h6>No Sections</h6>
            )}
          </MDBCol>
          <MDBCol md="8">
            <MDBCard narrow>
              <MDBView
                cascade
                className="gradient-card-header blue-gradient py-2 mx-4 d-flex justify-content-between align-items-center"
              >
                Student list in {section.name}
              </MDBView>
              <MDBCardBody
                style={{ maxHeight: "550px", overflowY: "auto" }}
                onDragOver={dragOver}
                onDrop={(e) => handleDrop(e, section)}
              >
                {students.length > 0 ? (
                  <MDBTable>
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>LRN</th>
                        <th>Full Name</th>
                        <th>Section</th>
                      </tr>
                    </thead>
                    <tbody>
                      {students.map((obj, index) => (
                        <tr key={index}>
                          <td>{index + 1}</td>
                          <td className="font-weight-bold">10537200112</td>
                          <td className="font-weight-bold">
                            {fullName(obj.student?.fullName)}
                          </td>
                          <td className="font-weight-bold">{section.name}</td>
                        </tr>
                      ))}
                    </tbody>
                  </MDBTable>
                ) : (
                  <MDBTypography
                    className="mb-0 text-black-50"
                    noteColor="danger"
                    note
                    noteTitle="Note: "
                  >
                    There are no students in the {section.name}
                  </MDBTypography>
                )}
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        </MDBRow>
      </MDBModalBody>
      <MDBModalFooter></MDBModalFooter>
    </MDBModal>
  );
}
