const subjectIsDuplicate = (sections, pickDays) => {
  var isDuplicate = false;
  var day = "";
  var timeConflict = {};
  for (let parentIndex = 0; parentIndex < sections.length; parentIndex++) {
    const section = sections[parentIndex];
    const schedules = section.schedules || [];

    for (let index = 0; index < schedules.length; index++) {
      const schedule = schedules[index];

      const isExistDay = pickDays.findIndex((value) =>
        schedule?.days.includes(value)
      );

      if (isExistDay > -1) {
        isDuplicate = true;
        timeConflict = schedule.time;
        day = schedule.days.join(" , ");
      }
    }
    if (isDuplicate) {
      return { isDuplicate, day, timeConflict, element: section };
    }
  }
  return { isDuplicate: false };
};

const scheduleIsValid = (
  array,
  pickDays,
  hourStart,
  hourEnd,
  minStart,
  minEnd,
  form
) => {
  var isConflict = false;
  var day = "";
  var timeConflict = {};

  for (let parentIndex = 0; parentIndex < array.length; parentIndex++) {
    const element = array[parentIndex];
    const schedules = element.schedules;

    for (let index = 0; index < schedules.length; index++) {
      const schedule = schedules[index];
      const indexOfDays = pickDays.findIndex((value) =>
        schedule.days.includes(value)
      );
      if (indexOfDays > -1) {
        const { time } = schedule;
        const { start, end } = time;
        const oldSched = [];
        const newSched = [];

        var _startHour = Number(start?.hours);
        var _endHour = Number(end?.hours);

        const startOldHour =
          _startHour >= 1 && _startHour <= 5 ? _startHour + 12 : _startHour;

        const startNewHour =
          hourStart >= 1 && hourStart <= 5 ? hourStart + 12 : hourStart;

        const endOldHour =
          _endHour >= 1 && _endHour <= 5 ? _endHour + 12 : _endHour;

        const endNewHour =
          hourEnd >= 1 && hourEnd <= 5 ? hourEnd + 12 : hourEnd;

        for (let hour = startOldHour; hour <= endOldHour; hour++) {
          oldSched.push(hour);
        }

        for (let hour = startNewHour; hour <= endNewHour; hour++) {
          newSched.push(hour);
        }

        var isExistHour = newSched.some((hour) => oldSched.includes(hour));

        const oldMinStart = Number(time.start?.min);
        const oldMinEnd = Number(time.end?.min);

        if (hourStart === _endHour) {
          if (minStart < oldMinEnd) {
            isConflict = true;
          } else {
            isConflict = false;
          }
        } else if (hourEnd === _startHour) {
          if (minEnd > oldMinStart) {
            isConflict = true;
          } else {
            isConflict = false;
          }
        } else {
          isConflict = isExistHour;
        }

        if (isConflict) {
          day = schedule.days.join(" , ");
          timeConflict = time;
        }
      } else {
        if (array.length !== parentIndex) {
          continue;
        } else {
          isConflict = false;
          break;
        }
      }
    }
    if (isConflict) {
      // para kung senior ichecheck ko yung semester
      if (form.lvl > 10) {
        if (element.lvl <= 10) {
          return { element, isConflict, day, timeConflict };
        } else {
          if (element.semester === form.semester) {
            return { element, isConflict, day, timeConflict };
          } else {
            return { isConflict: false };
          }
        }
      } else {
        // pang junior
        return { element, isConflict, day, timeConflict };
      }
    }
  }
  return { isConflict: false };
};

export default {
  scheduleIsValid,
  subjectIsDuplicate,
};
