import Swal from "sweetalert2";
const APPROVED = ({ sections, array, dispatch, auth, token, UPDATE }) => {
  const enabledOptionsHTML = [];
  const disabledOptionsHTML = [];
  const isMultiple = array.length === 1 ? false : true;

  sections.forEach((section) => {
    const condition = isMultiple
      ? section.students === section.capacity ||
        array.length + section.students > section.capacity
      : section.students === section.capacity;

    const optionHTML = `
        <option 
        ${
          condition
            ? 'disabled="disabled" style="background-color: red; color: white;"'
            : ""
        }
        value="${section._id}">
          ${section.name} - ${section.students}/${section.capacity}
        </option>`;

    if (
      section.students === section.capacity ||
      array.length + section.students >= section.capacity
    ) {
      disabledOptionsHTML.push(optionHTML);
    } else {
      enabledOptionsHTML.push(optionHTML);
    }
  });

  const optionsHTML = enabledOptionsHTML.concat(disabledOptionsHTML).join("");

  Swal.fire({
    icon: "question", // Set icon to question mark
    iconColor: "#00FF00",
    title: `<h4 style='font-size: 25px; margin-top:10px;'>Select the section where you would like to place this ${
      isMultiple ? `${array.length} students` : "student."
    }</h4>`,
    html: `
        <form id="selectForm">
          <select id="selectOption" class="swal2-select form-control w-75" required>
            <option value="">Select a section</option>
            ${optionsHTML}
          </select>
        </form>
      `,
    showCancelButton: true,
    cancelButtonText: "Cancel",
    confirmButtonText: "OK",
    reverseButtons: true,
    preConfirm: () => {
      const selectedOption = document.getElementById("selectOption").value;
      if (!selectedOption) {
        Swal.showValidationMessage("Please select a section");
      }
      return { selectedOption: selectedOption };
    },
  }).then((result) => {
    if (result.isConfirmed) {
      const section = result.value.selectedOption;
      const enrollees = array.map((enrollee) => ({
        _id: enrollee.enrolleeId,
        section,
        status: "APPROVED",
        registrar: { approvedBy: auth._id },
      }));
      dispatch(
        UPDATE({
          token,
          data: enrollees,
        })
      );
    }
  });
};

export default APPROVED;
