import React, { useEffect, useState } from "react";
import { MDBCard, MDBCardBody, MDBView, MDBCol } from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import { BROWSE } from "../../../../services/redux/slices/registrar/students";
import { useToasts } from "react-toast-notifications";

import Table from "./table";
import Search from "../../../../widgets/search";
import TableLoading from "../../../../widgets/tableLoading";
import { TeacherHeaderBatch } from "../../../../widgets/header";
import ViewEnrollee from "../../../../widgets/viewEnrollee";
import handleForm from "../../../../widgets/form";
import Level from "../../../../widgets/form/level";
import Card from "../../../../widgets/card-grades";
import { globalSearch } from "../../../../services/utilities";
const { StoreDefualtValueForForm } = handleForm;

const _form = {
  levels: [],
  level: 0,
  specializations: [],
  specialization: "",
  strands: [],
  strand: "",
  semesters: [],
  semester: "",
  sections: [],
  section: "",
};

export default function Students() {
  const { token, information } = useSelector(({ auth }) => auth),
    { collections, message, isSuccess, isLoading } = useSelector(
      ({ students }) => students
    ),
    [students, setStudents] = useState([]),
    [container, setContainer] = useState([]),
    [params, setParams] = useState({ progress: "", sy: "" }),
    [progress, setProgress] = useState([]),
    [selected, setSelected] = useState({}),
    [form, setForm] = useState(_form),
    [isChangeSY, setisChangeSY] = useState(false),
    [isView, setIsView] = useState(false),
    [isViewCard, setIsViewCard] = useState(false),
    [isSelectAll, setIsSelectAll] = useState(false),
    [didSearch, setDidSearch] = useState(false),
    dispatch = useDispatch(),
    { addToast } = useToasts();

  useEffect(() => {
    if (message) {
      addToast(message, {
        appearance: isSuccess ? "success" : "error",
      });
    }
  }, [isSuccess, message, addToast]);

  useEffect(() => {
    if (!isLoading && !didSearch) {
      StoreDefualtValueForForm({
        form: {
          level: form?.level,
          strand: form?.strand,
          specialization: form?.specialization,
          section: form?.section,
          semester: form?.semester,
        },
        isRegistrar: true,
        setForm,
        collections,
        setArray: setStudents,
        setIsSelectAll,
      });
    }
  }, [
    collections,
    form.level,
    form.strand,
    form.specialization,
    form.section,
    form.semester,
    isLoading,
    didSearch,
  ]);

  useEffect(() => {
    const defaultSY = information.previousBatches[0]?.sy;
    handleDefaultParams(defaultSY, information?.previousBatches);
  }, [information.previousBatches]);

  useEffect(() => {
    handleDefaultParams(params.sy, information?.previousBatches);
  }, [params.sy, information.previousBatches]);

  useEffect(() => {
    if (isChangeSY && params.sy && params.progress) {
      const batches = information.previousBatches
        .filter(
          ({ sy, progress: p }) => sy === params?.sy && params?.progress === p
        )
        .map(({ _id }) => _id);
      if (batches) {
        dispatch(
          BROWSE({
            token,
            data: { batch: batches },
          })
        );
        setisChangeSY(false);
      }
    }
  }, [information, token, dispatch, params, isChangeSY]);

  const handleDefaultParams = (_sy = "", batches) => {
    const _progress = batches
      .filter(({ sy }) => sy === _sy)
      .map(({ progress }) => progress);

    const _uniqueProgress = [...new Set(_progress)];
    setProgress(_uniqueProgress);
    setParams((prevParams) => ({
      progress: _uniqueProgress[0] || "",
      sy: prevParams.sy ? prevParams.sy : _sy,
    }));
    setisChangeSY(true);
  };

  const handleSearch = (e) => {
    e.preventDefault();

    const key = e.target.searchKey.value.toUpperCase();
    setStudents(globalSearch(students, key));

    setDidSearch(true);
  };

  const handleView = () => {
    if (container.length === 1) {
      setIsView(true);
      setSelected(container[0]);
    } else {
      addToast("Please Select 1 student", {
        appearance: "warning",
      });
    }
  };

  return (
    <>
      {!isView && !isViewCard ? (
        <MDBCard narrow className="pb-3">
          <MDBView
            cascade
            className="gradient-card-header blue-gradient py-2 mx-4 d-flex justify-content-between align-items-center"
          >
            <span className="ml-3 ">
              <i>Student List</i>
            </span>
            <div className="d-flex align-items-center justify-content-center w-50">
              <TeacherHeaderBatch params={params} setParams={setParams} />
              <MDBCol size="3">
                <select
                  className="form-control bg-light"
                  value={params.progress}
                  onChange={(e) => {
                    setParams({ ...params, progress: e.target.value });
                    setisChangeSY(true);
                  }}
                >
                  {progress.map((p, index) => (
                    <option key={index} value={p}>
                      {p.toUpperCase()}
                    </option>
                  ))}
                </select>
              </MDBCol>
            </div>

            <Search
              handleSearch={handleSearch}
              didSearch={didSearch}
              setDidSearch={setDidSearch}
              array={students}
              setArray={setStudents}
              isRegistrar={true}
              handleView={handleView}
            />
          </MDBView>
          <MDBCardBody>
            {isLoading ? (
              <TableLoading />
            ) : (
              <>
                {students.length > 0 ? (
                  <>
                    {!didSearch ? (
                      <>
                        <Level
                          form={form}
                          setForm={setForm}
                          isRegistrar={form.level === 1 ? false : true}
                          disableSemester={true}
                        />
                        <Table
                          collections={collections}
                          isSelectAll={isSelectAll}
                          setIsSelectAll={setIsSelectAll}
                          lvlKey={form.level}
                          students={students}
                          isViewAll={form.level === 1 ? true : false}
                          progress={params.progress}
                          container={container}
                          setContainer={setContainer}
                          setSelected={setSelected}
                          setIsView={setIsView}
                          setIsViewCard={setIsViewCard}
                        />
                      </>
                    ) : (
                      <Table
                        didSearch={didSearch}
                        students={students}
                        progress={params.progress}
                        container={container}
                        setContainer={setContainer}
                        isViewAll={form.level === 1 ? true : false}
                        isSelectAll={isSelectAll}
                        setIsSelectAll={setIsSelectAll}
                        setIsView={setIsView}
                        setSelected={setSelected}
                        setIsViewCard={setIsViewCard}
                      />
                    )}
                  </>
                ) : (
                  <h5 className="text-center">No Record.</h5>
                )}
              </>
            )}
          </MDBCardBody>
        </MDBCard>
      ) : (
        <>
          {isView && <ViewEnrollee selected={selected} setIsView={setIsView} />}
          {isViewCard && (
            <Card
              setIsViewCard={setIsViewCard}
              selected={selected}
              sy={params.sy}
            />
          )}
        </>
      )}
    </>
  );
}
