import React, { useEffect, useState } from "react";
import { MDBCard, MDBCardBody, MDBView } from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import {
  BROWSE,
  DESTROY,
} from "../../../../services/redux/slices/registrar/studentsDrop";
import { useToasts } from "react-toast-notifications";
import Table from "./table";
import Modal from "./modal";
import Search from "../../../../widgets/search";
import MultipleDestroy from "../../../../widgets/multipleDestroy";
import TableLoading from "../../../../widgets/tableLoading";
import handleForm from "../../../../widgets/form";
import Level from "../../../../widgets/form/level";
const { StoreDefualtValueForForm } = handleForm;
const _form = {
  levels: [],
  level: 0,
  specializations: [],
  specialization: "",
  sections: [],
  section: "",

  strands: [],
  strand: "",
  semesters: [],
  semester: "",
};
export default function StudentsDrop() {
  const { token } = useSelector(({ auth }) => auth),
    { collections, message, isSuccess, isloading } = useSelector(
      ({ studentsDrop }) => studentsDrop
    ),
    [students, setstudents] = useState([]),
    [studentsNotArrange, setStudentsNotArrange] = useState([]),
    [batch, setBatch] = useState(""),
    [batches, setBatches] = useState([]),
    [container, setContainer] = useState([]),
    [form, setForm] = useState(_form),
    [isSelectAll, setIsSelectAll] = useState(false),
    [show, setShow] = useState(false),
    [willCreate, setWillCreate] = useState(true),
    [selected, setSelected] = useState({}),
    [didSearch, setDidSearch] = useState(false),
    [isShowAll, setIsShowAll] = useState(false),
    dispatch = useDispatch(),
    { addToast } = useToasts();

  useEffect(() => {
    if (message) {
      addToast(message, {
        appearance: isSuccess ? "success" : "error",
      });
    }
  }, [isSuccess, message, addToast]);

  useEffect(() => {
    dispatch(BROWSE({ token, data: { batch: "65f40ff6ab7b08fcecfeaafe" } }));
  }, [dispatch, token]);

  useEffect(() => {
    const _batches = collections.map(({ sy }) => sy);
    if (_batches.length > 0) {
      setBatch(_batches[0]);
      setBatches(_batches);
      setStudentsNotArrange(collections[0].students || []);
    }
  }, [collections]);

  useEffect(() => {
    if (batch && studentsNotArrange.length > 0) {
      StoreDefualtValueForForm({
        form: {
          level: form.level,
          strand: form.strand,
          semester: form.semester,
          specialization: form.specialization,
          section: form.section,
        },
        setForm,
        collections: studentsNotArrange,
        isRegistrar: true,
        setArray: setstudents,
        isShowAll,
      });
    }
  }, [
    studentsNotArrange,
    form.level,
    form.strand,
    form.specialization,
    form.semester,
    isShowAll,
    batch,
  ]);

  const handleDelete = () => {
    MultipleDestroy(
      dispatch,
      DESTROY,
      token,
      container,
      addToast,
      setContainer
    );
  };

  const handleUpdate = () => {
    if (container.length === 1) {
      setSelected(container[0]);
      setWillCreate(false);
      setShow(true);
    } else {
      addToast("Please Select 1 Item", {
        appearance: "warning",
      });
    }
  };

  const handleSearch = (e) => {
    e.preventDefault();

    const key = e.target.searchKey.value.toUpperCase();

    setstudents(
      collections.filter(({ name }) => name.toUpperCase().includes(key))
    );

    setDidSearch(true);
  };

  console.log(students);

  return (
    <>
      <MDBCard narrow className="pb-3">
        <MDBView
          cascade
          className="gradient-card-header blue-gradient py-2 mx-4 d-flex justify-content-between align-items-center"
        >
          <span className="ml-3 ">Student Drop List</span>
          {students.length > 0 && (
            <div className="w-25">
              <select
                className="form-control bg-light"
                value={batch}
                onChange={({ target }) => setBatch(target.value)}
              >
                {batches.map((_batch, index) => (
                  <option key={index} value={_batch}>
                    SY - {_batch}
                  </option>
                ))}
              </select>
            </div>
          )}
          <Search
            handleSearch={handleSearch}
            didSearch={didSearch}
            setDidSearch={setDidSearch}
            array={students}
            setArray={setstudents}
            setShow={setShow}
            handleDelete={handleDelete}
            handleUpdate={handleUpdate}
          />
        </MDBView>
        <MDBCardBody>
          {isloading ? (
            <TableLoading />
          ) : (
            <>
              {!didSearch ? (
                <>
                  {students.length > 0 && (
                    <Level
                      form={form}
                      setForm={setForm}
                      isRegistrar={true}
                      disableSemester={true}
                      disableShowAll={true}
                    />
                  )}
                  <Table
                    collections={students}
                    isSelectAll={isSelectAll}
                    setIsSelectAll={setIsSelectAll}
                    lvlKey={form.level}
                    students={students}
                    setWillCreate={setWillCreate}
                    container={container}
                    setContainer={setContainer}
                  />
                </>
              ) : (
                <Table
                  didSearch={didSearch}
                  students={students}
                  container={container}
                  setContainer={setContainer}
                  isSelectAll={isSelectAll}
                  setIsSelectAll={setIsSelectAll}
                />
              )}
            </>
          )}
        </MDBCardBody>
      </MDBCard>

      <Modal
        collections={collections}
        show={show}
        previewForm={{
          lvl: form.level,
          strand: form.strand,
          specialization: form.specialization,
        }}
        toggle={() => {
          if (!willCreate) {
            setWillCreate(true);
            setSelected({});
          }
          setShow(false);
        }}
        selected={selected}
        willCreate={willCreate}
        setPreviewForm={setForm}
      />
    </>
  );
}
