import React, { useEffect, useState } from "react";
import { MDBCard, MDBCardBody, MDBView } from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import {
  BROWSE,
  DESTROY,
} from "../../../../services/redux/slices/resources/batches";
import { useToasts } from "react-toast-notifications";
import Modal from "./modal";
import Table from "./table";
import Search from "../../../../widgets/search";
import MultipleDestroy from "../../../../widgets/multipleDestroy";
import SwitchView from "../../../../widgets/view";
import TableLoading from "../../../../widgets/tableLoading";

export default function Batches() {
  const { token } = useSelector(({ auth }) => auth),
    { collections, message, isSuccess, isLoading } = useSelector(
      ({ batches }) => batches
    ),
    [batches, setBatches] = useState([]),
    [show, setShow] = useState(false),
    [willCreate, setWillCreate] = useState(true),
    [selected, setSelected] = useState({}),
    [container, setContainer] = useState([]),
    [didSearch, setDidSearch] = useState(false),
    [active, setActive] = useState("jhs"), // active bar
    dispatch = useDispatch(),
    { addToast } = useToasts();

  useEffect(() => {
    if (message) {
      addToast(message, {
        appearance: isSuccess ? "success" : "error",
      });
    }
  }, [isSuccess, message, addToast]);

  useEffect(() => {
    if (token) {
      dispatch(BROWSE({ token }));
    }
  }, [dispatch, token]);

  useEffect(() => {
    const filterBatch = collections.filter(
      ({ progress }) => progress === active
    );
    setBatches(filterBatch);
  }, [collections, active]);

  const handleSearch = (e) => {
    e.preventDefault();

    const key = e.target.searchKey.value.toUpperCase();

    setBatches(
      collections.filter(({ name }) => name.toUpperCase().includes(key))
    );

    setDidSearch(true);
  };

  const handleUpdate = () => {
    if (container.length === 1) {
      if (container[0].status === "DONE") {
        addToast("Sorry you can't update batch that has been done", {
          appearance: "warning",
        });
        return;
      }
      setSelected(container[0]);
      setWillCreate(false);
      setShow(true);
    } else {
      addToast("Please Select 1 Item", {
        appearance: "warning",
      });
    }
  };

  const handleDelete = () => {
    MultipleDestroy(
      dispatch,
      DESTROY,
      token,
      container,
      addToast,
      setContainer
    );
  };

  return (
    <>
      <MDBCard narrow className="pb-3">
        <MDBView
          cascade
          className="gradient-card-header blue-gradient py-2 mx-4 d-flex justify-content-between align-items-center"
        >
          <span className="ml-3 ">Batch List</span>

          <Search
            handleSearch={handleSearch}
            didSearch={didSearch}
            setDidSearch={setDidSearch}
            setShow={setShow}
            array={batches}
            setBatches={setBatches}
            handleDelete={handleDelete}
            handleUpdate={handleUpdate}
          />
        </MDBView>
        <MDBCardBody>
          {isLoading ? (
            <TableLoading />
          ) : (
            <>
              <SwitchView
                active={active}
                setActive={setActive}
                array={collections}
              />
              <Table
                container={container}
                setContainer={setContainer}
                batches={batches}
                handleDelete={handleDelete}
              />
            </>
          )}
        </MDBCardBody>
      </MDBCard>
      <Modal
        show={show}
        toggle={() => {
          if (!willCreate) {
            setWillCreate(true);
            setSelected({});
          }
          setShow(false);
        }}
        selected={selected}
        willCreate={willCreate}
      />
    </>
  );
}
