import React, { useEffect, useState } from "react";
import { MDBRow, MDBCol, MDBInput } from "mdbreact";
import "../learner.css";
const Parents = ({ setDetails, data }) => {

  const { parents } = data,
  { father: { fullName: { fname: fatherFname, mname: fatherMname, lname: fatherLname }, mobile: fatherMobile },
    mother: { fullName: { fname: motherFname, mname: motherMname, lname: motherLname }, mobile: motherMobile },
    guardian: { fullName: { fname: guardianFname, mname: guardianMname, lname: guardianLname }, mobile: guardianMobile }
  } = parents,
  [guardian, setGuardian] = useState("other")

  const handleNameChange = (obj, key, value) => {
    setDetails({
      ...data,
      parents: {
        ...parents,
        [obj]: {
          ...parents[obj],
          fullName: {
            ...parents[obj].fullName,
            [key]: value
          }
        }
      }
    });
  };

  const handleMobileChange = (obj, value) => {
    setDetails({ ...data, parents: { ...parents, [obj]: { ...parents[obj], mobile: value } } });
  }

  const handleGuadian = (e) => {
    const selectedGuardian = e.target.value;
  
    setGuardian(selectedGuardian);
  
    switch (selectedGuardian) {
      case "mother":
        setDetails({
          ...data,
          parents: {
            ...parents,
            guardian: {
              fullName: {
                fname: motherFname,
                mname: motherMname,
                lname: motherLname,
              },
              mobile: motherMobile,
            },
          },
        });
        break;
      case "father":
        setDetails({
          ...data,
          parents: {
            ...parents,
            guardian: {
              fullName: {
                fname: fatherFname,
                mname: fatherMname,
                lname: fatherLname,
              },
              mobile: fatherMobile,
            },
          },
        });
        break;
      default:
        setDetails({
          ...data,
          parents: {
            ...parents,
            guardian: {
              fullName: {
                fname: "",
                mname: "",
                lname: "",
              },
              mobile: "",
            },
          },
        });
        break;
    }
  };
  
  useEffect(() => {
    if(guardianFname){
      if(guardianFname === fatherFname){
        setGuardian("father");
      } else if(guardianFname === motherFname){
        setGuardian("mother");
      } else {
        setGuardian("other");
      }
    }
  }, [guardianFname, fatherFname, motherFname])

  return (
    <>
      <MDBRow style={{ backgroundColor: "#0000FF" }}>
        <MDBCol>
          <h5 className="font-weight-bold text-white mt-2 label mb-2 ml-5">
            IV. PARENT'S AND GUARDIAN'S INFORMATION
          </h5>
        </MDBCol>
      </MDBRow>

      <h6 className="mt-4 ml-4 mr-4">
        <strong>
          Father's Name
        </strong>
      </h6>
      <MDBRow className="ml-4 mr-4">
        <MDBCol sm="6" lg="3">
          <h6>First Name</h6>

          <input 
            placeholder="First Name" 
            className="form-control" 
            value={fatherFname}
            onChange={(e) => handleNameChange("father", "fname", e.target.value.toUpperCase())}
          />
        </MDBCol>

        <MDBCol sm="6" lg="3">
          <h6>Middle Name</h6>

          <input 
            placeholder="Middle Name" 
            className="form-control" 
            value={fatherMname}
            onChange={(e) => handleNameChange("father", "mname", e.target.value.toUpperCase())} 
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Last Name</h6>

          <input 
            placeholder="Last Name" 
            className="form-control"
            value={fatherLname}
            onChange={(e) => handleNameChange("father", "lname", e.target.value.toUpperCase())}
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Phone No.</h6>

          <input 
            placeholder="Phone No." 
            className="form-control" 
            value={fatherMobile}
            onChange={(e) => handleMobileChange("father", e.target.value.toUpperCase())} 
            min={1}
            maxLength={11}
            pattern="[0-9]*" 
          />
        </MDBCol>
      </MDBRow>

      <h6 className="mt-4 ml-4 mr-4">
        <strong>
          Mother's Name 
        </strong>
      </h6>
      <MDBRow className="ml-4 mr-4">
        <MDBCol sm="6" lg="3">
          <h6>First Name</h6>
          <input 
            placeholder="First Name" 
            className="form-control" 
            value={motherFname}
            onChange={(e) => handleNameChange("mother", "fname", e.target.value.toUpperCase())}
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Middle Name</h6>

          <input 
            placeholder="Middle Name" 
            className="form-control" 
            value={motherMname}
            onChange={(e) => handleNameChange("mother", "mname", e.target.value.toUpperCase())}
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Last Name</h6>

          <input 
            placeholder="Last Name" 
            className="form-control"
            value={motherLname}
            onChange={(e) => handleNameChange("mother", "lname", e.target.value.toUpperCase())} 
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Phone No.</h6>

          <input 
            placeholder="Phone No." 
            className="form-control" 
            value={motherMobile}
            onChange={(e) => handleMobileChange("mother", e.target.value.toUpperCase())} 
            min={1}
            maxLength={11}
            pattern="[0-9]*" 
          />
        </MDBCol>
      </MDBRow>

      <h6 className="mt-4 ml-4 mr-4">
        <strong>
          Legal Guardian's Name
        </strong> 
      </h6>
      <MDBRow className="ml-4 mr-4" style={{display: "flex", flexDirection: "row", gap: "5px", justifyContent: "flex-start",  flexWrap: "nowrap", alignItems: "center"}}>
        <MDBCol className="p-0" md="2">
          <MDBInput 
            label="Mother"
            type="checkbox"
            value="mother"
            name="mother"
            checked={guardian === "mother"}
            onChange={(e) => handleGuadian(e)}
            id="mother"
          />
        </MDBCol>
        <MDBCol className="p-0" md="2">
          <MDBInput 
            label="Father"
            type="checkbox"
            value="father"
            name="father"
            checked={guardian === "father"}
            onChange={(e) => handleGuadian(e)}
            id="father"
          />
          </MDBCol>
          <MDBCol className="p-0" md="8">
          <MDBInput 
            label="Other"
            type="checkbox"
            value="Other"
            name="other"
            checked={guardian === "other"}
            onChange={(e) => handleGuadian(e)}
            id="other"
          />

        </MDBCol>
      </MDBRow>
      <MDBRow className="mt-4 ml-4 mr-4">
        <MDBCol sm="6" lg="3">
          <h6> <span className="red-text">*</span>First Name</h6>

          <input 
            placeholder="First Name" 
            className="form-control" 
            value={guardianFname}
            onChange={(e) => handleNameChange("guardian", "fname", e.target.value.toUpperCase())}
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6>Middle Name</h6>

          <input 
            placeholder="Middle Name" 
            className="form-control" 
            value={guardianMname}
            onChange={(e) => handleNameChange("guardian", "mname", e.target.value.toUpperCase())}
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6> <span className="red-text">*</span>Last Name</h6>

          <input 
            placeholder="Last Name" 
            className="form-control" 
            value={guardianLname}
            onChange={(e) => handleNameChange("guardian", "lname", e.target.value.toUpperCase())}
          />
        </MDBCol>
        <MDBCol sm="6" lg="3">
          <h6> <span className="red-text">*</span>Phone No.</h6>

          <input 
            placeholder="Phone No." 
            className="form-control" 
            value={guardianMobile}
            onChange={(e) => handleMobileChange("guardian", e.target.value.toUpperCase())}
            min={1}
            maxLength={11}
            pattern="[0-9]*" 
          />
        </MDBCol>
      </MDBRow>
    </>
  );
};

export default Parents;
