import React from "react";
import { MDBCol, MDBRow } from "mdbreact";
import { School } from "../../../../../services/fakeDb";
import "./index.css";

const Title = ({ text }) => {
  return (
    <div className="db-header-title">
      <span>{text}</span>
    </div>
  );
};

export default function Header() {
  const { name, logo } = School;
  return (
    <div className="db-header">
      <MDBRow>
        <MDBCol md="5">
          <Title text={"Department of education"} />
        </MDBCol>
        <MDBCol md="2" className="d-flex justify-content-center">
          <div className="db-header-logo-container">
            <img
              src={logo}
              height="75"
              widht="75"
              alt="school-logo"
              className="db-header-logo"
            />
          </div>
        </MDBCol>
        <MDBCol md="5">
          <Title text={`LMBNHS`} />
        </MDBCol>
      </MDBRow>
    </div>
  );
}
