import React from "react";
import { MDBCol, MDBRow } from "mdbreact";

import Documents from "../documents";
import Enrollment from "../enrollment";
import Address from "../address";
import Parents from "../parents";
import Identify from "../identify";
import Health from "../health";

const Body = ({ setDetails, details, _doneSteps, stepCount, isNewReject }) => {
  const renderSteps = () => {
    switch (stepCount) {
      case 1:
        return <Identify setDetails={setDetails} data={details} />;
      case 2:
        return <Health setDetails={setDetails} data={details} />;
      case 3:
        return <Address />;
      case 4:
        return <Parents setDetails={setDetails} data={details} />;
      case 5:
        return <Documents setDetails={setDetails} data={details} />;
      case 6:
        return (
          <Enrollment
            setDetails={setDetails}
            data={details}
            isNewNReject={isNewReject}
          />
        );
      default:
        console.log("no more page");
        break;
    }
  };

  return (
    <>
      <MDBRow className="m-2">
        <MDBCol className="p-0">
          {stepCount === 1 && (
            <h6 className="mt-4">
              <strong>Description:</strong>{" "}
              <i>
                This form has {_doneSteps.length} steps that need to be
                completed to have a successful enrollment. Enjoy filling it up!
              </i>
            </h6>
          )}

          <h6>
            <strong>Note:</strong> <i>Fields with asterisk (*) are required.</i>
          </h6>
        </MDBCol>
      </MDBRow>
      <hr
        style={{
          border: "1 px solid black",
          width: "100%",
          transform: "scale(1.03)",
        }}
      />
      {renderSteps()}
    </>
  );
};

export default Body;
