import { MDBCol } from "mdbreact";
import React from "react";
import "./index.css";
import { useSelector } from "react-redux";

export default function Campus() {
  const { dashboard } = useSelector(({ enrollees }) => enrollees);

  const { campus = {} } = dashboard;
  return (
    <MDBCol
      size="2"
      className="text-center"
      style={{
        backgroundColor: "#111",
      }}
    >
      <h4
        className="text-center font-weight-bold my-2"
        style={{ color: "#c4c6ca" }}
      >
        CAMPUS
      </h4>
      {Object.entries(campus)?.map(([key, value]) => (
        <div className="campus-card mb-2" key={`campuses-${key}`}>
          <div className="campus-bg">
            <div className="campus-scene">
              <div className="campus-cube campus-animation">
                <span className="campus-side campus-top">
                  {String(value).padStart(5, "0")}
                </span>
                <span className="campus-side campus-front">{key}</span>
              </div>
            </div>
          </div>
          <div className="campus-blob"></div>
        </div>
      ))}
    </MDBCol>
  );
}
