import { MDBCol } from "mdbreact";
import React from "react";
import "./index.css";
import Students from "./students";
import StudentType from "./studentType";
import Status from "./status";
import { School } from "../../../../../../services/fakeDb";

export default function Enrollments({ fullView }) {
  const { isPublic } = School;

  return (
    <MDBCol
      size={isPublic ? "9" : "8"}
      className="text-center"
      style={{
        boxShadow: "0 2px 5px 2px rgba(255,255,255, 0.5",
        backgroundColor: "#000",
        zIndex: "1",
      }}
    >
      <p className="db-enrollees-title">ENROLLMENT DASHBOARD</p>
      <StudentType />
      <Students fullView={fullView} />
      <Status />
    </MDBCol>
  );
}
