import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import EnrollmentForm from "./enrollment form";
import { BROWSE } from "../../../../services/redux/slices/resources/batches";
import {
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBCardHeader,
  MDBCol,
  MDBContainer,
  MDBIcon,
  MDBRow,
} from "mdbreact";

import {
  GETUSER,
  RESET,
  SETROUTE,
} from "../../../../services/redux/slices/auth";
import { useHistory } from "react-router";

export default function Dashboard() {
  const { auth, token, information } = useSelector(({ auth }) => auth),
    { fullName, isMale } = auth,
    { status } = information?.enrolleeData ?? {},
    { collections } = useSelector(({ batches }) => batches),
    [openBatch, setOpenBatch] = useState([]),
    { isSuccess, message } = useSelector(({ credentials }) => credentials),
    [resolveIssue, setResolveIssue] = useState(false),
    [isNewStudent, setIsNewStudent] = useState(true),
    [isJhsOpen, setIsJhsOpen] = useState(false),
    [isShsOpen, setIsShsOpen] = useState(false),
    [enrollmentStatus, setEnrollmentStatus] = useState(status),
    [showForm, setShowForm] = useState(false),
    [batchStatus, setBatchStatus] = useState(""),
    [newStudentButReject, setNewStudentButReject] = useState(false),
    [oldPendingMsg, setOldPendingMsg] = useState(""),
    [student, setStudent] = useState([]),
    [canEnroll, setCanEnroll] = useState(false),
    history = useHistory(),
    MsgOldPending = `Dear ${isMale ? "Mr." : "Ms."} ${
      fullName.fname + " " + fullName.lname
    }, your previous pending enrollment request has been archived. Please await the start of the new enrollment period to reapply.`,
    dispatch = useDispatch();

  useEffect(() => {
    if (message) {
      if (isSuccess && !showForm) {
        dispatch(GETUSER({ token }));
      }
    }
  }, [message, dispatch, token, isSuccess, showForm]);

  useEffect(() => {
    if (token) {
      dispatch(BROWSE({ token }));
    }
  }, [dispatch, token]);

  useEffect(() => {
    if (information.enrolleeData) {
      setStudent(information);
      setResolveIssue(false);
      setEnrollmentStatus(information.enrolleeData?.status);
    }

    if (information.credentials) {
      setIsNewStudent(false);
    } else {
      setIsNewStudent(true);
    }
  }, [information, showForm]);

  const infoEnrolleeBatch = information.enrolleeData?.batch;
  const infoBatches = information?.batches;
  const studentEnrolleData = information?.enrolleeData;

  useEffect(() => {
    const batchOpen = collections.filter((item) => item.status === "ACTIVE");

    if (!batchOpen) {
      setShowForm(false);
    }

    let jhsOpenData = batchOpen.filter((item) => item.progress === "jhs");
    const isAnyJhsOpen = batchOpen.some((item) => item.progress === "jhs");
    let shsOpenData = batchOpen.filter((item) => item.progress === "shs");
    const isAnyShsOpen = batchOpen.some((item) => item.progress === "shs");

    if (!jhsOpenData) {
      jhsOpenData = jhsOpenData[0];
    }
    if (!shsOpenData) {
      shsOpenData = shsOpenData[0];
    }

    if (batchOpen) {
      setOpenBatch(batchOpen);
    }

    // enroll as grade 11 student
    if (
      enrollmentStatus === "APPROVED" &&
      studentEnrolleData?.lvl === 10 &&
      isAnyShsOpen &&
      (infoEnrolleeBatch?._id !== jhsOpenData[0]?._id ||
        jhsOpenData[0]?._id === undefined)
    ) {
      setShowForm(true);
      return;
    }
    // check if student status is pending or approved removed form
    const pendingUser = batchOpen.some(
      (item) => item._id === infoEnrolleeBatch?._id
    );

    if (enrollmentStatus === "PENDING" && pendingUser) {
      setOldPendingMsg("");
      setShowForm(false);
      return;
    }

    if (isNewStudent) {
      if (isAnyJhsOpen) {
        setIsJhsOpen(true);
        setShowForm(true);
        return;
      } else if (isAnyShsOpen) {
        setIsShsOpen(true);
        setShowForm(true);
        return;
      }
    }

    if (infoEnrolleeBatch?.progress === "jhs") {
      // check if status is approved
      if (
        enrollmentStatus === "APPROVED" &&
        infoEnrolleeBatch?._id !== jhsOpenData[0]?._id &&
        isAnyJhsOpen
      ) {
        setShowForm(true);
        return;
      }

      // check if acive batch same as the enrollees progress if not then student cant re enroll
      if (
        jhsOpenData[0]?.progress === studentEnrolleData?.progress &&
        enrollmentStatus === "REJECT"
      ) {
        setCanEnroll(true);
      }

      // check if new enrollee is reject
      if (
        infoEnrolleeBatch?._id === jhsOpenData[0]?._id &&
        infoBatches.length === 1 &&
        enrollmentStatus === "REJECT"
      ) {
        setNewStudentButReject(true);
        setShowForm(false);
        return;
      }

      // check if old enrollee is reject
      if (enrollmentStatus === "REJECT") {
        setShowForm(false);
        return;
      }

      // check if his status is pending and there's a new batch;
      if (
        enrollmentStatus === "PENDING" &&
        infoEnrolleeBatch?._id !== jhsOpenData[0]?._id &&
        !isAnyJhsOpen
      ) {
        setOldPendingMsg(MsgOldPending);
        setShowForm(false);
        return;
      }

      // check if the student is approved
      if (
        enrollmentStatus === "APPROVED" &&
        infoEnrolleeBatch?._id === jhsOpenData[0]?._id &&
        isAnyJhsOpen
      ) {
        setShowForm(false);
        return;
      } else if (
        enrollmentStatus === "APPROVED" &&
        infoEnrolleeBatch?._id !== jhsOpenData[0]?._id &&
        isAnyJhsOpen
      ) {
        setShowForm(true);
        return;
      }
      if (
        enrollmentStatus === "APPROVED" &&
        infoEnrolleeBatch?.progress !== jhsOpenData[0]?.progress
      ) {
        setShowForm(false);
        return;
      }

      if (infoEnrolleeBatch?._id !== jhsOpenData[0]?._id) {
        setShowForm(true);
        setOldPendingMsg("");
      }
    }

    // check enrollee is shs
    if (infoEnrolleeBatch?.progress === "shs") {
      // check if status is approved
      if (
        enrollmentStatus === "APPROVED" &&
        infoEnrolleeBatch?._id !== shsOpenData[0]?._id &&
        infoEnrolleeBatch?.progress === shsOpenData[0]?.progress
      ) {
        setOldPendingMsg("");
        setShowForm(true);
        return;
      }

      // check if acive batch same as the enrollees progress if not then student cant re enroll
      if (
        shsOpenData[0]?.progress === studentEnrolleData?.progress &&
        enrollmentStatus === "REJECT"
      ) {
        setOldPendingMsg("");
        setCanEnroll(true);
      }

      // check if new enrollee is reject
      if (
        infoEnrolleeBatch?._id === shsOpenData[0]?._id &&
        infoBatches.length === 1 &&
        enrollmentStatus === "REJECT"
      ) {
        setNewStudentButReject(true);
        setShowForm(false);
        return;
      }

      // check if old enrollee is reject
      if (enrollmentStatus === "REJECT") {
        setShowForm(false);
        return;
      }

      // check if his status is pending and there's a new batch;
      if (
        enrollmentStatus === "PENDING" &&
        infoEnrolleeBatch?._id !== shsOpenData[0]?._id
      ) {
        if (
          infoEnrolleeBatch?.semester &&
          infoEnrolleeBatch?.semester === shsOpenData[0]?.semester
        ) {
          setOldPendingMsg("");
          setShowForm(true);
          return;
        } else {
          setOldPendingMsg(MsgOldPending);
          setShowForm(false);
          return;
        }
      }

      // check if the student is approved
      if (
        enrollmentStatus === "APPROVED" &&
        infoEnrolleeBatch?._id === shsOpenData[0]?._id &&
        infoEnrolleeBatch.progress === shsOpenData[0]?.progress
      ) {
        setShowForm(false);
        return;
      } else if (
        enrollmentStatus === "APPROVED" &&
        infoEnrolleeBatch?._id !== shsOpenData[0]?._id &&
        infoEnrolleeBatch.progress === shsOpenData[0]?.progress
      ) {
        setOldPendingMsg("");
        setShowForm(true);
        return;
      }
    }

    batchOpen.forEach((active) => {
      setBatchStatus(active.status);
    });
  }, [
    collections,
    isNewStudent,
    enrollmentStatus,
    infoEnrolleeBatch,
    infoBatches,
    fullName.fname,
    fullName.lname,
    isMale,
    studentEnrolleData,
    MsgOldPending,
  ]);

  return (
    <MDBContainer style={{ marginTop: "200px" }}>
      {(showForm || resolveIssue) && (
        <EnrollmentForm
          setShowForm={setShowForm}
          status={batchStatus}
          back={resolveIssue}
          setBack={() => setResolveIssue((prev) => !prev)}
          isNewReject={newStudentButReject}
          studentData={student}
          batch={openBatch}
          enrolleeID={resolveIssue ? studentEnrolleData?._id : 0}
        />
      )}

      {oldPendingMsg && (
        <MDBCard>
          <MDBCardBody>
            <MDBRow>
              <MDBCol>{oldPendingMsg}</MDBCol>
            </MDBRow>
          </MDBCardBody>
        </MDBCard>
      )}

      {!showForm &&
      isNewStudent &&
      !isJhsOpen &&
      isShsOpen &&
      !enrollmentStatus ? (
        <MDBCard>
          <MDBCardBody>
            <MDBRow>
              <MDBCol>
                Currently no open Enrollment for Junior High School
              </MDBCol>
            </MDBRow>
          </MDBCardBody>
        </MDBCard>
      ) : !showForm &&
        !isNewStudent &&
        !isShsOpen &&
        isJhsOpen &&
        !enrollmentStatus ? (
        <MDBCard>
          <MDBCardBody>
            <MDBRow>
              <MDBCol>
                Currently no open Enrollment for Senior High School
              </MDBCol>
            </MDBRow>
          </MDBCardBody>
        </MDBCard>
      ) : (
        !showForm &&
        !isShsOpen &&
        !isJhsOpen &&
        !oldPendingMsg &&
        !enrollmentStatus && (
          <MDBCard>
            <MDBCardBody>
              <MDBRow>
                <MDBCol>Currently no open Enrollment</MDBCol>
              </MDBRow>
            </MDBCardBody>
          </MDBCard>
        )
      )}

      {!oldPendingMsg && !showForm && !resolveIssue && (
        <MDBCard className="mt-3 mr-5" style={{ width: "1300px" }}>
          <MDBCardHeader className="bg-white">
            <MDBRow>
              <MDBCol>
                <h5>
                  {enrollmentStatus && (
                    <>
                      Status:
                      {enrollmentStatus === "PENDING" ? (
                        <span className="blue-text font-weight-bold ml-2">
                          {enrollmentStatus.toUpperCase()}
                        </span>
                      ) : enrollmentStatus === "APPROVED" ? (
                        <span className="green-text font-weight-bold ml-2">
                          ENROLLED
                        </span>
                      ) : (
                        <span className="red-text font-weight-bold ml-2">
                          {enrollmentStatus.toUpperCase()}
                        </span>
                      )}
                    </>
                  )}
                </h5>
              </MDBCol>
            </MDBRow>
          </MDBCardHeader>
          <MDBCardBody>
            <MDBRow>
              {enrollmentStatus === "PENDING" && (
                <MDBCol>
                  <p style={{ fontSize: "1.1rem" }}>
                    <strong>
                      Dear {isMale ? "Mr." : "Ms."}{" "}
                      {fullName.fname + " " + fullName.lname}
                    </strong>
                    , We hope this message finds you well. We want to inform you
                    that your enrollment status is currently pending. Our team
                    is diligently reviewing the information you provided, and we
                    appreciate your patience during this process.
                    <br /> <br />
                    Thank you for choosing{" "}
                    <strong>Leonor M. Bautista National High School</strong>.
                    We're excited to see you in class for this school year.
                  </p>
                </MDBCol>
              )}

              {enrollmentStatus === "REJECT" && (
                <MDBCol md="12">
                  <p className="ml-2" style={{ fontSize: "1.1rem" }}>
                    <strong>
                      Dear {isMale ? "Mr." : "Ms."}{" "}
                      {fullName.fname + " " + fullName.lname}
                    </strong>
                    , {information.enrolleeData?.registrar?.message}
                  </p>
                  {!canEnroll && (
                    <div className={"mt-3 alert alert-warning"}>
                      <MDBIcon fas icon="info-circle" className="mr-2" />
                      Sorry Currently no open Enrollment
                    </div>
                  )}

                  <MDBBtn
                    color="danger"
                    onClick={() => setResolveIssue((prev) => !prev)}
                    disabled={!canEnroll}
                    style={{ fontSize: "1.1rem" }}
                  >
                    RESOLVE ISSUE IN YOUR FORM
                  </MDBBtn>
                </MDBCol>
              )}

              {enrollmentStatus === "APPROVED" && (
                <MDBCol>
                  <h5 style={{ fontSize: "1.3rem" }}>
                    <strong>
                      Dear {isMale ? "Mr." : "Ms."}
                      {fullName.fname} {fullName.lname}
                    </strong>{" "}
                    <h5 style={{ lineHeight: "28px" }} className="mt-2">
                      Congratulations! Your enrollment form has been approved,
                      and you are now officially part of &nbsp;
                      <strong>Leonor M. Bautista National High School</strong>.
                      Welcome to our school community! As you start your journey
                      with us, we kindly remind you of some important school
                      rules:
                    </h5>
                    <ol style={{ lineHeight: "35px" }}>
                      <li className="text-nowrap">
                        Attendance and punctuality are crucial for academic
                        success. Please ensure you arrive on time for classes
                        and activities.
                      </li>
                      <li className="text-nowrap">
                        Respect for teachers, staff, and fellow students is
                        paramount. We promote a positive and inclusive learning
                        environment for everyone.
                      </li>
                      <li>
                        Adherence to the school's dress code policy is required
                        at all times.
                      </li>
                      <li>
                        Participation in extracurricular activities and school
                        events is encouraged to enrich your educational
                        experience.
                      </li>
                      <li>
                        For any concerns or inquiries, do not hesitate to reach
                        out to our school administration or guidance counselors.
                      </li>
                    </ol>
                    We are excited to have you join our school community and
                    wish you a fulfilling and successful academic year!{" "}
                    <MDBBtn
                      color="primary"
                      className="p-2"
                      onClick={() => {
                        history.push("/schedule");
                        dispatch(SETROUTE("schedule"));
                        dispatch(RESET());
                      }}
                      style={{ fontSize: "1.1rem" }}
                    >
                      View Schedule
                    </MDBBtn>
                  </h5>
                </MDBCol>
              )}
            </MDBRow>
          </MDBCardBody>
        </MDBCard>
      )}
    </MDBContainer>
  );
}
