import React, { useEffect, useState } from "react";
import { MDBBtn, MDBIcon, MDBTable } from "mdbreact";

import PaginationButtons from "../../../../widgets/pagination/buttons";
import handlePagination from "../../../../widgets/pagination";
import Rows from "../../../../widgets/pagination/rows";
import handleMultiSeclect from "../../../../widgets/multipleSelect";
import { useDispatch, useSelector } from "react-redux";
import { UPDATE } from "../../../../services/redux/slices/resources/batches";
import { useToasts } from "react-toast-notifications";
import Swal from "sweetalert2";

const Table = ({ batches = [], container, setContainer }) => {
  const { token } = useSelector(({ auth }) => auth),
    [page, setPage] = useState(1),
    [max, setMax] = useState(5),
    [orderIndex, setOrderIndex] = useState(0),
    [isSelectAll, setIsSelectAll] = useState(false),
    arrangedBatches = batches.slice(),
    order = { ACTIVE: 1, WAITING: 2, INACTIVE: 3, DONE: 4 },
    dispatch = useDispatch(),
    { addToast } = useToasts();

  arrangedBatches.sort((a, b) => order[a.progress] - order[b.progress]);

  useEffect(() => {
    if (isSelectAll) {
      setContainer(batches);
    } else {
      setContainer((prev) => (prev.length === batches.length ? [] : prev));
    }
  }, [isSelectAll, batches, setContainer]);

  const handleBatchActivation = (e, batchObj) => {
    e.preventDefault();
    console.log(e.target);

    const { value } = e.target;
    if (!value) {
      return;
    }
    if (value === "ACTIVE") {
      const canActivate = arrangedBatches.filter(
        (batch) =>
          (batch.status === "ACTIVE" || batch.status === "INACTIVE") &&
          batch.progress === batchObj.progress
      );

      let canActive = true;
      if (canActivate[0]?._id !== batchObj._id || canActivate.length <= 0) {
        canActive = false;
      }

      if (!canActive) {
        return addToast(
          "Action cannot be done; there is still active enrollment.",
          {
            appearance: "error",
          }
        );
      }
    }

    Swal.fire({
      focusDeny: true,
      icon: "question",
      title: `Are you sure you want to ${value.toLowerCase()} this batch?`,
      text: "This action is irreversible.",
      confirmButtonText: '<span class="text-dark">Cancel</span>',
      confirmButtonColor: "#fff",
      showDenyButton: true,
      denyButtonText: "Proceed",
      denyButtonColor: "#3B71CA",
    }).then((res) => {
      if (res.isDenied) {
        dispatch(
          UPDATE({
            data: { ...batchObj, status: value },
            token,
          })
        );
      } else {
        return Swal.fire({
          title: "Changes are not Saved!",
          icon: "warning",
          showConfirmButton: false,
          timer: 2500,
          timerProgressBar: true,
        }).then(() => false);
      }
    });
  };

  return (
    <>
      <MDBTable responsive hover>
        <thead>
          <tr>
            <th>
              <input
                className="form-check-input"
                type="checkbox"
                checked={isSelectAll}
                onChange={() => setIsSelectAll(!isSelectAll)}
                id="checkbox"
              />
              <label
                htmlFor="checkbox"
                className="form-check-label mr-2 label-table"
              />
            </th>
            <th
              className="th-lg cursor-pointer"
              onClick={() =>
                setOrderIndex((prev) => {
                  if (prev > 1) return 0;

                  return prev + 1;
                })
              }
            >
              Sy&nbsp;
              <MDBIcon
                icon="sort"
                title="Sort by Name"
                className={`${!!orderIndex && "text-primary"}`}
              />
            </th>
            <th className="th-lg" colSpan="2">
              <div style={{ marginLeft: "20%" }}>Enrollment</div>
              <div className="d-flex" style={{ gap: "45%" }}>
                <span>Start</span>
                <span>End</span>
              </div>
            </th>

            <th className="th-lg" colSpan="2">
              <div style={{ marginLeft: "20%" }}>Classes</div>

              <div className="d-flex" style={{ gap: "45%" }}>
                <span className="text-left">Start</span>
                <span className="text-right">End</span>
              </div>
            </th>
            <th className="th-lg">Status</th>
            <th className="th-lg">Action</th>
          </tr>
        </thead>
        <tbody>
          {arrangedBatches?.length > 0 &&
            handlePagination(arrangedBatches, page, max).map((batch, index) => {
              const {
                _id,
                sy,
                enrollment,
                classes,
                status,
                semester,
                progress,
              } = batch;
              return (
                <tr key={_id}>
                  <td>
                    <input
                      className="form-check-input"
                      type="checkbox"
                      id={index}
                      checked={
                        isSelectAll
                          ? true
                          : container.some(({ _id }) => _id === batch._id)
                      }
                      onChange={() => {
                        setContainer(
                          handleMultiSeclect(container, batch, setIsSelectAll)
                        );
                        setIsSelectAll(false);
                      }}
                    />
                    <label
                      htmlFor={index}
                      className="form-check-label mr-2 label-table"
                    />
                  </td>
                  <td style={{ fontWeight: 400 }}>
                    {sy} {progress === "shs" && ` - ${semester} sem `}
                  </td>
                  <td style={{ fontWeight: 400 }}> {enrollment.start}</td>
                  <td style={{ fontWeight: 400 }}>{enrollment.end}</td>
                  <td style={{ fontWeight: 400 }}>{classes.start}</td>
                  <td style={{ fontWeight: 400 }}>{classes.end}</td>
                  <td style={{ fontWeight: 400 }}>{status}</td>
                  <td>
                    {status === "WAITING" ? (
                      <MDBBtn
                        size="sm"
                        className="m-0"
                        color="success"
                        value="ACTIVE"
                        onClick={(e) => handleBatchActivation(e, batch)}
                      >
                        ACTIVE
                      </MDBBtn>
                    ) : status === "ACTIVE" || status === "INACTIVE" ? (
                      <div className="d-flex align-items-center justify-center">
                        <MDBBtn
                          size="sm"
                          className="m-0"
                          color="primary"
                          value="DONE"
                          onClick={(e) => handleBatchActivation(e, batch)}
                        >
                          Done
                        </MDBBtn>
                        <MDBBtn
                          size="sm"
                          className="m-0"
                          color={status === "ACTIVE" ? "warning" : "success"}
                          value={status === "ACTIVE" ? "INACTIVE" : "ACTIVE"}
                          onClick={(e) => handleBatchActivation(e, batch)}
                        >
                          {status === "ACTIVE" ? "PAUSE" : "START"}
                        </MDBBtn>
                      </div>
                    ) : (
                      <MDBBtn size="sm" disabled className="m-0">
                        Done
                      </MDBBtn>
                    )}
                  </td>
                </tr>
              );
            })}
        </tbody>
      </MDBTable>
      <Rows setMax={setMax} max={max} />
      <PaginationButtons array={arrangedBatches} setPage={setPage} max={max} />
    </>
  );
};

export default Table;
